import {
  getTableList,
  getTableData,
  saveTableData,
  getFileId,
  setFileContent,
  removeTableData
} from '@api/fill'
export default {
  props: {
    // 表示类型
    tableName: {
      type: String
    },
    type: {
      type: Object,
      required: true
    },
    // 表的值
    val: {
      type: Object
    },
    // 查库的过滤语句
    query: {
      type: String
    },
    data: {
      type: Object
    }
  },
  mounted() {
    if (this.$route.path == '/') {
      this.readOnly = true
    } else {
      this.readOnly = false
    }
    if (this.type && this.type.id) {
      this.getDetail(this.type)
      this.getValue(this.type)
    }
  },
  watch: {
    type: function (type) {
      if (type && type.id) {
        this.refresh(type)
      }
    }
  },
  data() {
    return {
      readOnly: false,
      groupByData: [],
      columnData: [],
      columnLength: 0,
      theadRowLength: 0,
      newRowData: [],
      removeRowData: [],
      value: {},
      columnMap: {},
      groupBy: {},
      children: [],
      relation: {},
      tableData: [],
      rowData: {},
    }
  },
  computed: {
    canAddColumn: function () {
      return !!Object.keys(this.rowData).length
    }
  },
  methods: {
    removeItem(index) {
      let obj = this.columnData.splice(index,1)[0];
      if (this.newRowData.indexOf(obj) !== -1) {
        this.newRowData.splice(index,1)
      } else {
        this.removeRowData.push(obj)
      }
    },
    addBeforeItem(index) {
      this.addColumnData(index > 0? index - 1: 0)
    },
    reset() {
      this.groupByData = []
      this.columnData = []
      this.columnLength = 0
      this.theadRowLength = 0
      this.newRowData = []
      this.value = {}
      this.columnMap = {}
      this.groupBy = {}
      this.children = []
      this.relation = {}
      this.tableData = []
      this.rowData = {}
      this.removeRowData = []
    },
    refresh(type) {
      this.reset()
      this.getDetail(type)
      this.getValue(type)
    },
    flatChildren(data, step) {
      data.forEach((item) => {
        if (this.readOnly) {
          if (this.relation.group_by && item.relation.name == this.relation.group_by) {
            this.groupBy = item
            return
          }
        }
        
        if (!this.tableData[step]) {
          this.tableData[step] = []
        }
        if (item.name === 'id') {
          this.columnMap[item.relation.name] = item
          return;
        }
        this.tableData[step].push(item)
        if (item.children && item.type.type != 1) {
          let next = step + 1;
          this.flatChildren(item.children, next)
        } else {
          this.columnMap[item.relation.name] = item
        }
      })
      
    },
    addColumnData(index) {
      const obj = {}
      for (let key in this.columnMap) {
        obj[key] = {
          value: '',
          ref: this.columnMap[key]
        }
        // extendDeep(obj[key], this.columnMap[key])
      }
      if (typeof index == 'number') {
        this.columnData.splice(index, 0, obj)
      } else {
        this.columnData.push(obj)
      }
      /* if (this.relation.group_by) {
        const groupKey = Object.keys(this.rowData).pop()
        this.rowData[groupKey].column.push(obj)
      } else {
        if (index !== undefined) {
          this.columnData.splice(index, 0, obj)
        } else {
          this.columnData.push(obj)
        }
        
      } */
      this.newRowData.push(obj)
    },
    addGroupByData() {
      const name = `newKey${Math.random()}`;
      const obj = {
        groupBy: {
          ref: this.groupBy,
          value: ''
        },
        column: []
      }
      this.$set(this.rowData, name, obj)
      this.rowData[name] = obj
    },
    save() {
      this.beforeSave()
      this.$Message.success('保存成功');
      this.refresh(this.type)
    },
    addItem(name) {
      if (name == '添加分组行') {
        this.addGroupByData()
      } else {
        this.addColumnData()
      }
    },
    getValue() {
      this.value = getTableData(this.tableName, this.query)
      this.converValue()
    },
    getChildrenValue(child, keyMap) {
      let obj = {}
      child.children.forEach(item => {
        if (item.type.type == 2) {
          obj[item.name] = this.getChildrenValue(item, keyMap)
        } else {
          // TODO: 区分下是否是文件和图片
          if (item.type.no == 8 || item.type.no == 9) {
            let id = getFileId(keyMap[item.name].fileType)
            setFileContent({
              id,
              name: item.name,
              content: keyMap[item.name].value
            })
            obj[item.name] = id;
          } else {
            obj[item.name] = keyMap[item.name] ? keyMap[item.name].value : ''

          }
          /* if (item.name != this.relation.group_by) {
            if (item.type.no == 8 || item.type.no == 9) {
              let id = getFileId(keyMap[item.name].fileType)
              setFileContent({
                id,
                name: item.name,
                content: keyMap[item.name].value
              })
              obj[item.name] = id;
            } else {
              obj[item.name] = keyMap[item.name] ? keyMap[item.name].value : ''

            }
          } */

        }
      })
      return obj;
    },
    converValue() {
      if (this.relation.group_by && this.readOnly) {
        this.value.forEach(item => {
          let groupValue = item[this.relation.group_by];
          if (!this.rowData[groupValue]) {
            this.$set(this.rowData, groupValue, {
              groupBy: {
                ref: this.groupBy,
                value: groupValue
              },
              column: []
            })

          }
          let obj = {}
          if (Object.keys(item).length > 1) {
            this.flatValue(obj, item)
            delete obj[this.relation.group_by]
            this.rowData[groupValue].column.push(obj)
          }
        })
      } else {
        this.value.forEach(item => {
          let obj = {}
          this.flatValue(obj, item)
          this.columnData.push(obj)
        })
      }
    },
    flatValue(target, value) {
      for (let key in value) {
        if (this.columnMap[key]) {
          target[key] = {
            value: value[key],
            ref: this.columnMap[key]
          }
        } else {
          let child = this.children.find(item => item.name === key)
          if (!child) break
          if (child.type.type == 2) {
            this.flatValue(target, JSON.parse(value[key]))
          } else {
            target[key] = {
              value: value[key],
              ref: child
            }
          }
        }
      }
    },
    beforeSave() {
      if (this.relation.group_by && this.readOnly) {
        for (let key in this.rowData) {
          const val = this.rowData[key]
          if (val.column.length) {
            val.column.forEach(item => { // 插入的一条数据
              const obj = this.getChildrenValue(this, item)
              obj['基线id'] = this.data.relation.baselineId;
              obj[this.relation.group_by] = val.groupBy.value;
              let isNew = key.indexOf('newKey') !== -1
              if (!isNew) {
                isNew = this.newRowData.indexOf(item) != -1
              }

              this.saveData(obj, isNew)
            })
          } else {
            let obj = {}
            obj[this.relation.group_by] = val.groupBy.value;
            obj['基线id'] = this.data.relation.baselineId
            obj['序号'] = this.idRule;
            this.saveData(obj, key.indexOf('newKey') !== -1)
          }

        }

      } else {
        this.columnData.forEach(item => {
          const obj = this.getChildrenValue(this, item)
          obj['基线id'] = this.data.relation.baselineId;
          if (this.newRowData.indexOf(item) != -1) {
            this.saveData(obj, true)
          } else {
            this.saveData(obj, false)
          }
        })
        this.removeRowData.forEach(item => {
          removeTableData(this.tableName, item)
        })
      }
    },
    saveData(obj, isNew) {
      console.log('saveData', obj, isNew)
      for (let key in obj) {
        if (typeof (obj[key]) == 'object') {
          if (obj[key] instanceof Blob) {
            getFileId()
          }
          obj[key] = JSON.stringify(obj[key])
          break;
        }
      }

      saveTableData(this.tableName, obj, isNew)
    },

    getDetail(type) {
      const result = getTableList(type, this.tableName)
      this.children = JSON.parse(JSON.stringify(result.children));
      this.relation = JSON.parse(JSON.stringify(result.relation));
      this.flatChildren(this.children, 0)
      if (this.tableData[0].length === 1 && this.tableData.length > 1) {
        this.tableData.shift()
      }
      this.tableData[0].unshift({
        name: '操作',
        relation: {
          name: '操作'
        }
      })
      this.columnLength = Object.keys(this.columnMap).length;
      this.theadRowLength = this.tableData.length;
    }
  }
}