<template>
  <div id="basisData">
    <Split v-model="split1" min="0" max="200px">
      <Sider slot="left"/>
      <div class="right-box" slot="right">
        <div class="table-box"  v-show="viewState=='organization'">
          <div class="table-title">
            {{title}}
            <Button @click="save">保存</Button>
          </div>
          <Table  border  :columns="organization.columns" :data="organization.content" :max-height="clientHeight * 0.75" :disabled-hover="true">
            <template slot-scope="{ row }" slot="controls">
              <Button  @click="remove(row)">删除</Button>
            </template>
            <template slot-scope="{ row }" slot="no">
              <Input v-model="row.no"  @on-change="inputChange($event,row,'organization')"/>
            </template>
            <template slot-scope="{ row }" slot="name">
              <Input v-model="row.name"  @on-change="inputChange($event,row,'organization')"/>
            </template>
            <template slot-scope="{ row }" slot="address">
              <Input v-model="row.address"  @on-change="inputChange($event,row,'organization')"/>
            </template>
            <template slot-scope="{ row }" slot="person">
              <Input v-model="row.person"  @on-change="inputChange($event,row,'organization')"/>
            </template>
            <template slot-scope="{ row }" slot="telephone">
              <Input v-model="row.telephone"  @on-change="inputChange($event,row,'organization')"/>
            </template>
            <template slot-scope="{ row }" slot="managerName">
              <Input v-model="row.managerName"  @on-change="inputChange($event,row,'organization')"/>
            </template>
            <template slot-scope="{ row }" slot="managerPassword">
              <Input v-model="row.managerPassword"  @on-change="inputChange($event,row,'organization')"/>
            </template>
          </Table>
          <div class="append-box">
            <Button @click.stop="appendKey()">新增</Button>
          </div>
        </div>
        <div class="unit-box" v-show="viewState=='unit'">
          <span class="hint">注：每次操作自动保存</span>
          <div class="table-box"  >
            <div class="table-title">
              单位类型（单击选中一行 可操作右侧单位项）
            </div>
            <Table  border @on-current-change="nodeClick" :columns="unitType.columns" highlight-row :data="unitType.content" :max-height="clientHeight * 0.73" :disabled-hover="true">
              <template slot-scope="{ row }" slot="name">
                <Input v-model="row.name"  @on-change="inputChange($event,row,'unitType')" @on-blur="saveUnit"/>
              </template>
              <template slot-scope="{ row }" slot="description">
                <Input v-model="row.description"  @on-blur="saveUnit" @on-change="inputChange($event,row,'unitType')"/>
              </template>
            </Table>
            <div class="append-box">
              <Button @click.stop="appendUnitType()">新增</Button>
            </div>
          </div>
          <div class="table-box" >
            <div class="table-title">
              单位项
            </div>
            <Table  border  :columns="unitItem.columns" :data="unitItem.content" :max-height="clientHeight * 0.73" :disabled-hover="true">
              <template slot-scope="{ row }" slot="name">
                <Input v-model="row.name"  @on-blur="saveUnit" @on-change="inputChange($event,row,'unitItem')"/>
              </template>
              <template slot-scope="{ row }" slot="symbol">
                <Input v-model="row.symbol"  @on-blur="saveUnit" @on-change="inputChange($event,row,'unitItem')"/>
              </template>
              <template slot-scope="{ row }" slot="description">
                <Input v-model="row.description"  @on-blur="saveUnit" @on-change="inputChange($event,row,'unitItem')"/>
              </template>
            </Table>
            <div class="append-box" v-show="unitType.currentRow">
              <Button @click.stop="appendUnitItem()">新增</Button>
            </div>
          </div>
        </div>
      </div>
    </Split>
  </div>
</template>

<script>
import DB from "@lib/Db";
import Sider from '@component/Sider'
import Bus from '@event/bus';
export default {
    components: {
        Sider,
    },

    props: {

    },

    data(){
        return{
            clientHeight:document.documentElement.clientHeight,
            split1:0.15,
            title:'',
            viewState:"",
            organization:{
                columns:[

                    {
                        slot:'controls',
                        title:'操作',
                        width:80,
                        align: 'center',

                    },                {
                        align: 'center',
                        title:'编号',
                        slot:"no"
                    },{
                        slot:'name',
                        title:'单位名称',
                        align: 'center',

                    }
                    ,{
                        slot:'address',
                        title:'单位地址',
                        align: 'center',

                    },{
                        slot:'person',
                        title:'联系人',
                        align: 'center',

                    }
                    ,{
                        slot:'telephone',
                        title:'电话',
                        align: 'center',
                    }
                    ,{
                        slot:'managerName',
                        title:'管理员用户名',
                        align: 'center',
                    },{
                        slot:'managerPassword',
                        title:'管理员密码',
                        align: 'center',
                    }
                ],
                content:[],
            },
            organizationList:[],
            organizationType:0,
            unitType:{
                columns:[{
                    slot:"name",
                    title:"类型名称",
                    align: 'center',
                },{
                    slot:"description",
                    align: 'center',
                    title:"备注"
                }],
                currentRow:null,
                content:[]
            },
            unitItem:{
                columns:[{
                    slot:"name",
                    align: 'center',
                    title:"名称"
                },{
                    slot:"symbol",
                    align: 'center',
                    title:"符号"
                },{
                    align: 'center',
                    slot:"description",
                    title:"备注"
                }],
                content:[]
            }
        }
    },

    watch: {},

    created () {},
    beforeDestroy(){
        Bus.$off('basisData-choose')
    },
    mounted () {
        Bus.$on("basisData-choose",(data)=>{
            switch (data.name){
                case "承制单位":
                    this.viewState = 'organization'
                    this.title = data.name
                    this.organizationType = 1
                    this.processOrganizationList()
                    break;
                case "接装单位":
                    this.viewState = 'organization'
                    this.title = data.name
                    this.organizationType = 2
                    this.processOrganizationList()
                    break;
                case "度量衡":
                    this.viewState = 'unit'
                    this.unitType.currentRow = null
                    this.unitItem.content = []
                    this.unitType.content = DB.Select('unit_types','','')
                    break;
                default:
                    this.viewState = ""
                    break;

            }
        })
    },

    methods: {
        nodeClick(currentRow){
            this.unitType.currentRow = currentRow
            this.unitItem.content = DB.Select('units',"type_id = "+this.unitType.currentRow.id,"")
        },
        saveUnit(){
            this.unitType.content.forEach((item)=>{
                DB.Update('unit_types',{
                    id:item.id,
                    name:item.name,
                    description:item.description
                })
            })
            this.unitItem.content.forEach((item)=>{
                DB.Update('units',{
                    id:item.id,
                    name:item.name,
                    symbol:item.symbol,
                    description:item.description
                })
            })
        },
        appendUnitType(){
            let newUnitType = {
                name:"",
                description:""
            }
            newUnitType.id = DB.Insert("unit_types",newUnitType)
            this.unitType.content.push(newUnitType)
            this.$nextTick(()=>{
                let domList = document.getElementsByClassName("ivu-table-overflowY")
                if(domList[0]){
                    domList[0].scrollTop = domList[0].scrollHeight
                }
            })
        },
        appendUnitItem(){
            let newUnitItem = {
                name:"",
                description:"",
                symbol:"",
                type_id:this.unitType.currentRow.id
            }
            newUnitItem.id = DB.Insert("units",newUnitItem)
            this.unitItem.content.push(newUnitItem)
            this.$nextTick(()=>{
                let domList = document.getElementsByClassName("ivu-table-overflowY")
                if(domList[1]){
                    domList[1].scrollTop = domList[1].scrollHeight
                }
            })
        },
        processOrganizationList(){
            this.organizationList = DB.Select('organizations','type='+this.organizationType,'')
            this.userList = DB.Select("users","","")
            let list = []
            this.organizationList.forEach((item)=>{
                if(item['type'] == this.organizationType){
                    list.push(item)
                }
            })
            list.forEach((item)=>{
                item.person = item['contact']
                let user = this.userList.find((u)=>{
                    return u.id == item['manager_id']
                })
                if(user){
                    item.managerName = user['login_name']
                    item.managerPassword = user['password']
                }
            })
            this.organization.content = list
        },
        remove(row){
            this.organization.content.splice(row._index,1)
        },
        save(){
            let isHave = 0
            debugger
            this.organization.content.forEach((item)=>{
                let user = DB.selectOne('users','login_name = \''+item.managerName+"'")
                let organization = DB.selectOne('organizations','name= \''+item.name+"'")
                if(user&&item.id!=user['organization_id']){
                    isHave = 1
                }else if(organization&&organization.id!=item.id){
                    isHave = 2
                }else{
                    if(item.id){
                        if(item['manager_id']){
                            DB.Update("users",{
                                id:item['manager_id'],
                                "login_name":item.managerName,
                                "password":item.managerPassword,
                            })
                        }else{
                            item['manager_id'] = DB.Insert("users",{
                                "login_name":item.managerName,
                                role:"管理员",
                                "password":item.managerPassword,
                                "organization_id":item.id
                            })
                        }
                        DB.Update("organizations",{
                            id:item.id,
                            "no":item.no,
                            "name":item.name,
                            "address":item.address,
                            "telephone":item.telephone,
                            "contact":item.person,
                            "manager_id":item['manager_id']
                        })
                    }else{
                        item['manager_id'] = DB.Insert("users",{
                            "login_name":item.managerName,
                            role:"管理员",
                            "password":item.managerPassword,
                        })
                        item.id = DB.Insert("organizations",{
                            "no":item.no,
                            "name":item.name,
                            "address":item.address,
                            "telephone":item.telephone,
                            "contact":item.person,
                            "type":this.organizationType,
                            "manager_id":item['manager_id']
                        })
                        DB.Update("users",{
                            "organization_id":item.id,
                            id:item['manager_id']
                        })
                    }
                }
            })
            this.organizationList.forEach((item)=>{
                let organization = this.organization.content.find((c)=>{
                    return c.id == item.id
                })
                if(!organization){
                    DB.Remove('organizations',item.id)
                }
            })
            switch (isHave){
                case 0 :
                    this.$Message.success("保存成功！")
                    break;
                case 1:
                    this.$Message.error("管理员包含重复命名，无法保存！")
                    break;
                case 2:
                    this.$Message.error("组织包含重复命名，无法保存！")
                    break;
            }
        },
        inputChange(e,row,type){
            this[type].content[row._index] = row
        },
        appendKey(){
            this.organization.content.push({
                no:"",
                name:"",
                address:"",
                telephone:"",
                person:""
            })
            this.$nextTick(()=>{
                let domList = document.getElementsByClassName("ivu-table-overflowY")
                if(domList[0]){
                    domList[0].scrollTop = domList[0].scrollHeight
                }
            })
        }
    }
}
</script>

<style>
  #basisData .table-box{
    width: 95%;
    margin: auto;
  }
  #basisData .table-title{
    border:solid 1px #dcdee2;
    margin-top: 20px;
    height: 50px;
    line-height: 50px;
    border-bottom: none;
    text-align: center;
    background: rgb(225,241,249);
    position: relative;
  }
  #basisData .table-title button{
    position: absolute;
    right: 20px;
    top: 0;
    bottom: 0;
    margin: auto;
  }
  #basisData .btn-del{
    width: 20px;
    display: block;
    margin: auto;
  }
  #basisData .append-box{
    border:solid 1px #dcdee2;
    border-top: none;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 50px;
  }
  #basisData .append-box img{
    height: 50%;
  }
  #basisData .ivu-table-wrapper{
    overflow: visible;
  }
  #basisData .unit-box{
    display: flex;
    justify-content: space-around;
    padding-left: 1.5%;
    padding-right: 1.5%;
    padding-top: 2%;
  }
  #basisData .unit-box .hint{
    height: 6%;
    position: absolute;
    top: 0;
    width: 100%;
    left: 3%;
    display: flex;
    align-items: center;
  }
  #basisData .unit-box .table-box{
    width: 47%;
    margin: 0;
  }
  #basisData .unit-box .bottom-box{
    height: 7%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgb(236,241,248);
    position: absolute;
    bottom: 0;
    width: 100%;
  }
</style>