import Table from './table'
import { updateFillValue, extendDeep } from '@lib/util'
import { saveComplexData } from '@api/fill'

const commonTable = {}

extendDeep(commonTable, Table)

commonTable.props.hierarchy = {
  type: Array,
  default: []
}
commonTable.props.val = {
  type: Array,
  default: []
}
commonTable.props.tableName = {
  type: String
},

commonTable.methods.getValue = function () {
  this.value = Object.keys(this.val).length? this.val : [];
  this.converValue()
}
commonTable.methods.save = function () {
  const result = updateFillValue(this.hierarchy, this.beforeSave())
  saveComplexData(this.tableName, result)
  this.$Message.success('保存成功');
}
commonTable.methods.beforeSave = function () {
  const result = []
  if (this.relation.group_by) {
    for (let key in this.rowData) {
      const val = this.rowData[key]
      if (val.column.length) {
        val.column.forEach(item => { // 插入的一条数据
          const obj = this.getChildrenValue(this, item)
          obj[this.relation.group_by] = val.groupBy.value;
          result.push(obj)
        })
      } else {
        let obj = {}
        obj[this.relation.group_by] = val.groupBy.value;
        result.push(obj)
      }

    }


  } else {
    this.columnData.forEach(item => {
      const obj = this.getChildrenValue(this, item)
      result.push(obj)

    })
  }
  return result
}

export default commonTable