<template>
  <div class="configurationTree">
  <Split v-model="split1" min="0" max="200px">
    <div class="left-box" slot="left">
      <div class="tree-box">
        <Tree  ref="tree" empty-text="点击底部按钮创建节点" :data="treeList" @on-select-change="nodeClick" :render="renderContent" class="demo-tree-render"></Tree>
      </div>
      <div class="control-box" v-if="!isView">
        <Button @click.stop="append()">{{chooseData?'新增':'创建'}}</Button>
      </div>
    </div>
    <div class="right-box" slot="right">
      <div class="configuration-box" v-show="chooseData!=null">
        <div class="table-title">
          节点信息
        </div>
        <div class="configuration-info-box">
          <Composite  :isView="isView" v-if="chooseData" ref="composite" :content="item.elementItem.children"></Composite>
        </div>
        <div class="other-list">
          <div class="other-item" v-for="(other,index) in item.otherList" v-if="chooseData">
            <div class="table-title">
              {{other.name}}
            </div>
            <div class="other-info-box">
              <TableList :key="componentKey" :isView="isView" :ref="'ref-'+index" :controls="{isOpen:true,isFirst:true,isOther:true}"  @input="inputChange(other,$event)" :columns="other.columns" :item="other" :expandColumns="other.expandColumns" :content="other.value" v-if="other.type.type==3&&!other.valueSet"></TableList>
            </div>
          </div>
        </div>
      </div>
      <div class="control-box" v-if="!isView" v-show="chooseData!=null">
        <Button @click="save">保存</Button>
      </div>
    </div>
  </Split>
  </div>
</template>

<script>

import { Tree,Table,Input} from 'view-design';
import Bus from "@event/bus";
import DB from "@lib/Db";
import {dp} from "@lib/util"

export default {
   components: {},

    props: {
      item:{
        type:Object,
        default:{}
      },
        isView:{
          type:Boolean,
            default:false,
        }
    },

    data(){

      return{
        split1: 0.25,
        treeList:[],
        chooseData:null,
        configurationList:[],
          componentKey:0
      }
    },
    watch: {
        item(){
            this.load()
        }
    },

    created () {

    },
    beforeDestroy(){
        Bus.$off('configuration-edit-delete')
        Bus.$off('configuration-edit-paste')
        Bus.$off('edit-move')
    },
    mounted () {
        if(!this.isView){
            Bus.$on('configuration-edit-delete',()=>{
                this.removeItem()
            })
            Bus.$on('configuration-edit-copy',()=>{

            })
            Bus.$on('configuration-edit-cut',()=>{

            })
            Bus.$on('configuration-edit-paste',(result)=>{
                switch (result.type){
                    case 0:
                        DB.copyConfigurationNode(result.data.id,this.chooseData.id)
                        break;
                    case 1:
                        DB.moveConfigurationNode(result.data.id,this.chooseData.id)
                        break;
                }
                let index = 0
                if(this.chooseData.children.length){
                    index = DB.selectOne('data','id = '+this.chooseData.children[this.chooseData.children.length - 1].id,"")['serial_no'] + 1
                    DB.Update('data',{
                        id:result.data.id,
                        'serial_no':index
                    })
                }
                this.load()
            })
            Bus.$on('edit-move',(type)=>{
                this.move(this.chooseData.root,this.chooseData.node,this.chooseData,type)
            })
            Bus.$on('import-configuration',()=>{
                this.load()
            })
        }
        this.load()
    },
    methods: {
      load(){
          this.treeList = []
          this.configurationList = DB.Select("data",'"baseline_id" = '+this.item.relation.baselineId+" AND info_config_id = "+this.item.relation.infoConfigId,'"serial_no" ASC')
          if(this.configurationList.length){
              this.item.relation.configurationId = this.configurationList[0].id
              this.createConfigurationTree()
          }else{
              this.chooseData = null
          }
      },
      removeItem(){
          DB.removeConfigurationNode(this.chooseData.id)
          this.remove(this.chooseData.root,this.chooseData.node,this.chooseData)
      },
      save(){
          let obj = this.$refs.composite.getValue()
          if(obj.state){
              this.$Message.success('保存成功！')
              this.chooseData.data = JSON.stringify(obj.value)
              this.$set(this.chooseData,'title',this.getTitle(this.chooseData))
              DB.Update('data',{
                  id:this.chooseData.id,
                  data:this.chooseData.data
              })
              this.item.otherList.forEach((infoConfig,index)=>{
                  let infoObj = this.$refs['ref-'+index][0].getValue()
                  let value = DB.Select("data",'info_config_id='+infoConfig.id+' AND baseline_id = '+this.item.relation.baselineId + ' AND ref_id ='+this.chooseData.id,"")
                  value.forEach((object)=>{
                      DB.Remove("data",object.id)
                  })
                  infoObj.value.forEach((item)=>{
                      let content = item
                      if(typeof item == 'object'){
                          content = JSON.stringify(item)
                      }
                      DB.Insert("data",{
                          info_config_id:infoConfig.id,
                          baseline_id:this.item.relation.baselineId,
                          ref_id:this.chooseData.id,
                          data:content
                      })
                  })
              })
          }else{
              this.$Message.error('保存失败！请按规则填充数据')
          }
      },
      setTitle(title,configuration){
          let key = ""
          this.item.elementItem.children.forEach((child)=>{
              if(child.schema.no == this.item.lists.tree_show_column_no){
                  key = child.name
              }
          })
          if(!configuration.data){
              configuration.data = JSON.stringify({})
          }
          let data = JSON.parse(configuration.data)
          data[key] = title
          configuration.data = JSON.stringify(data)
          this.keyName = key
          return configuration
      },
      getTitle(configuration){
          let key = ""
          this.item.elementItem.children.forEach((child)=>{
              if(child.schema.no == this.item.lists.tree_show_column_no){
                  key = child.name
              }
          })
          let data = JSON.parse(configuration.data)
          this.keyName = key
          return data[key]
      },
      findConfiguration(configurationId,isRoot){
        let children = []
        let configuration = this.configurationList.find((item)=>{
          return item.id == configurationId
        })
        this.configurationList.forEach((item)=>{
          if(item.parent_id == configurationId){
            children.push(this.findConfiguration(item.id))
          }
        })
        configuration.title = this.getTitle(configuration)
        configuration.baselineId = configuration['baseline_id']
        configuration.parentId = configuration.parent_id
        configuration.dataType = 'configuration'
        if(!isRoot){
          configuration.isCanDelete = true
          configuration.isCanCopy = true
          configuration.isCanCut = true
        }
        configuration.isCanExport = true
        configuration.isCanPaste = true
        configuration.children = children
        if(children.length){
          this.$set(configuration,'expand',true)
        }
        return configuration
      },
      createConfigurationTree(){
          let treeObject = this.findConfiguration(this.item.relation.configurationId,true)
          this.treeList.push(treeObject)
          this.$nextTick(()=>{
              this.$set(this.treeList[0],'selected',true)
              if(this.$refs.tree){
                  this.$refs.tree.handleSelect(this.treeList[0].nodeKey)
              }
          })
      },
    createTableList(item,expandColumns,parentSlot){
        let childList = []
        let children = []
        if(!item.title){
            item.title = item.name
        }
        item.slot = (parentSlot?parentSlot+'-':'')+ item.name
        switch (item.type.type){
            case 0:
                expandColumns.push(item)
                break;
            case 1:
                let codeValues = DB.getCodeValues(this.item.relation.templateId,item.type.no)
                item.codeValues = codeValues
                expandColumns.push(item)
                break;
            case 2:
                let schemas = DB.getSchemaComponents(this.item.relation.templateId,item.type.no)
                schemas.forEach((schema)=>{
                    childList.push({
                        type:DB.getTypeByTemplateIdAndNo(this.item.relation.templateId,schema.type_no),
                        schema:schema,
                        name:schema.name
                    })
                })
                childList.forEach((child)=>{
                    let obj = this.createTableList(child,expandColumns,item.slot)
                    if(obj.type.type!=2||obj.children){
                        children.push(child)
                    }
                })
                break;
            case 3:
                if(!item.valueSet){
                    if(!item.value){
                        item.value = []
                    }
                    item.expandColumns = []
                    item.lists = DB.getTableSchema(this.item.relation.templateId,item.type.no)
                    item.lists.element_type.children.forEach((schema)=>{
                        let child = {
                            type:DB.getTypeByTemplateIdAndNo(this.item.relation.templateId,schema.type_no),
                            schema:schema,
                            name:schema.name,
                            title:schema.title
                        }
                        childList.push(child)
                    })
                    if(!item.lists.element_type.children.length){
                        childList.push({
                            type:item.lists.element_type,
                            name:"值"
                        })
                    }
                    childList.forEach((child)=>{
                        let obj = this.createTableList(child,item.expandColumns)
                        if(obj.type.type!=2||obj.children){
                            children.push(child)
                        }
                    })
                    item.columns = DB.extendDeep([],children,true )
                    children = []
                }
                if(expandColumns){
                    expandColumns.push(item)
                }
                break;
        }
        if(children.length){
            item.children = children
        }
        return item
    },
    createComposite(item,value){
        item.children = []
        let childList = []
        item.value = value
        if(!item.title){
            item.title = item.name
        }
        if(item.schema){
            item.id = item.schema.id
        }
        switch (item.type.type){
            case 0:

                break;
            case 1:
                let codeValues = DB.getCodeValues(this.item.relation.templateId,item.type.no)
                item.codeValues = codeValues
                break;
            case 2:
                let schemas = DB.getSchemaComponents(this.item.relation.templateId,item.type.no)
                schemas.forEach((schema)=>{
                    childList.push({
                        type:DB.getTypeByTemplateIdAndNo(this.item.relation.templateId,schema.type_no),
                        schema:schema,
                        name:schema.name,
                        title:schema.title,
                    })
                })
                childList.forEach((child)=>{
                    let childValue = child.value
                    if(value&&value[child.name]!==null&&value[child.name]!==undefined){
                        childValue = value[child.name]
                    }
                    if(!childValue&&childValue!=0){
                        childValue = ""
                    }
                    let obj = this.createComposite(child,childValue)
                    if(obj.children.length||obj.type.type!=2){
                        item.children.push(obj)
                    }
                })
                break;
            case 3:
                this.createTableList(item)
                break;
        }
        return item
    },
      chooseCurrentData(data){
            this.item.elementItem = this.createComposite(this.item.elementItem,JSON.parse(data.data))
            this.item.otherList.forEach((infoConfig)=>{
                  let value = DB.Select("data",'info_config_id='+infoConfig.id+' AND baseline_id = '+this.item.relation.baselineId + ' AND ref_id ='+data.id,"")
                  let newValue = []
                  value.forEach((object)=>{
                      let item = object['data']
                      if(DB.isJSON(object['data'])){
                          item = JSON.parse(object['data'])
                      }
                      newValue.push(item)
                  })
                  infoConfig.value = newValue
                  this.createTableList(infoConfig)
            })
          this.componentKey++
            this.chooseData = data
            this.chooseData.keyName = this.keyName
            Bus.$emit("chooseData",this.chooseData)
      },
      nodeClick(nodeList,node){
          node.selected = true
            this.chooseCurrentData(node)



      },
      renderContent (h, { root, node, data }) {
        data.root = root
        data.node = node
          const parentKey = root.find(el => el === node).parent;
          if(parentKey!=null){
              const parent = root.find(el => el.nodeKey === parentKey).node;
              let index = parent.children.findIndex((el)=>el.nodeKey === node.nodeKey)
              data.isCanMoveUp = index!=0
              data.isCanMoveDown = index != parent.children.length - 1
          }
        return h('span', {
          style: {
            display: 'inline-block',
            width: '100%',
            // opacity:data.isNew?'0.5':'1'
          }
        }, [
          h('span', [
    
            h('span', data.title)
          ])
        ]);
      },
      append () {
        let data = this.chooseData
        let child = {
            title: "新建节点",
            selected:true,
            isCanDelete:true,
            isCanCopy:true,
            isCanCut:true,
            isCanPaste:true,
            isCanMove:true,
            dataType:'configuration',
            children:[]
        }
        let index = 0
        let info = {}
        let children =  [];
        if(data){
            children = data.children;
            if(children.length){
                index = DB.selectOne('data','id = '+children[children.length - 1].id,"")['serial_no'] + 1
            }
            info.parent_id = data.id
        }
        info.serial_no  = index
        info.info_config_id = this.item.relation.infoConfigId
        info.baseline_id =  this.item.relation.baselineId
        info = this.setTitle(child.title,info)
        child.id = DB.Insert('data', info)
        for(var key in info){
            child[key] = info[key]
        }
        if(data){
            children.push(child);
            data.selected = false
            this.$set(data, 'expand', true);
            this.$set(data, 'children', children);
        }else{
            this.treeList.push(child)
        }
        this.$nextTick(()=>{
            this.$refs.tree.handleSelect(child.nodeKey)
//            this.chooseCurrentData(child)
        })
      },
      move (root, node, data ,type) {
          const parentKey = root.find(el => el === node).parent;
          let index
          let otherIndex
          if(parentKey!=null){
              const parent = root.find(el => el.nodeKey === parentKey).node;
              index = parent.children.findIndex((el)=>el.nodeKey === data.nodeKey);
              parent.children[index].selected = false
              if(type == 'up'){
                  otherIndex = index - 1
              }else{
                  otherIndex = index + 1
              }
              parent.children.splice(otherIndex,1,...parent.children.splice(index,1,parent.children[otherIndex]))
              this.$set(parent.children[otherIndex],'selected',true)
              /*互换序号*/
              let newIndex = DB.selectOne('data','id = '+parent.children[index].id,'')['serial_no']
              let oldIndex = DB.selectOne('data','id = '+parent.children[otherIndex].id,'')['serial_no']
              DB.Update("data",{
                  id:parent.children[otherIndex].id,
                  "serial_no":newIndex
              })
              DB.Update("data",{
                  id:parent.children[index].id,
                  "serial_no":oldIndex
              })
              this.$nextTick(()=>{
                  this.chooseCurrentData(parent.children[otherIndex])
              })
          }
      },
      remove (root, node, data ) {
        const parentKey = root.find(el => el.nodeKey === node.nodeKey).parent;
        let index
        if(parentKey!=null){
          const parent = root.find(el => el.nodeKey === parentKey).node;
          index = parent.children.findIndex(el => el.nodeKey === node.nodeKey);
            parent.children.splice(index, 1);
            if(!parent.children.length){
                this.$set(parent,'expand',false)
            }
            this.$refs.tree.handleSelect(parent.nodeKey)
        }
      }
    }
}
</script>
<style >
