import Vue from 'vue';
import VueRouter from 'vue-router'
import Layout from '@layout/index'
import auth from '@lib/auth'
import Index from '@pages/index/index'

import Design from '@pages/design/index'
import Fill from '@pages/fill/index'
import Result from '@pages/analysis/result'
import Compare from '@pages/analysis/compare'
import Change from '@pages/analysis/change'
import Login from '@pages/login'
import User from '@pages/users/index'
import BasisData from '@pages/basisData/index'
import Technical from '@pages/technical/index'
import TechnicalMain from '@pages/technical/newMain'
import TechnicalDetail from '@pages/technical/detail'
import TechnicalMaintain from '@pages/technical/maintain'
import Condition from '@pages/analysis/index'

Vue.use(VueRouter);

const routes =  [
  {
    path: '/',
    component: Layout,
    meta: { requiresAuth: true },
    props: (route) => ({
      dataType: route.query.dataType,
      id: route.query.id
    }),
    children: [
      {
        path: '',
        component: Index
      }
    ]
  },
  {
    path: '/login',
    component: Login,
    meta: { requiresAuth: false }
  },
  {
    path: '/design',
    component: Layout,
    meta: { requiresAuth: true },
    children: [
      {
        path: '',
        component: Design
      }
    ]
  },
  {
    path: '/fill',
    component: Layout,
    meta: { requiresAuth: true },
    children: [
      {
        path: '',
        component: Fill
      }
    ]
  },
  {
    path: '/technical',
    component: Layout,
    meta: { requiresAuth: true },
    children: [
      {
        path: '',
        component: Technical,
        children:[
            {
                path: 'main',
                component: TechnicalMain,
            },
            {
                path: 'detail',
                component: TechnicalDetail,
                meta: { requiresAuth: true },
                props: {}
            },
            {
                path: 'maintain',
                component: TechnicalMaintain,
                meta: { requiresAuth: true },
                props: {}
            }
        ]
      }
    ] 
  },
    {
        path: '/search',
        component: Layout,
        meta: { requiresAuth: true ,keepAlive:false},
        props: (route) => ({
            channel: route.query.channel // channel = 分析或者检索
        }),
        children: [
            {
                path: '',
                meta: { keepAlive:false},
                component: Condition
            },
        ]
    },
  {
    path: '/analysis',
    component: Layout,
    meta: { requiresAuth: true ,keepAlive:false},
    props: (route) => ({
      channel: route.query.channel // channel = 分析或者检索
    }),
    children: [
      {
        path: '',
          meta: { keepAlive:false},
          component: Condition
      },
      {
        path: 'change',
        component: Change,
        meta: { requiresAuth: true },
        props: {}
      },
      {
        path: 'result',
        component: Result,
        meta: { requiresAuth: true },
        props: (route) => ({
          channel: route.query.channel // channel = 分析或者检索
        })
      },
      {
        path: 'compare',
        component: Compare,
        meta: { requiresAuth: true },
        props: (route) => ({
          channel: route.query.channel // channel = 分析或者检索
        })
      },
      {
        path: 'change',
        component: Change,
        meta: { requiresAuth: true },
        props: (route) => ({
          channel: route.query.channel // channel = 分析或者检索
        })

      }
    ]
  },
  {
    path: '/users',
    component: Layout,
    meta: { requiresAuth: true },
    children: [
      {
        path: '',
        component: User
      }
    ]
  },
    {
        path: '/basisData',
        component: Layout,
        meta: { requiresAuth: true },
        children: [
            {
                path: '',
                component: BasisData
            }
        ]
    }
]
const router = new VueRouter({
  routes
});

/*解决重复路由报错问题*/
const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location) {
    return originalPush.call(this, location).catch(err => err)
}

router.beforeEach((to, from, next) => {
  if (to.matched.some(item => item.meta.requiresAuth)) {
    if (auth.loggedIn()) {
      next()
    } else {
      next({
        path: '/login'
      })
    }
  } else {
    next()
  }
  
})

export default router
