export function isComplexTypes (type) {
  return ['1', '2', '3', '4', '5', '6', '7', '8', '9'].findIndex(item => item === String(type)) !== -1
}

export function dp (info) {
  const e = document.createElement('li')
  e.appendChild(document.createTextNode(info))
  document.getElementById('debug').appendChild(e)
}

export function extendDeep (target, origin, isArray = false) {
  const assign = function (obj, name, value) {
    if (typeof value === 'object' && value !== null) {
      const isArr = Array.isArray(value)
      obj[name] = extendDeep(isArr ? [] : {}, value, isArr)
    } else {
      obj[name] = value
    }
  }
  if (isArray) {
    origin.forEach((item, index) => {
      assign(target, index, item)
    })
  } else {
    Object.entries(origin).forEach(item => {
      assign(target, item[0], item[1])
    })
  }
  return target
}

export function getKeyValue (keys) {
  let obj = keys.reduce((acc, cur) => {
    if (acc) return acc[cur]
    return undefined
  }, fillValue)
  if (!obj) obj = {}
  return obj
}

let fillValue = {}

export function setFillValue (val) {
  fillValue = {}
  fillValue = val
}

export function updateFillValue (keys, value) {
  if (keys.length) {
    keys.reduce((acc, cur, index, arry) => {
      if (!acc[cur]) {
        acc[cur] = {}
      }
      if (index === arry.length - 1) {
        if (typeof value === 'object') {
          if (Array.isArray(value)) {
            acc[cur] = value.slice(0)
          } else {
            acc[cur] = { ...acc[cur], ...value }
          }
        } else {
          acc[cur] = value
        }
      }
      return acc[cur]
    }, fillValue)
  } else {
    fillValue = { ...fillValue, ...value }
  }

  return fillValue
}
