/* eslint-disable no-fallthrough */

import { getDatas } from '@api/design'
import { saveComplexData, getFileId, setFileContent } from '@api/fill'
import { updateFillValue } from '@lib/util'
export default {
  props: {
    // 表示类型
    type: {
      type: Object,
      required: true
    },
    // 表的值
    val: {
      type: Object,
      required: true
    },
    tableName: {
      type: String
    },
    data: {
      type: Object
    },
    // 表示层级关系
    hierarchy: {
      type: Array,
      default: []
    }
  },
  data() {
    return {
      children: [],
      relation: {},
      readOnly: false,
      value: {}
    }
  },
  mounted() { 
    if (this.$route.path == '/') {
      this.readOnly = true
    } else {
      this.readOnly = false
    }
    this.getDetail(this.type)
  },
  watch: {
    type: function (type) {
      this.getDetail(type)
    }
  },
 
  methods: {
    save() {
      let obj = {}
      this.children.forEach(item => {
        if (item.type.type == 2 || item.type.type == 3) return
        if (item.type.no == 8 || item.type.no == 9) {
          // 处理文件类型
          const fileData = {
            name: item.relation.name,
            content: item.value
          }
          fileData.id = getFileId(item.fileType)
          setFileContent(fileData)
          item.value = fileData.id
        }
        obj[item.name] = item.value
      });
      const result = updateFillValue(this.hierarchy, obj)
      console.log(result)
      saveComplexData(this.tableName, result)
      this.$Message.success('保存成功');
    },
    getDetail(type) {

      const result = getDatas(type, this.val)
      this.children = JSON.parse(JSON.stringify(result.children)) 
      if (result.relation) {
        this.relation = JSON.parse(JSON.stringify(result.relation))
      }

    }
  }
}
