<template>
  <div id="login">
    <p class="title">飞机交付技术状态管理系统</p>
    <div class="login-box">

      <div class="input-item">
        <Input v-model="loginInfo.loginName" @on-blur="inputBlur"  placeholder="用户名"/>
      </div>
      <div class="input-item">
        <Input v-model="loginInfo.password" type="password"  placeholder="密码"/>
      </div>
      <div class="bottom-item">
        <div class="controls">
          <div class="save">    <Checkbox v-model="checkState">记住密码</Checkbox>
            </div>
        </div>
        <div class="login-btn"><Button long :size="'large'" @click="login">登录</Button></div>

      </div>

    </div>
  </div>
</template>

<script>
    import DB from "@lib/Db";

    import auth from "@lib/auth";
export default {
    components: {},

    props: {

    },

    data(){
        return{
          loginInfo:{
              loginName:"",
              password:"",
          },
            checkState:false,
            saveMap:{}
        }
    },

    watch: {},

    created () {},

    mounted () {
        let saveMap = localStorage.getItem("user-saveMap")
        let saveName = localStorage.getItem("user-saveName")
        if(saveMap){
            this.saveMap = JSON.parse(saveMap)
            if(saveName){
                this.loginInfo.loginName = saveName
                if(this.saveMap[this.loginInfo.loginName]){
                    this.loginInfo.password = this.saveMap[this.loginInfo.loginName]
                    this.checkState = true
                }
            }

        }

        let users = DB.Select("users","","")
        if(!users.length){
            DB.Insert("users",{
                "login_name":"admin",
                role:"管理员",
                "password":"admin",
                "organization_id":null
            })
        }

    },

    methods: {
        inputBlur(){
            if(this.saveMap[this.loginInfo.loginName]){
                this.loginInfo.password = this.saveMap[this.loginInfo.loginName]
            }
        },
        login(){
            let users = DB.Select("users","","")
            let user = null
            if(this.loginInfo.loginName==''){
                this.$Message.error("请输入用户名！")
            }
            else if(this.loginInfo.password==''){
                this.$Message.error("请输入密码！")

            }else{
                users.forEach((item)=>{
                    if(item['login_name']==this.loginInfo.loginName&&item['password']){
                        user = item
                    }
                })
                if(user){
                    if(user['password']==this.loginInfo.password){
                        if(this.checkState){
                            this.saveMap[this.loginInfo.loginName] = this.loginInfo.password
                        }else{
                            delete this.saveMap[this.loginInfo.loginName]
                        }
                        localStorage.setItem("user-saveMap",JSON.stringify(this.saveMap))
                        localStorage.setItem("user-saveName",this.loginInfo.loginName)
                        DB.currentUser = user
                        auth.login(user)
                        this.$router.push({
                            path:'/'
                        })
                    }else{
                        this.$Message.error("密码输入错误！")
                    }
                }else{
                    this.$Message.error("此用户名不存在！")
                }
            }

        },
        selectChange(e,row){
            this.content[row._index].role = row.role
            this.content[row._index].organizationId = row.organizationId
        },
        inputChange(e,row){
            this.content[row._index].loginName = row.loginName
            this.content[row._index].password = row.password
        },
        appendKey(){
            this.content.push({
                loginName:"",
                password:"",
                role:"",
                organizationId:""
            })
            this.$nextTick(()=>{
                let domList = document.getElementsByClassName("ivu-table-overflowY")
                if(domList.length){
                    domList[0].scrollTop = domList[0].scrollHeight
                }
            })

        }
    }
}
</script>

<style type="text/css">
  #login{
    background:url(../assets/login/startup.png);
    background-size: 100% 100%;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  #login .title{
    position: absolute;
    color: #fff;
    font-size: 2.5vw;
    top: 9vw;
    font-weight: bold;
    width: 100vw;
    text-align: center;
  }
  #login .login-box{
    width: 430px;
    height: 320px;
    background: rgba(255,255,255,0.5);
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 30px;
  }
  #login .login-box .input-item{
    width: 80%;
    margin-top: 32px;
    height: 50px;
  }
  #login .login-box .input-item .ivu-input-wrapper{
    width: 100% !important;
    height: 100%;
  }
  #login .login-box .input-item .ivu-input{
    height:100% !important;
  }
  #login .login-box .bottom-item{
    width: 80%;
    margin-top: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  #login .login-box .bottom-item .controls{
    width: 40%;
    display: flex;
    justify-content: space-between;
  }
  #login .login-box .bottom-item .controls .save{
  }
  #login .login-box .bottom-item .controls .forget{
  }
  #login .login-box .bottom-item .login-btn{
    width: 150px;
    height: 50px;
    font-size: 18px;
  }
</style>