import Vue from 'vue'

function dynamicRender(h, prop) {
  switch (this.data.type.type) {
    case 1:
    case '1': //枚举

      return h('Select', {
        props: {
          value: this.data.value,
          disabled: this.data.disabled,
        },
        on: {
          'on-change': value => {
            this.data.value = value
          }
        }
      }, [
        this.data.options.map((item) => h('Option', {
          props: {
            value: item.no,
            label: item.value
          }
        }))
      ])

    case 3:
    case '3': //列表
      return null
    case 2:
    case '2': {
      let desc = {}
      try {
        desc = JSON.parse(this.data.relation.description)
      } catch (err) {
        console.log(err)
      }
      
      return h('div', [
        h('Select', {
          props: {
            value: this.data.value,
            disabled: this.data.disabled,
          },
          on: {
            'on-change': value => {
              this.data.value = value
            }
          }
        }, [
          this.data.options.map((item) => h('Option', {
            props: {
              value: item[desc.value],
              label: item[desc.show]
            }
          }))
        ]),
        h('Button', {
          props: {
            icon: 'ios-add'
          },
          on: {
            'click': () => {
              alert('点击我了')
            }
          }
        }, `新建${this.data.type.name}`)
      ])
    }

    case 0:
    case '0': // 内建
    {
      switch (this.data.type.no) {
        case 2:
        case '2': // 实数
          return h('FormItem', {
            props: {
              required: this.data.relation.is_required == 1,
              prop,
              rules: [{
                validator: (rule, value, callback) => {
                  let meta = {}
                  try {
                    meta = JSON.parse(this.data.relation.description)
                  } catch (err) {
                    console.log(err)
                  }
                  if (!Number(value)) {
                    return callback(new Error('填写的内容不符合实数类型'))
                  }
                  if (meta.precision) {
                    let decima = value.split('.')[1]
                    if (decima.length != meta.precision) {
                      return callback(new Error(`内容类型的精度需要有${meta.precision}位小数`))
                    }

                  }
                  callback()

                },
                trigger: 'blur'
              }]
            }
          }, [
            h('Input', {
              props: {
                value: this.data.value,
                disabled: this.data.disabled,
              },
              on: {
                'input': value => {
                  console.log(value)
                  this.data.value = value
                }
              }
            })
          ])
        case 3: // 文本
        case '3':
          return h('FormItem', {
            props: {
              required: this.data.relation.is_required == 1,
              prop
            }
          }, [
            h('Input', {
              props: {
                value: this.data.value,
                disabled: this.data.disabled,
              },
              on: {
                'input': value => {
                  console.log(value)
                  this.data.value = value
                }
              }
            })
          ])
        case 5: // 数值
        case '5':
          return h('FormItem', {
            props: {
              required: this.data.relation.is_required == 1,
              prop,
              rules: [{
                validator: (rule, value, callback) => {
                  
                  if (!Number(value)) {
                    return callback(new Error('填写的内容不符合数值类型'))
                  }
                  
                  callback()

                },
                trigger: 'blur'
              }]
            }
          }, [
            h('Input', {
              props: {
                value: this.data.value,
                disabled: this.data.disabled,
              },
              on: {
                'input': value => {
                  console.log(value)
                  this.data.value = value
                }
              }
            })
          ])
        case 1:
        case '1': //整形
          return h('FormItem', {
            props: {
              required: this.data.relation.is_required == 1,
              prop,
              rules: [{
                validator: (rule, value, callback) => {
                  let meta = {}
                  try {
                    meta = JSON.parse(this.data.relation.description)
                  } catch (err) {
                    console.log(err)
                  }
                  if (!Number(value)) {
                    return callback(new Error('填写的内容不符合数字类型'))
                  }
                  if (!Number.isInteger(Number(value))) {
                    return callback(new Error('填写的内容是不是整数'))
                  }
                  if (meta.min) {
                    if (Number(meta.min) > Number(value)) return callback(new Error(`填写的内容不能小于${meta.min}`))
                  }
                  if (meta.max) {
                    if (Number(meta.max) < Number(value)) return callback(new Error(`填写的内容不能大于${meta.max}`))
                  }
                  callback()
                },
                trigger: 'blur'
              }]
            }
          }, [
            h('Input', {
              props: {
                value: this.data.value,
                disabled: this.data.disabled,
              },
              on: {
                'input': value => {
                  console.log(value)
                  this.data.value = value
                }
              }
            })
          ])

      }
    }


  }
}
Vue.component('Attributes', {
  props: {
    data: {
      type: Object,
      required: true
    },
    index: {
      required: true
    }

  },
  methods: {
    removeItem() {
      if (this.data.disabled) return
      this.remove(this.index)
    },
    moveUp() {
      if (this.data.disabled) return
      this.sort('up', this.index)
    },
    moveDown() {
      if (this.data.disabled) return
      this.sort('down', this.index)
    },
    selectUnit() {
      if (this.data.disabled) return
      this.openUnit(this.index)
    }

  },
  render: function (h) {
    const prop = `${this.index}.value`;
    return h('Row', {
        props: {
          type: 'flex',
          align: 'middle',
          'class-name': 'item-list'
        }
      },
      [
        h('Col', {
            attrs: {
              class: 'attr-required'
            }
          },
          [
            this.data.relation.is_required == '0' ? null : '※'
          ]),
        h('Col', {
            attrs: {
              class: ''
            },

          },
          this.data.name),
        h('Col', {
            attrs: {
              class: 'col-auto'
            }
          },[dynamicRender.bind(this, h,prop)]
          )
      ]
    )
  }
})