import { getFileValue } from '@api/fill'
export default {
  props: {
    tableName: {
      type: String
    },
    data: {
      type: Object,
      required: true
    },
    val: {
      type: Object
    },
    type: {
      type: Object,
      required: true
    },
    index: {
      type: Number,
    },
    id: {
      type: Number
    }
  },
  data() {
    return {
      readOnly: false,
      fileId: null
    }
  },
  computed: {
    isEnumType: function () {
      return this.type.type == 1
    }
  },
  mounted() {
    if (this.$route.path == '/') {
      this.readOnly = true
    } else {
      this.readOnly = false
    }
    if (this.type.no == 8 || this.type.no == 9) {
      this.getFile()
    }
  },
  methods: {
    getFile() {
      if (this.data.value) {
        this.fileId = this.data.value
        this.data.value = getFileValue(this.tableName, this.data.value, this.data.ref.relation.name)
      }
    },

    openFile() {
      if (db_openfile) {
        db_openfile(this.fileId)
      }
    },
    handleUploadImage(file) {
      const reader = new FileReader()
      reader.onload = () => {
        this.data.value = reader.result
        this.data.fileType = file.type
      }
      reader.readAsDataURL(file)
      return false
    },
    handleUploadFile(file) {
      const reader = new FileReader()
      reader.onload = () => {
        this.data.value = new Blob([reader.result])
        this.data.fileType = file.type
      }
      reader.readAsArrayBuffer(file)
      return false
    }
  }
}