
<template>
    <div id="sidebar">
        <Spin size="large" fix v-if="spinShow">
            <Icon type="ios-loading" size=18 class="demo-spin-icon-load"></Icon>
            <div>正在创建模板...</div>
        </Spin>
        <div class="main" v-show="index==pageIndex" v-for="(page,index) in pageList" :key="index">
            <div class="template-select" v-if="routePath=='/analysis'">
                <Select v-model="templateIndex" @on-change="templateChange">
                    <Option v-for="(item,index) in templateList" :value="index" :key="index">{{ item.name }}</Option>
                </Select>
            </div>
            <div class="trees">
                <Tree :load-data="loadData" :ref="'tree-'+index" :show-checkbox="page.showCheckBox"  empty-text="" :data="page.treeList" @on-check-change="nodeCheck" @on-select-change="nodeClick" :render="renderContent" class="demo-tree-render"></Tree>
            </div>
            <div class="btn-append" v-show="isCanAppend">
                <img src="../assets/sidebar/分割线@2x.png" class="line">
                <div class="btn-box">
                        <Button @click="appendTagSwitch(page)">新增</Button>
                </div>
            </div>
        </div>
        <div class="foot" v-if="pageList.length>1">
            <div @click="choosePage(index)"  class="page-tag" v-for="(page,index) in pageList"  :key="index" :class="pageIndex==index?'choose':'default'">{{page.name}}</div>
        </div>
        <Modal
                v-model="modal.showState"
                class-name="modal"
                :closable="false"
                :mask-closable="false"
                :styles="{top: '0',width:'420px'}"
        >
            <div slot="header" class="modal-header">
                <div class="title">{{modal.title}}</div>
                <div class="control">
                    <img src="../assets/sidebar/关闭1@3x.png" @click="modalCancel">
                    <img src="../assets/sidebar/确认1@3x.png" @click="modalConfirm">
                </div>
            </div>
            <div class="infoConfig-info" v-show="modal.viewState=='infoConfig'">
                <div class="modal-info-item">
                    <div class="must">※</div>
                    <div class="label">名称：</div>
                    <Input v-model="modal.infoConfig.info.name" placeholder="请输入名称" />
                </div>
                <div class="modal-info-item">
                    <div class="must">※</div>
                    <div class="label">类型：</div>
                    <RadioGroup v-model="modal.infoConfig.info.type" >
                        <Radio  :label="0" >
                            数据包
                        </Radio>
                        <Radio  :label="1" >
                            数据项
                        </Radio>
                    </RadioGroup>
                </div>
            </div>
            <div class="template-list" v-show="modal.viewState == 'templateModel'">
                <div class="template-item" v-for="(template,index) in templateList" :key="index" :class="index == modal.model.templateIndex?'choose':''">
                    <span class="check" @click="checkTemplateModel(index)">
                        <img v-show="index == modal.model.templateIndex" src="../assets/sidebar/勾选 拷贝@3x.png">
                    </span>
                    <span class="name">{{template.name}}</span>
                </div>
            </div>
            <div class="template-list" v-show="modal.viewState == 'template'">
                <div class="template-item" v-for="(template,index) in templateList" :key="index" :class="index == modal.template.index?'choose':''">
                    <span class="check" @click="checkTemplate(index)">
                        <img v-show="index == modal.template.index" src="../assets/sidebar/勾选 拷贝@3x.png">
                    </span>
                    <span class="name">{{template.name}}</span>
                </div>
            </div>
            <div class="model-info" v-show="modal.viewState == 'model'">
                <div class="modal-info-item">
                    <div class="must">※</div>
                    <div class="label">机型名称：</div>
                    <Input v-model="modal.model.info.name" placeholder="请输入机型名称" />
                </div>
                <div class="modal-info-item">
                    <div class="must">※</div>
                    <div class="label">机型分类：</div>
                    <Select v-model="modal.model.info.typeNo" style="width: auto;">
                        <Option v-for="(item,index) in modal.model.codeList" :value="item.no" :key="index">{{ item.value }}</Option>
                    </Select>
                </div>
                <div class="modal-info-item">
                    <div class="must">※</div>
                    <div class="label">状态号：</div>
                    <Input v-model="modal.model.info.stateNo" placeholder="请输入状态号" />
                </div>
                <div class="modal-info-item">
                    <div class="must">※</div>
                    <div class="label">批次号：</div>
                    <Input v-model="modal.model.info.batchNo" placeholder="请输入批次号" />
                </div>
                <div class="modal-info-item">
                    <div class="must" style="visibility: hidden">※</div>
                    <div class="label">备注：</div>
                    <Input v-model="modal.model.info.remark" placeholder="请输入备注"  />
                </div>
            </div>
            <div class="air-info" v-show="modal.viewState == 'air'">
                <div class="modal-info-item">
                    <div class="must">※</div>
                    <div class="label">批次选择：</div>
                    <div class="select-input">
                        <Select v-model="modal.air.info.batchId" style="width: auto;" :disabled="modal.air.viewState=='batch'">
                            <Option v-for="(item,index) in modal.air.batchList" :value="item.id" :key="index">{{ item.title }}</Option>
                        </Select>
                    </div>
                    <span class="btn-create" v-show="modal.air.viewState=='organization'&&false" @click="intoChooseTemplateModel(true)">新建</span>
                </div>
                <div class="modal-info-item">
                    <div class="must">※</div>
                    <div class="label">创建个数：</div>
                    <InputNumber max="10" v-model="modal.air.info.count" placeholder="请输入创建个数" />
                </div>
                <div class="modal-info-item">
                    <div class="must">※</div>
                    <div class="label">接装部队：</div>
                    <div class="select-input">
                        <Select v-model="modal.air.info.organizationId" style="width: auto;" :disabled="modal.air.viewState=='organization'">
                            <Option v-for="(item,index) in modal.air.organizationList" :value="item.id" :key="index">{{ item['name'] }}</Option>
                        </Select>
                    </div>
                    <span class="btn-create" v-show="modal.air.viewState=='batch'&&false" @click="intoCreateOrganization(true)">新建</span>
                </div>
            </div>
            <div slot="footer" class="modal-footer">
                <Button v-show="modal.viewState == 'templateModel'"  size="large"  @click="intoCreateModel()">下一步</Button>
                <Button v-show="modal.viewState != 'templateModel'"  size="large"  @click="modalConfirm">完成</Button>
            </div>
        </Modal>
    </div>
</template>

<script>
import { Tree,Modal,Input} from 'view-design';
import {dp} from '@lib/util'
import Bus from '@event/bus';
import DB from '@lib/Db';
export default {
    name: 'Sider',

    components: {},

    props: {},

    data () {
        return {
            spinShow:false,
            routePath:"",
            pageList:[],
            pageIndex:0,
            templateList:[],//模板
            templateIndex:0,
            typeList:[],//类型
            chooseData:null,
            treeList:[],
            modelList:[],//机型
            stateList:[],//状态
            batchList:[],//批次
            organizationList:[],//组织
            airList:[],//飞机
            baselineList:[],
            configurationList:[],
            isCanAppend:false,
            modal:{
                showState:false,
                viewState:'model',//organization  air  model template
                title:"",
                infoConfig:{
                    info:{
                        name:"",
                        type:0
                    }
                },
                template:{
                    index:0
                },
                model:{
                    isOutSide:false,
                    templateIndex:0,
                    codeList:[],
                    info:{
                        name:"",
                        typeNo:"",
                        stateNo:"",
                        batchNo:"",
                        remark:"",
                    }
                },
                organization:{
                    isOutSide:false,
                    info:{
                        name:"",
                        location:"",
                        person:"",
                        telephone:""
                    }
                },
                air:{
                    viewState:'batch',//organization
                    batchList:[],
                    organizationList:[],
                    info:{
                        count:1,
                        organizationId:0,
                        batchId:0,
                    }
                }
            },

        }
    },

    computed: {
        // Tree
    },

    watch: {

    },

    beforeDestroy(){
        Bus.$off('edit-delete')
        Bus.$off('edit-paste')
        Bus.$off('import-air')
        Bus.$off('import-batch')
        Bus.$off('import-resource')
        Bus.$off('import-template')
        Bus.$off('design-update')
        Bus.$off('design-restore')

        Bus.$off('fill-save')
        Bus.$off('fill-restore')
        Bus.$off('fill-release')
    },
    created () {

    },
    mounted () {
        Bus.$on('import-air',()=>{
            if(this.routePath == "/fill"){
                this.setChooseData(null)
                this.onShowFill()
            }
        })
        Bus.$on('import-batch',()=>{
            if(this.routePath == "/fill"){
                this.setChooseData(null)
                this.onShowFill()
            }
        })
        Bus.$on('import-resource',()=>{
            if(this.routePath == "/fill"){
                this.setChooseData(null)
                this.onShowFill()
            }
        })
        Bus.$on('import-template',()=>{
            if(this.routePath == "/design"){
                this.setChooseData(null)
                this.onShowDesign()
            }
        })
        Bus.$on('edit-delete',()=>{
            this.removeItem()
        })
        Bus.$on('edit-paste',(result)=>{
            if(result.data.dataType == 'air' || result.data.dataType == 'batch'){
                let newInfo = {
                    title:result.data.title,
                    isCanCopy:true,
                    isCanCut:true,
                    iconType:'word',
                    dataType:result.data.dataType,
                    selected:true
                }
                let oldInfo
                this.chooseData.selected = false
                this.chooseData.expand = true
                switch (result.type){
                    case 0:
                        if(result.data.dataType == 'batch'){
                            oldInfo = DB.selectOne("batchs","id = "+result.data.id)
                            oldInfo['state_id'] = this.chooseData.id
                            delete oldInfo['id']
                            newInfo.isCanAppend = true
                            newInfo.stateId = this.chooseData.id
                            newInfo.id = DB.Insert("batchs",oldInfo)
                            newInfo.batchId = newInfo.id
                            newInfo.baselineId = DB.cloneBaseLine(result.data.baselineId,0,newInfo.id,"")
                            newInfo.modelId = DB.selectOne('states','id='+newInfo.stateId)['model_id']
                            newInfo.templateId = DB.selectOne('models','id='+newInfo.modelId)['template_id']
                        }else{
                            oldInfo = DB.selectOne("airs","id = "+result.data.id)
                            oldInfo['use_organization_id'] = this.chooseData.id
                            delete oldInfo['id']
                            newInfo.modelId = result.data.modelId
                            newInfo.stateId = result.data.stateId
                            newInfo.batchId = result.data.batchId
                            newInfo.templateId = result.data.templateId
                            newInfo.organizationId = this.chooseData.id
                            newInfo.id = DB.Insert("airs",oldInfo)
                            newInfo.airId = newInfo.id
                            newInfo.baselineId = DB.cloneBaseLine(result.data.baselineId,1,newInfo.id,"")
                        }
                        break;
                    case 1:
                        newInfo.baselineId = result.data.baselineId
                        if(result.data.dataType == 'batch'){
                            DB.Update('batchs',{
                                id:result.data.id,
                                "state_id":this.chooseData.id
                            })
                            newInfo.isCanAppend = true
                            newInfo.stateId = this.chooseData.id
                            newInfo.modelId = DB.selectOne('states','id='+newInfo.stateId)['model_id']
                            newInfo.templateId = DB.selectOne('models','id='+newInfo.modelId)['template_id']
                            newInfo.id = result.data.id
                            this.remove(result.data.root,result.data.node,result.data)
                        }else{
                            DB.Update('airs',{
                                id:result.data.id,
                                "use_organization_id":this.chooseData.id
                            })
                            newInfo.modelId = result.data.modelId
                            newInfo.stateId = result.data.stateId
                            newInfo.batchId = result.data.batchId
                            newInfo.templateId = result.data.templateId
                            newInfo.organizationId = this.chooseData.id
                            newInfo.id = result.data.id
                            this.remove(result.data.root,result.data.node,result.data)
                        }
                        break;
                }
                newInfo.loading = false
                newInfo.children = []//this.createFillChildren(newInfo)
                this.chooseData.children.push(newInfo)
                this.$nextTick(()=>{
                    this.setChooseData(newInfo)
                })
            }else{
                Bus.$emit('configuration-edit-paste',result)
            }
        })
        this.routePath = this.$route.path
        this.touchtime = new Date().getTime();
        switch (this.routePath){
            case "/":
                this.onShowHome()
                Bus.$on('fill-restore',(isTrash)=>{
                    this.setChooseData(null)
                    this.onShowHome()
                })
                Bus.$on('fill-release',()=>{
                    this.removeItem(true)
                })
                break;
            case "/design"://设计
                Bus.$on('design-update',(data)=>{
                    this.chooseData.title = data.name
                    if(data.dataType=='infoConfig'){

                    }else{
                        this.chooseData.type = DB.getTypeByTemplateIdAndNo(this.chooseData.templateId,data.typeNo)
                    }
                    switch (data.dataType){
                        case 'template':
                            this.chooseData.isNew = false
                            break;
                    }
                })
                this.onShowDesign()
                Bus.$on('design-restore',(data)=>{
                    this.onShowDesign()
                    this.$nextTick(()=>{

                        this.setChooseData(this.pageList[0].treeList[0])
                    })
                })
                break;
            case "/fill"://填报
                this.isCanAppend = true
                this.onShowFill()
                Bus.$on('fill-save',(data)=>{
                    let value
                    let type
                    let schema
                    switch (this.chooseData.dataType){
                        case "trash":
                            if(this.routePath=='/fill'){
                                Bus.$emit("fill-choose",{
                                    dataType:this.chooseData.dataType,
                                })
                            }else if(this.routePath=='/design'){
                                Bus.$emit("design-choose",{
                                    type:-3,
                                },'trash')
                            }
                            break;
                        case "model":
                            this.chooseData.title = data['name']
                            data.id = this.chooseData.id
                            DB.Update('models',data)
                            break;
                        case "state":
                            this.chooseData.title = data['no']
                            data.id = this.chooseData.id
                            DB.Update('states',data)
                            break;
                        case "batch":
                            this.chooseData.title = data['no']
                            data.id = this.chooseData.id
                            DB.Update('batchs',data)
                            break;
                        case "organization":
                            data.id = this.chooseData.id
                            DB.Update('organizations',data)
                            break;
                        case "air":
                            this.chooseData.title = this.chooseData.title.substring(0,this.chooseData.title.lastIndexOf('-')+1) + data['no']
                            data.id = this.chooseData.id
                            DB.Update('airs',data)
                            break;
                        case "infoConfig":
                            type = DB.getTypeByTemplateIdAndNo(this.chooseData.templateId,this.chooseData.type_no)
                            value = DB.Select("data",'info_config_id='+this.chooseData.id+' AND baseline_id = '+this.chooseData.baselineId,"")
                            switch (type.type){
                                case 2:
                                    if(!value.length){
                                        DB.Insert("data",{
                                            info_config_id:this.chooseData.id,
                                            baseline_id:this.chooseData.baselineId,
                                            data:JSON.stringify(data)
                                        })
                                    }else{
                                        DB.Update("data",{
                                            id:value[0].id,
                                            data:JSON.stringify(data)
                                        })
                                    }
                                    break;
                                case 3:
                                    db_executeSql("BEGIN TRANSACTION")
                                    value.forEach((object)=>{
                                        DB.Remove("data",object.id)
                                    })
                                    data.forEach((item)=>{
                                        let content = item
                                        if(typeof item == 'object'){
                                            content = JSON.stringify(item)
                                        }
                                        DB.Insert("data",{
                                            info_config_id:this.chooseData.id,
                                            baseline_id:this.chooseData.baselineId,
                                            data:content
                                        })
                                    })
                                    db_executeSql("COMMIT")
                                    break;
                                default:
                                    if(!value.length){
                                        DB.Insert("data",{
                                            info_config_id:this.chooseData.id,
                                            baseline_id:this.chooseData.baselineId,
                                            data:data,
                                        })
                                    }else{
                                        DB.Update("data",{
                                            id:value[0].id,
                                            data:data,
                                        })
                                    }
                                    break;
                            }
                            break;
                    }
                })
                Bus.$on('fill-release',()=>{
                    this.removeItem(true)
                })
                Bus.$on('fill-restore',(isTrash)=>{
                    this.onShowFill()
                    if(isTrash){
                        this.$nextTick(()=>{
                            this.setChooseData(this.pageList[0].treeList[0])
                        })
                    }
                })
                break;
            case "/analysis"://分析 检索
            case "/search":
                this.onShowAnalysis()
                break;

            case "/technical"://技术状态控制
                this.onShowTechnical()
                break;
            case "/basisData":
                this.onShowBasisData()
                break;
        }
    },

    destroyed () {},

    methods: {
        loadData (item, callback) {
            let data = this.createFillChildren(item)
            callback(data);
        },
        templateChange(data){
            Bus.$emit('analysis-template-change',this.templateList[this.templateIndex].id)
            this.onShowAnalysis()
        },
        nodeCheck(nodeList,node){
            let batchList = []
            let airList = []
            let organizationList = []
            let stateList = []
            switch (this.pageIndex){
                case 0:
                    nodeList.forEach((item)=>{
                        if(item.dataType == 'batch'){
                            batchList.push(item)
                        }else if(item.dataType == 'state'){
                            stateList.push(item)
                        }
                    })
                    this.pageList[2].treeList.forEach((item)=>{
                        let batch = batchList.find((b)=>{
                            return b.id == item.batchId
                        })
                        if(batch){
                            airList.push(item)
                        }
                        this.$set(item,'checked',batch!=null)
                    })
                    this.pageList[1].treeList.forEach((item)=>{
                        let air = airList.find((a)=>{
                            return a.organizationId == item.id
                        })
                        let isHaveChild = false
                        this.pageList[2].treeList.forEach((a)=>{
                            if(a.organizationId == item.id){
                                isHaveChild = true
                            }
                        })
                        if(isHaveChild){
                            this.$set(item,'checked',air!=null)
                        }
                        if(item.checked){
                            organizationList.push(item)
                        }
                    })
                    break;
                case 1:
                    organizationList = nodeList
                    if(node.dataType == 'organization'){
                        this.pageList[2].treeList.forEach((item)=>{
                            let organization = nodeList.find((o)=>{
                                return o.id == item.organizationId
                            })
                            if(organization){
                                airList.push(item)
                            }
                            this.$set(item,'checked',organization!=null)
                        })
                        this.pageList[0].treeList.forEach((model)=>{
                            model.children.forEach((state)=>{
                                state.children.forEach((batch)=>{
                                    let isHaveChild = false
                                    let air = this.pageList[2].treeList.find((a)=>{
                                        return a.checked == true&&a.batchId == batch.id
                                    })
                                    this.pageList[2].treeList.forEach((a)=>{
                                        if(a.batchId == batch.id){
                                            isHaveChild = true
                                        }
                                    })
                                    if(isHaveChild){
                                        if(air!=null){
                                            this.$set(batch,'checked',true)
                                        }else{
                                            this.$refs['tree-0'][0].handleCheck({checked:false,nodeKey:batch.nodeKey})
                                        }
                                    }
                                    if(batch.checked){
                                        batchList.push(batch)
                                    }
                                })
                                let haveCheck = state.children.find((batch)=>{
                                    return batch.checked == true
                                })
                                if(haveCheck || state.checked){
                                    stateList.push(state)
                                }
                            })
                        })
                    }
                    break;
                case 2:
                    airList = nodeList
                    if(node.dataType == 'air'){
                        this.pageList[1].treeList.forEach((item)=>{
                            let isHaveChild = false
                            this.pageList[2].treeList.forEach((a)=>{
                                if(a.organizationId == item.id){
                                    isHaveChild = true
                                }
                            })
                            let air = nodeList.find((a)=>{
                                return a.organizationId == item.id
                            })
                            if(isHaveChild){
                                this.$set(item,'checked',air!=null)
                            }
                            if(item.checked){
                                organizationList.push(item)
                            }
                        })
                        this.pageList[0].treeList.forEach((model)=>{
                            model.children.forEach((state)=>{
                                state.children.forEach((batch)=>{
                                    let isHaveChild = false
                                    let air = this.pageList[2].treeList.find((a)=>{
                                        return a.checked == true&&a.batchId == batch.id
                                    })
                                    this.pageList[2].treeList.forEach((a)=>{
                                        if(a.batchId == batch.id){
                                            isHaveChild = true
                                        }
                                    })
                                    if(isHaveChild){
                                        if(air!=null){
                                            this.$set(batch,'checked',true)
                                        }else{
                                            this.$refs['tree-0'][0].handleCheck({checked:false,nodeKey:batch.nodeKey})
                                        }
                                    }
                                    if(batch.checked){
                                        batchList.push(batch)
                                    }
                                })
                                let haveCheck = state.children.find((batch)=>{
                                    return batch.checked == true
                                })
                                if(haveCheck || state.checked){
                                    stateList.push(state)
                                }
                            })
                        })
                    }
                    break;
            }
            Bus.$emit("analysis-check",{
                airList:airList,
                organizationList:organizationList,
                batchList:batchList,
                stateList:stateList
            })
        },
        createInfoConfig(){
            if(this.modal.infoConfig.info.name == ""){

                return
            }
            const node = this.modal.infoConfig.node
            const parentKey = node.root.find(el => el.nodeKey === node.nodeKey).parent;
            let index
            let parent
            if(parentKey!=null){
                parent = node.root.find(el => el.nodeKey === parentKey).node;
                index = parent.children.indexOf(node);

                let info = {
                    name:this.modal.infoConfig.info.name,
                    template_id:node.templateId,
                    type:this.modal.infoConfig.info.type,
                }
                let restriction = {
                    min: 0,
                    max: Infinity,
                    precision: 5,
                    format: null
                }
                info.restriction_id = DB.Insert("restrictions",restriction)
                if(node.parentId){
                    info.parent_id = node.parentId
                }
                if(info.type == 1){
                    info.type_no = 2
                }
                info.id = DB.Insert('info_configs',info)
                info.title = info.name
                info.dataType = 'infoConfig'
                info.children = []
                info.expand = true
                if(info.type == 0){
                    info.children.push({
                        title:'新建模块',
                        iconType:'append',
                        parentId:info.id,
                        templateId:node.templateId
                    })
                }
                parent.children.splice(index, 0 ,info);
                this.$nextTick(()=>{
                    this.setChooseData(info)
                })
            }
            this.modal.showState = false
        },
        intoCreateInfoConfig(){
            this.modal.title = "新建模块"
            this.modal.infoConfig.info.name = ""
            this.modal.infoConfig.info.type = 0
            this.modal.viewState = 'infoConfig'
            this.modal.showState = true
        },
        intoCreateOrganization(flag){
            this.modal.showState = true
            this.modal.viewState = 'organization'
            this.modal.organization.isOutSide = flag
            this.modal.title = "新建接装单位"
        },
        intoChooseTemplate(){
            this.templateList = DB.Select("templates","state = 0","")
            this.modal.showState = true
            this.modal.viewState = 'template'
            this.modal.title = "克隆模板"
        },
        intoChooseTemplateModel(flag){
