        modalCancel(){
            switch (this.modal.viewState){
                case 'model':
                    if(this.modal.model.isOutSide){
                        this.modal.model.isOutSide = false
                        this.modal.viewState = 'air'
                        this.updateAirModal()
                    }else{
                        this.modal.showState = false
                    }
                    break;
                case 'organization':
                    if(this.modal.organization.isOutSide){
                        this.modal.organization.isOutSide = false
                        this.modal.viewState = 'air'
                        this.updateAirModal()
                    }else{
                        this.modal.showState = false
                    }
                    break;
                default:
                    this.modal.showState = false
                    break;
            }
        },
        modalConfirm(){
            switch (this.modal.viewState){
                case 'infoConfig':
                    this.createInfoConfig()
                    break;
                case 'template':
                    let newTemplate = {
                        type:-2,
                        title: "新建模板",
                        dataType:"template",
                        isCanExport:true,
                        pageIndex:1,
                        selected:true,
                        isCanDelete:true,
                        isNew:true,
                        isCanEdit:true,
                        children:[],
                        expand:true,
                        iconType:'tree',
                    }
                    let cloneTemplateId = this.templateList[this.modal.template.index].id
                    if(this.chooseData){
                        this.chooseData.selected = false
                    }
                    this.spinShow = true
                    setTimeout(()=>{
                        newTemplate.id = DB.cloneTemplate(cloneTemplateId,newTemplate.title,"")
                        newTemplate.children = this.createTemplateChildren(newTemplate)
                        this.pageList[0].treeList.splice(this.pageList[0].treeList.length-1,0,newTemplate)
                        this.setChooseData(newTemplate)
                        this.spinShow = false
                    },300)
                    this.modal.showState = false
                    break;
                case 'templateModel':
                    this.intoChooseTemplateModel()
                    break;
                case 'model':
                    this.createModel()
                    break;
                case 'air':
                    this.createAir()
                    break;
            }
        },
