        intoCreateModel(){
            if(this.modal.model.templateIndex >= 0){
                this.modal.model.codeList = []
                let templateId = this.templateList[this.modal.model.templateIndex].id
                this.typeList.forEach((type)=>{
                    type.id = Number(type.id)
                    if(type.no == 264 && type.template_id == templateId){
                        this.codeList.forEach((code)=>{
                            if(code.parent_no == type.no && code.template_id == templateId){
                                this.modal.model.codeList.push(DB.extendDeep({},code))
                            }
                        })
                    }
                })
                this.modal.viewState = 'model'
                this.modal.title = this.templateList[this.modal.model.templateIndex].name+"-机型信息"
            }
