        createFillChildren(object){
            let children = []
            let infoConfigs = DB.Select('info_configs','template_id = '+object.templateId,'')
            const createInfo = (info)=>{
                if(info.type == 0){
                    info.dataType = 'expand'
                }else{
                    info.dataType = 'infoConfig'
                }
                info.title = info.name
                info.stateId = object.stateId
                info.batchId = object.batchId
                info.modelId = object.modelId
                if(object.dataType == 'air'){
                    info.airId = object.id
                }
                info.templateId = object.templateId
                info.baselineId = object.baselineId
                info.children = []
                infoConfigs.forEach((child)=>{
                    if(child.parent_id == info.id){
                        info.children.push(createInfo(child))
                    }
                })
                return info
            }
            infoConfigs.forEach((info)=>{
                if(!info.parent_id){
                    createInfo(info)
                    children.push(info)
                }
            })
            return children
        },
