                                let state = DB.selectOne('states', 'id='+ batch["state_id"])
                                let model = DB.selectOne('models', 'id='+ state["model_id"])
                                let baseline = DB.selectOne('baselines', '"object_id"='+ air.id+' AND "object_type"=1')
                                let airTitle = model.name+"-"+state.no+'-'+batch.no+'-'+air.no
                                let newAir = {
                                    title: airTitle,
                                    no: air.no,
                                    pageIndex:1,
