                                    organizationId:organization.id,
                                    batchId:batch.id,
                                    stateId:state.id,
                                    modelId:model.id,
                                    templateId:model["template_id"],
                                    baselineId:baseline.id,
                                    isCanEdit:true,
                                    isCanCopy:true,
                                    iconType:"word",
                                    children:[],
                                    dataType:air.dataType
                                }
                                newAir.loading = false
                                newAir.children = []//this.createFillChildren(newAir)
                                if(newAir.baselineId == this.$route.query.id){
                                    newAir.selected = true
                                    this.chooseData = newAir
                                    newOrganization.expand = this.chooseData!=null&&this.$route.query.dataType == 'air'
                                }
                                newOrganization.children.push(newAir)
                            }
