        createOrganizationTree(treeList,keyList,keyIndex,callBack,organizationId){
            let organization = treeList.find((item)=>{
                return item.title == keyList[keyIndex]
            })
            if(!organization){
                organization = {
                    title:keyList[keyIndex],
                    children:[],
                    iconType:"tree",
                    isCanEdit:true,
                    dataType:"organization"
                }
                if(organizationId){
                    this.airList.forEach((air)=>{
                        if(organizationId == air["use_organization_id"]){
                            let baseline = DB.selectOne('baselines', '"object_id"='+ air.id+' AND "object_type"=1')
                            if(baseline.id == this.$route.query.id){
                                organization.expand = true
                            }
                        }
                    })
                }
                treeList.push(organization)
            }
            if(keyList[keyIndex+1]){
                this.createOrganizationTree(organization.children,keyList,keyIndex+1,callBack,organizationId)
            }else{
                callBack(organization)
            }
        },
