        createTemplateChildren(template){
            let children = []
            let infoConfigs = DB.Select('info_configs','template_id = '+template.id,'')
            const createInfo = (info)=>{
                info.children = []
                infoConfigs.forEach((child)=>{
                    if(child.parent_id == info.id){
                        info.children.push(createInfo(child))
                    }
                })
                if(info.type == 0){
                    info.children.push({
                        title:'新建模块',
                        iconType:'append',
                        parentId:info.id,
                        templateId:template.id
                    })
                }
                return info
            }
            infoConfigs.forEach((info)=>{
                info.dataType = 'infoConfig'
                info.title = info.name
                if(!info.parent_id){
                    createInfo(info)
                    children.push(info)
                }
            })
            children.push({
                title:'新建模块',
                iconType:'append',
                templateId:template.id
            })
            return children
        },
