        updateTypeList(){
            this.typeList = DB.Select("types","","")
            this.typeList.forEach((item)=>{
                item.title = item.name
                item.dataType = "type"
                switch (item.type) {
                    case 0:
                        switch (item.id) {
                            case 1:
                                item.iconType='text'
                                break;
                            case 2:
                                item.iconType='richText'
                                break;
                            case 3:
                                item.iconType='numeric'
                                break;
                            case 4:
                                item.iconType='integer'
                                break;
                            case 5:
                                item.iconType='real'
                                break;
                            case 6:
                                item.iconType='date'
                                break;
                            case 7:
                                item.iconType='time'
                                break;
                            case 8:
                                item.iconType='data'
                                break;
                            case 9:
                                item.iconType='image'
                                break;
                        }
                        break;
                    case 1:
                        item.iconType='select'
                        break;
                    case 2:
                        item.iconType='map'
                        break;
                    case 3:
                        item.iconType='array'
                        break;
                }
            })
        },
        removeItem(isView){
            if(this.chooseData){
                if(this.chooseData.dataType == "infoConfig"){
                    Bus.$emit('configuration-edit-delete')
                }
                if((this.chooseData.node&&this.chooseData.isCanDelete) || isView){
                    let chooseData =null
                    switch (this.chooseData.dataType){
                        case "template":
                            let model = DB.selectOne('models','"template_id"='+this.chooseData.id,)
                            if(model){
                                this.$Message.error('已有机型选用此模板，删除失败！')
                                return
                            }
                            DB.Update('templates',{
                                id:this.chooseData.id,
                                state:1
                            })
                            Bus.$emit('design-choose',null)
                            break;
                        case "batch":
                            if(!isView){
                                DB.Update('batchs',{
                                    id:this.chooseData.id,
                                    state:1
                                })
                                this.pageList[1].treeList.forEach((tree)=>{
                                    tree.children = tree.children.filter(item => item.batchId != this.chooseData.id);
                                })
                                let airList = DB.Select('airs',`"batch_id" = ${this.chooseData.id}`,"")
                                airList.forEach((item)=>{
                                    DB.Update('airs',{
                                        id:item.id,
                                        state:1
                                    })
                                })
                            }
                            break;
                        case "air":
                            if(!isView){
                                DB.Update('airs',{
                                    id:this.chooseData.id,
                                    state:1
                                })
                            }
                            break;
                    }
                    this.remove(this.chooseData.root,this.chooseData.node,this.chooseData)
                    this.setChooseData(chooseData)

                }
            }
        },
        appendTagSwitch(page){
            if(this.chooseData){
                this.append(this.chooseData)
            }else{
                switch (this.routePath){
                    case "/fill":
                        if(this.pageIndex == 0){
                            this.intoChooseTemplateModel()
                        }else if(this.pageIndex == 1){
                            this.$Message.error('请选择一个接装单位！')
//                                this.intoCreateOrganization()
                        }
                        break;
                }
            }
        },
        choosePage(index){
            if(this.pageIndex!=index&&this.chooseData){
                this.$set(this.chooseData,'selected',false)
                this.setChooseData(null)
            }
            this.pageIndex = index
        },
        nodeClick(nodeList,node){
            if(node.iconType=='append'){
                node.selected = false
                const parentKey = node.root.find(el => el.nodeKey === node.nodeKey).parent;
                let index
                let parent
                if(parentKey!=null){
                    parent = node.root.find(el => el.nodeKey === parentKey).node;
                    index = parent.children.indexOf(node);

                }
                let child
                switch (node.title){
                    case '新建模块':
                        this.modal.infoConfig.node = node
                        this.intoCreateInfoConfig()

                        break;
                    case '新建模板':
                        this.intoChooseTemplate()
                        if(this.chooseData){
                            this.chooseData.selected = true
                        }
                        break;
                }
                return
            }
            if(node.dataType=='expand'){
                node.selected = false
                this.$set(node,'expand',!node.expand)
                if(this.chooseData){
                    this.chooseData.selected = true
                }
                return
            }
            if( new Date().getTime() - this.touchtime < 500 ){
                if(nodeList[0]){
                    this.setChooseData(nodeList[0])
                }else{
                    if(this.chooseData&&this.chooseData == node){
                        this.setChooseData(null)
                        node.selected = false //双击取消
                    }
                }
            }else{
                if(this.chooseData&&this.chooseData==node){
                    node.selected = true //单击取消
                }else{
                    if(this.chooseData){
                        this.chooseData.selected = true
                    }
                    node.selected = false
                }
                this.touchtime = new Date().getTime();
            }
        },
        renderContent (h, { root, node, data }) {
            data.root = root
            data.node = node
            let children  = [
                h('span', data.title)
            ]
            switch (data.dataType){
                case 'infoConfig':
                    data.isCanEdit = true
                    if(this.routePath == '/design'){
                        data.isCanRemove = true
                    }
                    if(data.type == 0){
                        data.iconType = 'tree'
                    }else{
                        data.iconType = 'word'
                    }
                    break;
            }
            if(data.dataType == 'expand'){
                data.iconType = 'tree'
            }
            if(data.iconType){
                children.unshift(h('Icon', {
                    props: {
                        type: data.iconType?data.iconType:'ios-paper-outline'
                    },
                    style: {
                        marginRight: '8px'
                    }
                }))
            }
            if(data.isCanRemove){
                children.push(h('Button', {
                    props: Object.assign({}, {
                        type: 'default',
                        size: 'small',
                    }, {
                        icon:  'node-remove'
                    }),
                    style: {
                        display: 'inline-block',
                        float: 'right',
                        marginLeft: '5px',
                        border:'none',
                        background: 'transparent',
                    },
                    on: {
                        click: () => { this.remove(root, node, data) }
                    }
                }))
            }
            return h('span', {
                style: {
                    display: 'inline-block',
                    width: '100%',
                    color:data.isNew?'#7070ff':'',
                    opacity:data.isCut?'0.5':'1'
                }
            }, [
                h('span',{style: {
                    display:'flex',
                    'justify-content':'center',
                    'align-items':'center'
                }}, children)
            ]);
        },
        setChooseData(data){
            if(data){
                this.$set(data,'selected',true)
//                data.selected = true
            }
            this.chooseData = data
            Bus.$emit("chooseData",this.chooseData)
            if(this.chooseData){
                if(this.chooseData.isCanEdit || this.chooseData.isCanSend){
                    let value = ""
                    let type
                    let schema
                    let lists
                    switch (this.chooseData.dataType){
                        case "basisData":
                            Bus.$emit("basisData-choose",{
                                name:this.chooseData.title
                            })
                            break;
                        case "trash":
                            if(this.routePath=='/fill'){
                                Bus.$emit("fill-choose",{
                                    dataType:this.chooseData.dataType,
                                })
                            }else if(this.routePath=='/design'){
                                Bus.$emit("design-choose",{
                                    type:-3,
                                },'trash')
                            }
                            break;
                        case "type":
                            Bus.$emit("editType",{
                                id:this.chooseData.id,
                                name:this.chooseData.title,
                                type:this.chooseData.type,
                                template_id:this.chooseData.template_id,
                                no:this.chooseData.no,
                                saved:this.chooseData.isNew?false:true
                            })
                            break;
                        case "template":
                            Bus.$emit("design-choose",{
                                id:this.chooseData.id,
                                name:this.chooseData.title,
                                saved:this.chooseData.isNew?false:true
                            },'template')
                            break;
                        case "model":
                            if(this.routePath.indexOf('/technical')>=0) {
                                Bus.$emit("technical-edit",null)
                            }else{
                                value = DB.selectOne('models', 'id=' + this.chooseData.id)
                                if (!value) {
                                    value = {}
                                }
                                type = DB.getTypeByTemplateIdAndNo(this.chooseData.templateId, DB.selectOne("lists", "parent_no=" + DB.tableNos['models'], "").element_type_no)
                                Bus.$emit("fill-choose", {
                                    id: this.chooseData.id,
                                    name: this.chooseData.title,
                                    title:this.chooseData.title,
                                    relation: {
                                        templateId: this.chooseData.templateId,
                                    },
                                    type: type,
                                    value: value,
                                }, (childList) => {
                                    return DB.processForeignKey(DB.removeUnneedFields(childList, [ 'id', 'template_id', 'state']))
                                })
                            }
                            break;
                        case "state":
                            if(this.routePath.indexOf('/technical')>=0) {
                                Bus.$emit("technical-edit",null)
                            }else{
                                value = DB.selectOne('states','id='+this.chooseData.id)
                                if(!value){
                                    value = {}
                                }
                                type = DB.getTypeByTemplateIdAndNo(this.chooseData.templateId,DB.selectOne("lists","parent_no="+DB.tableNos['states'],"").element_type_no)
                                Bus.$emit("fill-choose",{
                                    id:this.chooseData.id,
                                    title:this.chooseData.title,
                                    name:this.chooseData.title,
                                    relation: {
                                        modelId:this.chooseData.modelId,
                                        templateId:this.chooseData.templateId,
                                    },
                                    type:type,
                                    value:value,
                                },(childList)=>{
                                    return DB.removeUnneedFields(childList,['id','template_d','state','model_id'])
                                })
                            }
                            break;
                        case "batch":
                            if(this.routePath.indexOf('/technical')>=0){
                                Bus.$emit("technical-edit",{
                                    id:this.chooseData.id,
                                    name:this.chooseData.title,
                                    relation: {
                                        modelId:this.chooseData.modelId,
                                        stateId:this.chooseData.stateId,
                                        templateId:this.chooseData.templateId,
                                        baselineId:this.chooseData.baselineId
                                    },
                                })
                            }else if(this.routePath=='/fill'||this.routePath=='/'){
                                value = DB.selectOne('batchs','id='+this.chooseData.id)
                                if(!value){
                                    value = {}
                                }
                                type = DB.getTypeByTemplateIdAndNo(this.chooseData.templateId,DB.selectOne("lists","parent_no="+DB.tableNos['batchs'],"").element_type_no)
                                Bus.$emit("fill-choose",{
                                    id:this.chooseData.id,
                                    name:this.chooseData.title,
                                    title:this.chooseData.title,
                                    relation: {
                                        modelId:this.chooseData.modelId,
                                        stateId:this.chooseData.stateId,
                                        templateId:this.chooseData.templateId,
                                        baselineId:this.chooseData.baselineId
                                    },
                                    type:type,
                                    value:value,
                                    dataType:this.chooseData.dataType
                                },(childList)=>{
                                    return DB.removeUnneedFields(childList,['id', 'state_id', 'air_no_range', 'engine_no_range', 'state'])
                                })
                            }
                            break;
                        case "organization":
                            if(this.routePath.indexOf('/technical')>=0) {
                                Bus.$emit("technical-edit",null)
                            }else{
                                Bus.$emit("fill-choose",null)
                            }
                            break;
                        case "air":
                            if(this.routePath.indexOf('/technical')>=0){
                                Bus.$emit("technical-edit",{
                                    id:this.chooseData.id,
                                    name:this.chooseData.title,
                                    relation:{
                                        templateId:this.chooseData.templateId,
                                        baselineId:this.chooseData.baselineId,
                                        organizationId:this.chooseData.organizationId,
                                        stateId:this.chooseData.stateId,
                                        batchId:this.chooseData.batchId,
                                        modelId:this.chooseData.modelId
                                    },
                                })
                            }else if(this.routePath=='/fill'||this.routePath=='/'){
                                value = DB.selectOne('airs','id='+this.chooseData.id)
                                if(!value){
                                    value = {}
                                }
                                type = DB.getTypeByTemplateIdAndNo(this.chooseData.templateId,DB.selectOne("lists","parent_no="+DB.tableNos['airs'],"").element_type_no)
                                Bus.$emit("fill-choose",{
                                    id:this.chooseData.id,
                                    name:this.chooseData.title,
                                    relation:{
                                        templateId:this.chooseData.templateId,
                                        baselineId:this.chooseData.baselineId,
                                        organizationId:this.chooseData.organizationId,
                                        stateId:this.chooseData.stateId,
                                        batchId:this.chooseData.batchId,
                                        modelId:this.chooseData.modelId
                                    },
                                    type:type,
                                    dataType:this.chooseData.dataType,
                                    value:value,
                                },(childList)=>{
                                    return DB.processForeignKey(DB.removeUnneedFields(childList,["id", 'batch_id',   'state','user_id']))
                                })
                            }
                            break;
                        case "infoConfig":
                            if(this.routePath == '/design'){
                                let sendType = 'type'
                                if(this.chooseData.type == 0){
                                    sendType = 'infoConfig'
                                }
                                Bus.$emit("design-choose",{
                                    name:this.chooseData.title,
                                    id:this.chooseData.id,
                                    templateId:this.chooseData.template_id,
                                },sendType)
                            }else{
                                type = DB.getTypeByTemplateIdAndNo(this.chooseData.templateId,this.chooseData.type_no)
                                value = DB.Select("data",'info_config_id='+this.chooseData.id+' AND baseline_id = '+this.chooseData.baselineId,"")
                                switch (type.type){
                                    case 2:
                                        if(value.length&&value[0]['data']){
                                            value = JSON.parse(value[0]['data'])
                                        }else{
                                            value = {}
                                        }
                                        break;
                                    case 3:
                                        let newValue = []
                                        value.forEach((object)=>{
                                            let item = object['data']
                                            if(DB.isJSON(object['data'])){
                                                item = JSON.parse(object['data'])
                                            }
                                            newValue.push(item)
                                        })
                                        value = newValue
                                        break;
                                    default:
                                        if(!value.length){
                                            value = ""
                                        }else{
                                            value = value[0]['data']
                                        }
                                        break;
                                }
                                Bus.$emit("fill-choose",{
                                    id:this.chooseData.id,
                                    name:this.chooseData.title,
                                    title:this.chooseData.title,
                                    is_required:this.chooseData.is_required,
                                    restriction_id:this.chooseData.restriction_id,
                                    unit_id:this.chooseData.unit_id,
                                    relation:{
                                        templateId:this.chooseData.templateId,
                                        baselineId:this.chooseData.baselineId,
                                        stateId:this.chooseData.stateId,
                                        batchId:this.chooseData.batchId,
                                        modelId:this.chooseData.modelId,
                                        tableNo:this.chooseData.table_no,
                                        infoConfigId:this.chooseData.id
                                    },
                                    dataType:this.chooseData.dataType,
                                    type:type,
                                    value:value,
                                    schema:schema,
                                    saved:true
                                },(childList)=>{
                                    return childList
                                })
                                break;
                            }

                            break;
                    }
                }
            }else{
                switch (this.routePath){
                    case '/':
                    case '/fill':
                        Bus.$emit("fill-choose",null)
                        break;
                    case '/design':
                        Bus.$emit("design-choose",null)
                    default:
                        if(this.routePath.indexOf('/technical')>=0){
                            Bus.$emit("technical-edit",null)
                        }

                        break;
                }
            }
        },
        append (data,item) {
            if(!data.isCanAppend){
                return
            }
            switch (data.dataType){
                case "model":
                    const modelList = data.children
                    let newState = {
                        modelId:data.id,
                        templateId:data.templateId,
                        title: "新建状态",
                        iconType:'tree',
                        selected:true,
                        dataType:"state",
                        isNew:true,
                        isCanEdit:true,
//                        isCanDelete:true,
                        isCanAppend:true,
                        isCanPaste:true,
                        children:[]
                    }

                    modelList.unshift(newState);
                    data.selected = false
                    this.$set(data, 'children', modelList);
                    this.$set(data, 'expand', true);
                    newState.id = DB.Insert('states',{
                        "model_id":data.id,
                        "no":"新建状态"
                    })
                    this.setChooseData(newState)
                    break;
                case "state":
                    const stateList = data.children
                    let newBatch = {
                        title: "新建批次",
                        stateId:data.id,
                        modelId:data.modelId,
                        templateId:data.templateId,
                        iconType:'word',
                        selected:true,
                        dataType:"batch",
                        isNew:true,
                        isCanEdit:true,
                        isCanCopy:true,
                        isCanCut:true,
                        isCanDelete:true,
                        isCanAppend:true,
                    }
                    newBatch.id = DB.Insert('batchs',{
                        "state_id":data.id,
                        "no":"新建批次",
                        "state":"0"
                    })
                    let model = DB.selectOne('models','id ='+DB.selectOne('states','id = '+data.id)["model_id"])
                    newBatch.batchId = newBatch.id
                    newBatch.baselineId = DB.Insert('baselines',{
                        "object_id":newBatch.id,
                        "object_type":0,
                    })
                    newBatch.loading = false
                    newBatch.children = []//this.createFillChildren(newBatch)
                    stateList.unshift(newBatch);
                    data.selected = false
                    this.$set(data, 'children', stateList);
                    this.$set(data, 'expand', true);
                    this.setChooseData(newBatch)
                    break;
                case "organization":
                    this.modal.showState = true
                    this.modal.viewState = 'air'
                    this.modal.air.viewState = 'organization'
                    this.modal.title = "单机数据包创建"
                    this.updateAirModal()
                    this.modal.air.info.organizationId = data.id
                    break;
                case "batch":
//                    this.modal.showState = true
//                    this.modal.viewState = 'air'
//                    this.modal.air.viewState = 'batch'
//                    this.modal.title = "单机数据包创建"
//
//
//                    this.updateAirModal()
//                    this.modal.air.info.batchId = data.id
                    break;
            }

        },
        remove (root, node, data) {
            switch (data.dataType){
                case 'infoConfig':
                    this.setChooseData(null)
                    Bus.$emit('design-choose',null)
                    DB.Remove('info_configs',data.id)
                    break;
            }
            switch (data.nodeType){
                case 'supportConfig':
                    this.setChooseData(null)
                    Bus.$emit('design-choose',null)
                    DB.Remove('support_configs',data.id)
                    break;
                case 'batchExtend':
                    this.setChooseData(null)
                    Bus.$emit('design-choose',null)
                    DB.Remove('schemas',data.id)
                    break;
            }
            setTimeout(()=>{
                const parentKey = root.find(el => el === node).parent;
                let index
                if(parentKey!=null){
                    const parent = root.find(el => el.nodeKey === parentKey).node;
                    index = parent.children.indexOf(data);
                    parent.children.splice(index, 1);
                }else{
                    let i = root.findIndex((el)=>{
                        return el === node
                    })
                    this.pageList.forEach((page)=>{
                        index = page.treeList.findIndex((tree)=>{
                            return tree.id == data.id && (tree.dataType?tree.dataType == data.dataType:true)
                        })
                        if(index>=0){
                            page.treeList.splice(index,1)
                        }
                    })
                }
            },200)


        }
    }
}
</script>
