<template>
  <div class="t-table">
      <div class="header complex-header" v-if="!readOnly">
      </div>
      <a class="content-save" @click="save">
          <Icon type="ios-checkmark" size="20" />
        </a>
      <table class="tableType">
        <colgroup>
        
        </colgroup>
        <thead>
         <tr v-for="(row, rowIndex) in tableData" :key="rowIndex">
          <td v-for="(col, colIndex) in row" :colspan="col.children && col.type.type != 1? col.children.length: 1" :rowspan="col.children && col.type.type != 1? 1: theadRowLength - rowIndex" :key="colIndex">
            {{col.relation.name}}
          </td>
         </tr>
        </thead>
        <tbody class="t-body" v-if="relation.group_by && readOnly">
          <template v-for="(row, rowKey) in rowData" >
            <tr>
              <td :colspan="columnLength">
                <StatusItem :tableName="tableName" :type="row.groupBy.ref.type" :data="row.groupBy"/>
              </td>
            </tr>
            <tr v-for="(item, index) in row.column" :key="index">
              <template v-for="(val, name, subIndex) in item" >
                <td :key="name" v-if="name != 'id'" :class="{'td-file':val.ref.type.no == 8 || val.ref.type.no == 9 }">
                <StatusItem :tableName="tableName" :id="val['id']? val['id'].vaule: ''" :subIndex="index" :configurationId="data.relation.configurationId" :subKey="rowKey" :uploadCb="uploadCallback" :type="val.ref.type" :index="subIndex" :data="val" :key="val.ref.relation.id"/>
                </td>
              </template>
            </tr>
          
          </template>
        </tbody>
        <tbody class="t-body" v-else>
          <tr v-for="(item, index) in columnData" :key="index">
            <td class="t-td t-operate">
              <a @click="addBeforeItem(index)">添加</a>
              <a @click="removeItem(index)"> 删除</a>
            </td>
            <template v-for="(val, name, subIndex) in item">
              <td v-if="name != 'id'" :key="name" :class="{'td-file':val.ref.type.no == 8 || val.ref.type.no == 9 }">
              <StatusItem :tableName="tableName" :id="val['id']? val['id'].vaule: ''" :subIndex="index" :uploadCb="uploadCallback" :configurationId="data.relation.configurationId" :type="val.ref.type" :index="subIndex" :data="val" :key="val.ref.relation.id"/>
              <!-- <TableItem :type="val.type" :index="subIndex" :data="val" :key="val.relation.id"/> -->
              </td>
            </template>
          </tr>
        </tbody>

      </table>
      <Row type="flex" class="t-add-wrapper" justify="center" align="middle" v-if="columnLength && !readOnly" >
          <a href="javascript:void(0)" class="t-add" @click="addColumnData" >
              <img src="@assets/sidebar/添加@2x.png" alt="">
            </a>
          <!-- <Dropdown v-else trigger="click" @on-click="addItem">
            <a href="javascript:void(0)" class="t-add">
              <img src="@assets/sidebar/添加@2x.png" alt="">
            </a>
            <DropdownMenu slot="list" >
              <DropdownItem name="添加分组行">添加{{ relation.group_by }}</DropdownItem>
              <DropdownItem name="添加内容行" v-if="canAddColumn">添加行</DropdownItem>
            </DropdownMenu>
          </Dropdown> -->
      </Row>
    </div>
</template>

<script>
import StatusItem from './StatusItem'
import table from '@mixin/table'
export default {
  
  components: {
    StatusItem
  },
  mixins: [table],
  methods: {
      uploadCallback(obj, rowIndex, rowKey) {
          const rowData = this.rowData[rowKey]
          const column = rowData.column[rowIndex]
          column['path'].value = obj.path
          column['名称'].value = obj.name
          column['附件'].value = obj.content
      }
  }
  
}
</script>

<style>

</style>