<template>
  <div id="technical">
    <Split v-model="split1" min="0" max="200px">
      <Sider slot="left"/>
      <div class="right-box" slot="right">
        <router-view v-if="current"></router-view>
      </div>
    </Split>
  </div>
</template>

<script>
import Sider from '@component/Sider'
import Bus from '@event/bus';

export default {
    data() {
        return {
            split1: 0.15,
            index: 1,
            isHave:false,
            current:null,
        }
    },
    components: {
        Sider,
    },
    beforeDestroy(){
        Bus.$off("technical-edit")
    },
    mounted(){
        Bus.$on("technical-edit",(content)=>{
            if(content){
                if(this.current != content){
                    this.current = null
                    setTimeout(()=>{
                        this.current = content
                        this.$router.push({
                            path:'/technical/main',
                            query:{
                                configurationId:this.current.relation.configurationId,
                                baselineId:this.current.relation.baselineId,
                                templateId:this.current.relation.templateId
                            }
                        })
                    },200)
                }
            }else{
                this.current = null
            }

        })
    }
}
</script>

<style>
  #technical .right-box{
    padding-left: 10px;
    width: 100%;
    height: 100%;
  }
</style>