<template>
  <Row v-if="readOnly" type="flex" justify="start">
    <Col v-if="isEnumType" class="t-td t-value">
      {{ data | enumFormat(true)}}
    </Col>
    <Col v-if="type.no == 2 || type.no == 1 || type.no == 3 || type.no == 4 || type.no == 5" class="col-auto t-td-read">
      {{ data.value}}
    </Col>
    <Col v-if="type.no == 6" class="col-auto t-td-read">
      {{ data | dateFormat(true) }}
    </Col>
    <Col v-if="type.no == 7" class="col-auto t-td-read">
      {{ data | timeFormat(true) }}
    </Col>
    <Col v-if="type.no == 8" class="col-auto t-td-read">
      <a class="" @click="openFile">
        <Icon type="ios-folder-open" />
      </a>
    </Col>
    <Col v-if="type.no == 9" v-viewer class="col-auto t-td-read">
      <img class="img-thumbnail" :src="data.value" alt="">
    </Col>
  </Row>
  <Row v-else type="flex" justify="start" >
    <Col v-if="isEnumType" class="t-td t-value">  
      <Select v-model="data.value" size="small">
        <Option v-for="(item, index) in data.ref.children" :label="item.value" :value="item.id" :key="index"></Option>
      </Select>
    </Col>
    <Col v-else-if="data.ref.isForeignKey" class="t-td">{{data.ref.children[0][data.ref.desc.show]}}</Col>
    <Col v-else class="t-td t-value">
      <Select v-model="data.value" v-if="data.ref.name == '构型节点id'">
        <Option v-for="(item,index) in configurationSelectList" :value="item.id" :key="index">{{item.index}} {{ item.title }}</Option>
      </Select>
      <template v-else>
        <Input v-if="type.no == 2" type="textarea" v-model="data.value" placeholder="请输入..." />
        <Input v-if="(type.no == 1 || type.no == 3 || type.no == 4 || type.no == 5)" v-model="data.value" placeholder="请输入..."/>
        <DatePicker v-if="type.no == 6" v-model="data.value" :format="data.ref.relation.restriction.format" />
        <vue-timepicker v-if="type.no == 7" v-model="data.value" :format="data.ref.relation.restriction.format" />
        <template v-if="type.no == 8">
          <Row type="flex" justify="center" align="middle">
            <Icon type="ios-folder-open" v-if="data.value" class="t-file"/>
            <Upload :before-upload="handleUploadFile" action="/">
              <Button size="small" icon="ios-cloud-upload-outline" shape="circle">上传文件</Button>
            </Upload>
          </Row>
          
          
        </template>
        <template v-if="type.no == 9">
          <Row type="flex" justify="center" align="middle">
            <img class="img-thumbnail" v-if="data.value" :src="data.value" alt="">
            <Upload :before-upload="handleUploadImage" accept="image/png, image/jpeg" action="/">
              <Button size="small" icon="ios-cloud-upload-outline" shape="circle">上传图片</Button>
            </Upload>
          </Row>
          
        </template>
      </template>
      
      
    </Col>
    <Col class="t-td">
      {{data.ref.relation.restriction.unit.value}}
    </Col>
  </Row>
</template>

<script>
import { getConstructorTree, getFileValue } from '@api/fill'
import tableItemMixin from '@mixin/tableItem'
export default {
  props: {
    
    index: {
      type: Number,
    },
    uploadCb: {
        type: Function
    },
    subKey: {
      type: String
    },
    subIndex: {
      type: Number
    },
    configurationId: {
      type: Number
    },
  },
  data() {
    return {
      configurationSelectList:[],
      configurationList:[]
    }
  },
  mixins: [tableItemMixin],
  computed: {
    isEnumType: function () {
      return this.type.type == 1
    }
  },
  mounted() {
    if (this.data.ref.name === '构型节点id') {
      this.configurationList = this.objDeepCopy(getConstructorTree())
      this.findConfiguration(this.configurationId,0)
    }
    
    if (this.$route.path == '/') {
      this.readOnly = true
    } else {
      this.readOnly = false
    }
    if (this.type.no == 8 || this.type.no == 9) {
      this.getFile()
    }
    
  },
  methods: {
    
    objDeepCopy (source) {
      var _typeof = typeof Symbol === "function" && typeof Symbol.iterator === "symbol" ? function (obj) { return typeof obj; } : function (obj) { return obj && typeof Symbol === "function" && obj.constructor === Symbol && obj !== Symbol.prototype ? "symbol" : typeof obj; };
      var sourceCopy = source instanceof Array ? [] : {};
      for (var item in source) {
          sourceCopy[item] = _typeof(source[item]) === 'object' ? this.objDeepCopy(source[item]) : source[item];
      }
      return sourceCopy;
    },
    findConfiguration(configurationId,index){
      let children = []
      let configuration = this.configurationList.find((item)=>{
          return item.id == configurationId
      })

      //只是为了弄下拉框
      configuration.index = index
      this.configurationSelectList.push(configuration)
      //

      this.configurationList.forEach((item)=>{
          if(item.parent_id == configurationId){
              let i = children.length
              children.push(this.findConfiguration(item.id,index+"-"+i))
          }
      })
      configuration.disabled = true
      configuration.title = configuration['名称']
      configuration.baselineId = configuration['基线id']
      configuration.parentId = configuration.parent_id
      configuration.isCanDelete = true
      configuration.children = children
      if(children.length){
          this.$set(configuration,'expand',true)
      }
      return configuration
    },
    
    handleUploadFile(file) {
      const reader = new FileReader()
      reader.onload = () => {
        this.data.value = new Blob([reader.result])
        this.data.fileType = file.type
        /* vm.uploadCb({
          content: new Blob([reader.result]),
          type: file.type,
          path: file.name,
          name: file.name
        }, vm.subIndex, vm.subKey) */
      }
      reader.readAsArrayBuffer(file)
      return false
      }
  }
  
}
</script>

