import DB from "@lib/Db";
class Authentication {
  constructor() {
    this.user = null;
    this.checkSession();
  }
  checkSession() {
    const user = localStorage.getItem('user')
    if (user) {
        localStorage.clear()
      this.user = JSON.parse(user)
    }
  }
  loggedIn() {
    return !!this.user
  }
  /**
   * 登录的时候同步用户信息
   * @param {Object} user  
   */
  login(user) {
    // localStorage.setItem('user', JSON.stringify(user))
    this.user = user
  }
  exit() {
    this.user = null;
    localStorage.removeItem('user')
  }
   getCurrentUserRole (){
        let role = -1 //管理单位 0、1  承制单位  2、3 接装单位 4、5
        if(this.user){
            let o = DB.selectOne('organizations','manager_id = '+this.user.id)
            if(o){
                switch (o['type']){
                    case 0:
                        if(this.user.role == '管理员'){
                            role = 0
                        }else{
                            role = 1
                        }
                        break;
                    case 1:
                        if(this.user.role == '管理员'){
                            role = 2
                        }else{
                            role = 3
                        }
                        break;
                    case 2:
                        if(this.user.role == '管理员'){
                            role = 4
                        }else{
                            role = 5
                        }
                        break;
                }
            }
        }
        return role
    }
}

const auth = new Authentication();

export default auth