import Vue from 'vue'

Vue.filter('dateFormat', function(val) {
    return val.value
})
Vue.filter('timeFormat', function(val) {
    return val.value
})
Vue.filter('enumFormat', function(val, isTable = false) {
    let type = null
    if (isTable) {
        type = val.ref.children.find(item => item.no == val.value)
    } else {
        type = val.children.find(item => item.no == val.value)
    }
    return type.value;
    
}) 
