<!--技术通报名称-->
<!--技术通报号-->
<!--技术状态更改对象-->
<!--技术状态更改说明-->
<!--技术状态更改日期-->
<!--技术状态更改人-->
<!--技术通报文件引入（能够打开）-->
<!--备注-->
<template>
  <div id="technicalMain">
    <div class="table-box">
      <div class="table-title">
        技术状态控制
      </div>
      <Table  border  :columns="columns" :data="content" :max-height="clientHeight * 0.75">
      <template slot-scope="{ row }" slot="time">
          {{DatetimeToStr(row.time)}}
      </template>
        <template slot-scope="{ row }" slot="control_file_id">
            <Button v-show="row.control_file_id" @click="openFile(row.control_file_id)">打开文件</Button>
        </template>
      </Table>
    </div>
    <div class="append-box">
        <Button @click.stop="append()">新增</Button>
    </div>
      <Modal
              v-model="modal.showState"
              class-name="modal"
              :closable="false"
              :mask-closable="false"
              :styles="{top: '0',width:'520px'}"
      >
          <div slot="header" class="modal-header">
              <div class="title">{{modal.title}}</div>
              <div class="control">
                  <img src="../../assets/sidebar/关闭1@3x.png" @click="cancel()"><img src="../../assets/sidebar/确认1@3x.png" @click="confirm()">
              </div>
          </div>
          <div class="control-info" v-show="modal.viewState == 'model'">
              <Form :model="modal.info"  ref="formValidate" :rules="ruleValidate" :label-width="160">
                  <FormItem label="技术通报名称" prop="name">
                      <Input v-model="modal.info.name"></Input>
                  </FormItem>
                  <FormItem label="技术通报号" prop="no">
                      <Input v-model="modal.info.no"></Input>
                  </FormItem>
                  <FormItem label="技术状态更改人" prop="operator">
                      <Input v-model="modal.info.operator"></Input>
                  </FormItem>
                  <FormItem label="技术状态更改对象" prop="object">
                      <Input type="textarea" v-model="modal.info.object"></Input>
                  </FormItem>
                  <FormItem label="技术状态更改说明" prop="abstract">
                      <Input type="textarea" v-model="modal.info.abstract"></Input>
                  </FormItem>
                  <FormItem label="技术通报文件" prop="control_file_id">
                      <Upload
                              multiple
                              type="drag"
                              :before-upload="handleUpload"
                              action="">
                          <div style="padding: 5px 0">
                              <Icon type="ios-cloud-upload" size="30" style="color: #3399ff;width:auto;height: auto"></Icon>
                              <p>选择文件</p>
                          </div>
                      </Upload>
                      <Button v-show="modal.info.control_file_id" @click="openFile(modal.info.control_file_id)">打开文件</Button>
                  </FormItem>
                  <FormItem label="备注">
                      <Input type="textarea" v-model="modal.info.description"></Input>
                  </FormItem>
              </Form>
          </div>
          <div slot="footer" class="modal-footer">
              <Button  size="large"  @click="confirm">确定</Button>
          </div>
      </Modal>
  </div>
</template>

<script>
import Bus from '@event/bus';
import DB from '@lib/Db';
import {dp} from '@lib/util'
export default {
    data() {
        return {
            clientHeight:document.documentElement.clientHeight,
            columns:[
                {
                    slot:'time',
                    title:'时间',
                    align: 'center',
                }
                ,{
                    key:'name',
                    title:'技术通报名称',
                    align: 'center',

                },{
                    key:'no',
                    title:'技术通报号',
                    align: 'center',

                }
                ,{
                    key:'object',
                    title:'技术状态更改对象',
                    align: 'center',
                }
                ,{
                    key:'abstract',
                    title:'技术状态更改说明',
                    align: 'center',
                },
                {
                    key:'operator',
                    title:'技术状态更改人',
                    align: 'center',
                },
                {
                    slot:'control_file_id',
                    title:'文件',
                    align:'center'
                },
                {
                    key:'description',
                    title:'备注',
                    align:'center'
                }
            ],
            content:[],
            modal:{
                showState:false,
                viewState:'model',//organization  air  model template
                title:"新增技术状态变更通报",
                info:{
                    name:"",
                    object:"",
                    no:"",
                    abstract:"",
                    description:"",
                    control_file_id:null,
                    operator:""
                }
            },
            ruleValidate: {
                name: [
                    {
                        required: true,
                        message: '名称不能为空',
                        trigger: 'blur'
                    }
                ],
                no: [
                    { required: true, message: '通报号不能为空', trigger: 'blur' },
                ],
                operator: [
                    {
                        required: true,
                        message: '更改人不能为空',
                        trigger: 'blur'
                    }
                ]
            }
        }
    },
    methods:{
        openFile(id){
            DB.OpenFile(id)
        },
        NumToStr(num){
            if(num<10){
                num = "0"+num
            }
            return num
        },
        DatetimeToStr(datetime){
            let date = new Date(Number(datetime))
            return date.getFullYear()+"-"+this.NumToStr((date.getMonth()+1))+"-"+this.NumToStr(date.getDate())+"  "+this.NumToStr(date.getHours())+":"+this.NumToStr(date.getMinutes())
        },
        confirm(){
            this.$refs.formValidate.validate((valid) => {
                if(valid){
                    let date = Date.now()
                    let info = {}
                    for(var key in this.modal.info){
                        info[key] = this.modal.info[key]
                    }
                    info['baseline_id'] = Number(this.$route.query.baselineId)
                    info['time'] = date
                    DB.Insert("histories",info)
                    this.modal.showState = false
                    this.content.unshift(info)
                }
            })
        },
        cancel(){
            this.modal.showState = false
        },
        handleUpload(file){
            if(file){
                let file_reader = new FileReader();
                let blob = null
                file_reader.onload = () => {
                    let fc = file_reader.result;
                    if(!this.modal.info.control_file_id){
                        this.modal.info.control_file_id = DB.Insert('files',{
                            type:file.type,
                            ext:file.name.substring(file.name.lastIndexOf('.')+1)
                        })
                    }
                    DB.FillFileContent("files",this.modal.info.control_file_id,"content",fc)
                };
                file_reader.readAsArrayBuffer(file);
            }
            return false
        },
        fileChange(e){
            let file = e.target.files[0]
            if(file){
                let file_reader = new FileReader();
                let blob = null
                file_reader.onload = () => {
                    let fc = file_reader.result;
//                    blob = new Blob([fc])
                    this.modal.info.file.content = fc
                    e.target.value = ""
                };
                file_reader.readAsArrayBuffer(file);
                this.modal.info.file = file
                this.modal.info.file.ext = file.name.substring(file.name.lastIndexOf(".")+1)
                this.modal.info.name = file.name
                this.modal.info.fileName = file.name
            }
        },
        intoDetail(changeHistoryId,path,isEdit){
            let query = {
                configurationId:this.$route.query.configurationId,
                baselineId:this.$route.query.baselineId,
                changeHistoryId:changeHistoryId,
                templateId:this.$route.query.templateId,
            }
            if(isEdit){
                query.isEdit = true
            }
            this.$router.push({
                path:'/technical/'+path,
                query:query
            })
        },
        append(){
            this.modal.info = {
                name:"",
                object:"",
                no:"",
                abstract:"",
                description:"",
                control_file_id:null,
                operator:""
            }
            this.modal.showState = true
        }
    },
    mounted(){
        let content = DB.Select("histories",'"baseline_id" = '+this.$route.query.baselineId,"time DESC")
        this.content = content
    },
    components: {

    },
}
</script>

<style>
   .modal .control-info .ivu-input-wrapper{
       width: 100% !important;
   }
  #technicalMain{
    display: flex;
    justify-content: center;
    position: relative;
    width: 100%;
    height: 100%;
  }
  #technicalMain .table-box{
    width: 1600px;

  }
  #technicalMain .table-title{
    border:solid 1px #dcdee2;
    margin-top: 20px;
    height: 50px;
    line-height: 50px;
    border-bottom: none;
    text-align: center;
    background: rgb(225,241,249);
    position: relative;
  }
  #technicalMain .detail-btn{
    width:15px;
    display: block;
    margin: auto;
  }
  #technicalMain .append-box{
    height: 50px;
    position: absolute;
    bottom: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    border-top:solid 1px rgb(225,225,225);
  }
  #technicalMain .append-box img{
    height: 40%;
  }
</style>