
<template>
  <div class="t-table">
      <div class="header complex-header" v-if="!readOnly">
      </div>
      <a class="content-save" @click="save">
          <Icon type="ios-checkmark" size="20" />
        </a>
      <div class="t-body">
        <SingleItem v-if="child.name" :hierarchy="hierarchy" :tableName="tableName" :type="child.type" :data="child" :val="val"/>
      </div>
      
      
    </div>
</template>

<script>
import { getTaskType, getValue, saveComplexData, getFileId, setFileContent } from '@api/fill'
import SingleItem from './SingleItem'
export default {
    props: {
        data: {
            type: Object,
            required: true
        },
        type: {
            type: Object
        },
        tableName: {
          type: String
        },
        hierarchy: {
          type: Array,
          default: () => []
        },
        val: {
          type: Object
        }
    },
    data() {
      return {
        readOnly: false,
        child: {},
        value: {},
        id: ''
      }
    },
    watch: {
      data: function() {
        this.getData()
      }
    },
    mounted() {
      if (this.$route.path == '/') {
      this.readOnly = true
    } else {
      this.readOnly = false
    }
      this.getData()
    },
    components: {
      SingleItem
    },
    methods: {
        save() {
          
          let obj = { ...this.value}
          if (this.child.type.type === 2 || this.child.type.type == 3) {
            this.$Message.success('保存成功')
            return
          }
          if (this.child.type.no == 8 || this.child.type.no == 9) {
          // 处理文件类型
            const fileData = {
              name: this.child.relation.name,
              content: this.child.value
            }
            fileData.id = getFileId(this.child.fileType)
            setFileContent(fileData)
            this.child.value = fileData.id
          }
          obj[this.child.name] = this.child.value
          saveComplexData(this.tableName, obj)
          this.$Message.success('保存成功');
        },
        getValue() {
          this.id = this.tableName == '状态'? this.data.relation.stateId: this.data.relation.batchId
          this.value = getValue(this.tableName, this.id)
           
        },
        getData() {
          this.child = getTaskType(this.type, this.data.id, this.data.subType)
          this.getValue()
          this.child.value = this.value[this.data.name]
        }
    }
}
</script>

