<template>
  <div id="menu">
      <Modal
              v-model="modal.showState"
              class-name="modal"
              :closable="false"
              :mask-closable="false"
              :styles="{top: '0',width:'420px'}"
      >
          <div slot="header" class="modal-header">
              <div class="title">{{modal.title}}</div>
              <div class="control">
                  <img src="../assets/sidebar/关闭1@3x.png" @click="cancel()">
              </div>
          </div>
          <div class="import-box" v-show="modal.viewState=='importConfiguration'||modal.viewState=='importResource'||modal.viewState=='importAir'||modal.viewState=='importBatch'||modal.viewState=='importTemplate'||modal.viewState=='importOrganization'">
              <Form :model="modal.import"  ref="formValidate"  :label-width="100">
                  <FormItem label="打开数据包" prop="path">
                      <div class="info-file">
                          <Input v-model="modal.import.path" readonly placeholder="点击图标选择数据包"></Input>
                          <div class="info-file-choose">
                              <input type="file" id="chooseFile"  @change="fileChange($event)">
                          </div>
                      </div>
                  </FormItem>
              </Form>
             <div class="import-detail">

             </div>
          </div>
          <div class="about-box" v-show="modal.viewState=='about'">
              <p>
                  <span class="label">版本：</span>
                  <span class="value">v1.0.2</span>
              </p>
              <p>
                  <span class="label">版权：</span>
                  <span class="value">航空兵所 保留所有权利</span>
              </p>
              <p>
                  <span class="label">版本时间：</span>
                  <span class="value">2020年12月24日</span>
              </p>
              <p>
                  <span class="label">警告：</span>
                  <span class="value">本计算机程序受著作权法和国际条约保护，未经授权严禁复制和传播</span>
              </p>
          </div>
          <div class="systemHistory-box" v-show="modal.viewState=='systemHistory'">
              <div class="list-box">
                  <p v-for="(item,index) in modal.systemHistory.viewList" :key="index">
                      <span class="time">{{DatetimeToStr(item.time)}}</span>
                      <span class="text">{{item.content}}</span>
                  </p>
              </div>
          </div>
          <div class="backup-box" v-show="modal.viewState=='backup'">
              <Form :model="modal.backup"  ref="backupFormValidate"  :label-width="100" v-show="modal.backup.state==0">
                  <FormItem label="备份版本" prop="version">
                      <Input v-model="modal.backup.version" maxlength="20" placeholder="请输入备份版本"></Input>
                  </FormItem>
                  <FormItem label="备份文件名" prop="fileName">
                      <Input v-model="modal.backup.fileName" maxlength="20" placeholder="请输入文件名" @on-change="fileNameChange"></Input>&nbsp;.db
                  </FormItem>
                  <FormItem label="备注" prop="remark">
                      <Input v-model="modal.backup.remark" maxlength="50" type="textarea" ></Input>
                  </FormItem>
                  <FormItem label="已备份文件" prop="list">
                      <div class="list-box">
                          <p v-for="(item,index) in modal.databaseList" :key="index" @click="chooseDatabase(index)" :class="index==modal.backup.chooseIndex?'choose':''">
                              <span class="version">{{item.version}}</span>
                              <span class="fileName">{{item.fileName}}.db</span>
                          </p>
                      </div>
                  </FormItem>
              </Form>
              <div class="confirm-box" v-show="modal.backup.state==1">
                  监测到文件已存在，执行操作将替换当前文件，是否执行？
              </div>
          </div>
          <div class="automaticBackup-box" v-show="modal.viewState=='automaticBackup'">
              <Form :model="modal.automaticBackup"  :label-width="100" >
                  <FormItem label="备份时间" prop="minute">
                      <InputNumber v-model="modal.automaticBackup.minute" :min="1" :max="1440"></InputNumber>
                      分钟后备份
                  </FormItem>
                  <FormItem label="状态" prop="time">
                      <span v-show="modal.automaticBackup.state==0">未定时</span>
                      <span v-show="modal.automaticBackup.state==1">生效中,{{modal.automaticBackup.time}}秒后自动备份&nbsp;&nbsp;<Button @click="modal.automaticBackup.state=0">取消</Button></span>
                      <span v-show="modal.automaticBackup.state==2">已备份</span>
                  </FormItem>
              </Form>
          </div>
          <div class="restore-box" v-show="modal.viewState=='restore'">
              <Form :model="modal.restore"  :label-width="100" v-show="modal.restore.state==0">
                  <FormItem label="备份版本" prop="version">
                      <span>{{modal.restore.version}}</span>
                  </FormItem>
                  <FormItem label="备份文件名" prop="fileName">
                      <span>{{modal.restore.fileName}}&nbsp;.db</span>
                  </FormItem>
                  <FormItem label="备注" prop="remark">
                      <span style="word-wrap:normal;">{{modal.restore.remark}}</span>
                  </FormItem>
                  <FormItem label="已备份文件" prop="list">
                      <div class="list-box">
                          <p v-for="(item,index) in modal.databaseList" :key="index" @click="chooseDatabase(index)" :class="index==modal.restore.chooseIndex?'choose':''">
                              <span class="version">{{item.version}}</span>
                              <span class="fileName">{{item.fileName}}.db</span>
                          </p>
                      </div>
                  </FormItem>
              </Form>
              <div class="confirm-box" v-show="modal.restore.state==1">
                  恢复将替换当前数据库！是否继续恢复？
              </div>
          </div>
          <div class="release-box" v-show="modal.viewState == 'release'">
              <Form :model="modal.release"  :label-width="100" v-show="modal.restore.state==0">
                  <FormItem label="安装单位" prop="organizationIndex">
                      <Select v-model="modal.release.organizationIndex">
                          <Option v-for="(item,index) in modal.release.organizationList" :value="index" :key="index">{{ item['name']?item['name']:'未知名称' }}</Option>
                      </Select>
                  </FormItem>
                  <FormItem label="单位类型" prop="type">
                      <span v-if="modal.release.organizationList.length">
                          {{modal.release.organizationList[modal.release.organizationIndex]['type'] == 0?'管理':''}}
                          {{modal.release.organizationList[modal.release.organizationIndex]['type'] == 1?'承制':''}}
                          {{modal.release.organizationList[modal.release.organizationIndex]['type'] == 2?'接装':''}}
                      </span>
                  </FormItem>
              </Form>
          </div>
          <div slot="footer" class="modal-footer" v-show="modal.footerState">
              <Button  size="large"  @click="confirm" v-show="modal.viewState=='release'">发布</Button>
              <Button  size="large"  @click="confirm" v-show="modal.viewState=='automaticBackup'">确定</Button>
              <Button  size="large"  @click="confirm" v-show="modal.viewState=='restore'&&modal.restore.state==1">确定</Button>
              <Button  size="large" :disable="modal.databaseList.length"  @click="modal.restore.state=1" v-show="modal.viewState=='restore'&&modal.restore.state==0">恢复</Button>
              <Button  size="large"  @click="backupConfirm" v-show="modal.viewState=='backup'&&modal.backup.state==0">备份</Button>
              <Button  size="large"  @click="cancel" v-show="modal.viewState=='backup'&&modal.backup.state==1">取消</Button>
              <Button  size="large"  @click="confirm" v-show="modal.viewState=='backup'&&modal.backup.state==1">确定</Button>
              <Button  size="large"  @click="importData" v-show="modal.viewState.indexOf('import')>=0&&modal.import.state == 0">导入</Button>
              <Button  size="large"  @click="cancel"  v-show="modal.viewState.indexOf('import')>=0&&modal.import.state != 0">完成</Button>
          </div>
      </Modal>
      <div class="item-menu" v-for="(menu,index) in menuList" :key="index" v-if="(menu.name=='文件'&&(role==0||role==2||role==4))||menu.name!='文件'">
          <Dropdown @on-click="itemClick">
              <div style="  width: 77px;height: 28px;text-align: center">{{menu.name}}</div>
              <DropdownMenu slot="list" v-if="menu.name=='文件'" >
                  <Dropdown placement="right-start">
                      <DropdownItem name="import">
                          导入
                          <Icon type="ios-arrow-forward"></Icon>
                      </DropdownItem>
                      <DropdownMenu slot="list">
                          <DropdownItem name="importAir" :disabled="!menu.import.airState">单机数据包</DropdownItem>
                          <DropdownItem name="importBatch" :disabled="!menu.import.batchState">批次数据包</DropdownItem>
                          <!--<DropdownItem name="importResource" :disabled="!menu.import.resourceState">保障资源数据包</DropdownItem>-->
                          <DropdownItem name="importConfiguration" :disabled="!menu.import.configurationState">构型数据包</DropdownItem>
                          <DropdownItem name="importTemplate" :disabled="!menu.import.templateState">模板数据包</DropdownItem>
                          <DropdownItem name="importOrganization" :disabled="!menu.import.organizationState">单位数据包</DropdownItem>
                      </DropdownMenu>
                  </Dropdown>
                  <Dropdown placement="right-start">
                      <DropdownItem name="export">
                          导出
                          <Icon type="ios-arrow-forward"></Icon>
                      </DropdownItem>
                      <DropdownMenu slot="list">
                          <DropdownItem name="exportAir" :disabled="!menu.export.airState">单机数据包</DropdownItem>
                          <DropdownItem name="exportBatch" :disabled="!menu.export.batchState">批次数据包</DropdownItem>
                          <!--<DropdownItem name="exportResource" :disabled="!menu.export.resourceState">保障资源数据包</DropdownItem>-->
                          <!--<DropdownItem name="exportConfiguration" :disabled="!menu.export.configurationState">构型数据包</DropdownItem>-->
                          <DropdownItem name="exportTemplate" :disabled="!menu.export.templateState">模板数据包</DropdownItem>
                          <DropdownItem name="exportOrganization" :disabled="!menu.export.organizationState">单位数据包</DropdownItem>
                      </DropdownMenu>
                  </Dropdown>
                  <DropdownItem name="automaticBackup">数据库自动备份</DropdownItem>
                  <DropdownItem name="backup">数据库手动备份</DropdownItem>
                  <DropdownItem name="restore" >数据库恢复</DropdownItem>
                  <DropdownItem name="release" v-show="role==0">程序发布</DropdownItem>
              </DropdownMenu>
              <DropdownMenu slot="list" v-if="menu.name=='编辑'">
                  <DropdownItem :disabled="!menu.isCanDelete" name="edit-delete">删除</DropdownItem>
                  <DropdownItem :disabled="!menu.isCanCopy" name="edit-copy">复制</DropdownItem>
                  <DropdownItem :disabled="!menu.isCanCut" name="edit-cut">剪切</DropdownItem>
                  <DropdownItem :disabled="!menu.isCanPaste" name="edit-paste">粘贴</DropdownItem>
                  <Dropdown placement="right-start">
                      <DropdownItem :disabled="!menu.isCanMoveUp&&!menu.isCanMoveDown">
                          移动
                          <Icon type="ios-arrow-forward"></Icon>
                      </DropdownItem>
                      <DropdownMenu slot="list">
                          <DropdownItem name="edit-move-up" :disabled="!menu.isCanMoveUp">上移</DropdownItem>
                          <DropdownItem name="edit-move-down" :disabled="!menu.isCanMoveDown">下移</DropdownItem>
                      </DropdownMenu>
                  </Dropdown>
              </DropdownMenu>
              <DropdownMenu slot="list" v-if="menu.name=='数据'">
                  <DropdownItem v-show="(item.name=='填报'&&role<4)||(item.name=='设计'&&role<2)||(item.name!='填报'&&item.name!='设计')" :name="item.routePath" :key="itemIndex" v-for="(item,itemIndex) in menu.routeList" :selected="routePath=='/'?item.routePath=='/':(item.routePath!='/'?(routePath.indexOf(item.routePath)>=0?true:false):false)">{{item.name}}</DropdownItem>
              </DropdownMenu>
              <DropdownMenu slot="list" v-if="menu.name=='系统'">
                  <DropdownItem name="systemHistory">系统历史</DropdownItem>
                  <DropdownItem v-show="role==0||role==2||role==4" name="userManager" :selected="'/users'==routePath">用户管理</DropdownItem>
                  <DropdownItem v-show="role<2" name="basisDataManager" :selected="'/basisData'==routePath">基础数据管理</DropdownItem>
                  <DropdownItem name="about">关于</DropdownItem>
              </DropdownMenu>
          </Dropdown>
      </div>
      <div class="currentUser" v-if="user&&organization">
          <span>单位名称：{{organization['name']?organization['name']:'未知名称'}}</span>
          <span>单位性质：{{organization['type']==0?'管理':''}}          {{organization['type']==1?'承制':''}}
          {{organization['type']==2?'接装':''}}</span>
          <span>用户名称：{{user['login_name']}}</span>
          <span>用户权限：{{user['role']}}</span>
          <Button style="margin-left: 20px;background:rgb(208,240,252);" @click="logout" size="small">登出</Button>
          <!--<img src="../assets/menu/登出1@3x.png" style="margin-left: 20px" @click="logout">-->
      </div>
  </div>
</template>

<script>
import Bus from '@event/bus';
import  DB from '@lib/Db'
import auth from "@lib/auth";
import Checkbox from "../../node_modules/view-design/src/components/checkbox/checkbox.vue";
export default {
  name: 'Menu',

  components: {Checkbox},

  props: {},

  data () {
      return {
          routePath:'',
          modal:{
              showState:false,
              footerState:false,
              viewState:'',//organization  air  model template
              title:"",
              import:{
                  path:"",
                  file:null,
                  state:0
              },
              systemHistory:{
                  viewList:[]
              },
              databaseList:[],
              backup:{
                  state:0,
                  chooseIndex:-1,
                  version:"",
                  fileName:"",
                  remark:""
              },
              automaticBackup:{
                  time:0,
                  state:0,
                  minute:0,
                  timer:null
              },
              restore:{
                  state:0,
                  chooseIndex:-1,
                  version:"",
                  fileName:"",
                  remark:""
              },
              release:{
                  organizationIndex:0,
                  organizationList:[]
              }
          },
          menuList:{
              file:{
                  name:"文件",
                  buttonWord:"F",
                  import:{
                      templateState:true,
                      airState:false,
                      batchState:false,
                      organizationState:true,
                      configurationState:false,
                      resourceState:false
                  },
                  export:{
                      templateState:false,
                      airState:false,
                      batchState:false,
                      organizationState:true,
                      configurationState:false,
                      resourceState:false
                  }
              },
              edit:{
                  name:"编辑",
                  buttonWord:"E",
                  isCanAppend:false,
                  appendList:[],
                  isCanCopy:false,
                  isCanCut:false,
                  isCanMoveUp:false,
                  isCanMoveDown:false,
                  isCanDelete:false,
                  isCanPaste:false,
                  currentData:null,
                  ready:{
                      data:null,
                      type:0,//复制  1剪切
                  }
              },
              data:{
                  name:"数据",
                  buttonWord:"D",
                  routeList:[
                      {
                          name:"浏览",
                          routePath:'/'
                      },
                      {
                          name:"设计",
                          routePath:'/design'
                      },
                      {
                          name:"填报",
                          routePath: '/fill'
                      },
//                      {
//                          name:"检索",
//                          routePath:'/search'
//                      },
                      {
                          name:"检索分析",
                          routePath: '/analysis'
                      },
                      {
                          name:"变更",
                          routePath: '/technical'
                      },
                  ]
              },
              system:{
                  name:"系统",
                  buttonWord:"S"
              }
        },
          user:{},
          organization:{

          },
          role:-1
      }
  },

  computed: {},

    watch: {
        $route(to, from) {
            this.routePath = to.path
            this.menuList.edit.isCanPaste = false
            this.menuList.edit.currentData = null
            this.menuList.edit.isCanAppend = false
            this.menuList.edit.isCanCopy = false
            this.menuList.edit.isCanCut = false
            this.menuList.edit.isCanMoveUp = false
            this.menuList.edit.isCanMoveDown = false
            this.menuList.edit.isCanDelete = false
            this.menuList.edit.isCanPaste = false

            this.menuList.file.import.airState = false
            this.menuList.file.import.batchState = false

            this.menuList.file.export.airState = false
            this.menuList.file.export.batchState = false
            this.menuList.file.export.templateState = false
        }
    },
  created () {

  },

  mounted () {
      this.user = auth.user
      this.organization = DB.selectOne('organizations','id='+this.user['organization_id'])
      this.userList = DB.Select('users','','')
      this.role = auth.getCurrentUserRole()
      this.routePath = this.$route.path
      Bus.$on("chooseData",(data)=>{
          if(!data){
              data = {}
          }
          this.menuList.file.import.airState = data.isCanPaste && data.dataType == 'organization' && this.routePath == '/fill'
          this.menuList.file.import.batchState = data.isCanPaste  && data.dataType == 'state' && this.routePath == '/fill'
          this.menuList.file.import.configurationState = data.isCanPaste  && data.dataType == 'configuration' && this.routePath == '/fill'
          this.menuList.file.import.resourceState = data.isCanPaste  && data.dataType == 'fill-property' && this.routePath == '/fill'

          this.menuList.file.export.airState = data.isCanCopy && data.dataType == 'air' && this.routePath == '/'
          this.menuList.file.export.batchState = data.isCanCopy && data.dataType == 'batch' && this.routePath == '/'
          this.menuList.file.export.resourceState = data.isCanExport && data.dataType == 'fill-property' && this.routePath == '/'
          this.menuList.file.export.configurationState = data.isCanExport && data.dataType == 'configuration' && this.routePath == '/'

          this.menuList.file.export.templateState = data.isCanExport && data.dataType == 'template'


          this.menuList.edit.isCanPaste = false
          this.menuList.edit.currentData = data
          this.menuList.edit.isCanAppend = data.isCanAppend
          this.menuList.edit.isCanCopy = data.isCanCopy && this.routePath != '/'
          this.menuList.edit.isCanCut = data.isCanCut && this.routePath != '/'
          this.menuList.edit.isCanDelete = data.isCanDelete && this.routePath != '/'
          this.menuList.edit.isCanMoveUp = data.isCanMoveUp && this.routePath != '/'
          this.menuList.edit.isCanMoveDown = data.isCanMoveDown && this.routePath != '/'
          if(data.isCanPaste&&this.menuList.edit.ready.data&&this.routePath == '/fill'){
              switch (data.dataType){
                  case "configuration":
                      let findChild = (node)=>{
                          let flag = (node.id == data.id)
                          node.children.forEach((child)=>{
                              if(!flag){
                                  flag = findChild(child)
                              }
                          })
                          return flag
                      }
                      if(!data.children.find((child)=>{
                          return child.id == this.menuList.edit.ready.data.id
                      })||this.menuList.edit.ready.type == 0){
                          this.menuList.edit.isCanPaste = !findChild(this.menuList.edit.ready.data)
                      }
                      break;
                  case "state":
                  case "organization":
                      if(!data.children.find((child)=>{
                              return child.id == this.menuList.edit.ready.data.id
                          })||this.menuList.edit.ready.type == 0){
                          this.menuList.edit.isCanPaste = true
                      }
                      break;
              }

          }
      })
  },
    beforeDestroy(){
        Bus.$off("chooseData")

    },
  destroyed () {
  },

  methods: {
      automaticBackup(){
          if(this.modal.automaticBackup.timer){
              clearInterval(this.modal.automaticBackup.timer)
          }
          this.modal.automaticBackup.state = 1
          this.modal.automaticBackup.time = this.modal.automaticBackup.minute * 60
          this.modal.automaticBackup.timer = setInterval(()=>{
              if(this.modal.automaticBackup.time <= 0 ){
                  if(this.modal.automaticBackup.state == 1){
                      this.modal.automaticBackup.state = 2
                      let time = Date.now()
                      this.modal.backup.version = this.DatetimeToVersion("数据库备份-",time)
                      this.modal.backup.fileName = this.DatetimeToVersion("数据库备份-",time)

                      this.modal.backup.time = time
                      this.modal.backup.remark = ""
                      this.backup(()=>{
                          this.modal.databaseList.unshift({
                              version:this.modal.backup.version,
                              fileName:this.modal.backup.fileName,
                              remark:this.modal.backup.remark,
                              time:this.modal.backup.time
                          })
                      })
                  }
              }else{
                  this.modal.automaticBackup.time --

              }
          },1000)
      },
      restore(){
          let state =  db_restore(this.modal.restore.fileName+".db")
          switch (state){
              case 0:
                  this.setSystemHistory({
                      content:"恢复了数据库 版本："+this.modal.restore.version+" 文件："+this.modal.restore.fileName+".db",
                  })
                  this.$Message.success("恢复成功！即将重新登录")
                  setTimeout(()=>{
                      this.logout()
                  },1000)
                  break;
              case -1:
                  this.$Message.error("备份数据文件不存在！")
                  break;
              case -2:
                  this.$Message.error("备份目录不存在！")
                  break;
              case -3:
                  this.$Message.error("恢复失败！")
                  break;
          }
      },
      fileNameChange(e){
          let index = this.modal.databaseList.findIndex((item)=>{
              return item.fileName == this.modal.backup.fileName
          })
          if(index>=0){
              this.modal.backup.chooseIndex = index
              this.modal.backup.version = this.modal.databaseList[index].version
              this.modal.backup.remark = this.modal.databaseList[index].remark
          }else{
              this.modal.backup.chooseIndex = -1
          }
      },
      chooseDatabase(index){
          let key = this.modal.viewState
          this.modal[key].chooseIndex = index
          this.modal[key].version = this.modal.databaseList[index].version
          this.modal[key].remark = this.modal.databaseList[index].remark
          this.modal[key].fileName = this.modal.databaseList[index].fileName
      },
      backup(callBack){
          let state = db_backup(this.modal.backup.fileName+".db")
          switch (state){
              case 0:
                  this.setSystemHistory({
                      content:"备份了数据库 版本："+this.modal.backup.version+" 文件："+this.modal.backup.fileName+".db",
                  })
                  this.$Message.success("备份成功！")
                  if(callBack){
                      callBack()
                      this.setDatabaseList()
                      this.modal.showState = false
                  }
                  break;
              case -2:
                  this.$Message.error("备份目录不存在！")
                  break;
              case -3:
                  this.$Message.error("备份失败！")
                  break;
          }
      },
      backupConfirm(){
          let index = this.modal.databaseList.findIndex((item)=>{
              return item.fileName == this.modal.backup.fileName
          })
          if(index>=0){
              this.modal.backup.state = 1
          }else{
              this.backup(()=>{
                  this.modal.databaseList.unshift({
                      version:this.modal.backup.version,
                      fileName:this.modal.backup.fileName,
                      remark:this.modal.backup.remark,
                      time:Date.now()
                  })
              })
          }
      },
      getDatabaseList(){
          let list = []
          let cache = localStorage.getItem('backup')
          if(cache){
              list = JSON.parse(cache)
          }
          return list
      },
      setDatabaseList(){
          localStorage.setItem('backup',JSON.stringify(this.modal.databaseList))
      },
      logout(){
          auth.exit()
          this.$router.replace({
              path:'/login'
          })
      },
      NumToStr(num){
          if(num<10){
              num = "0"+num
          }
          return num
      },
      DatetimeToVersion(label,datetime){
          let date = new Date(Number(datetime))
          return label+date.getFullYear()+"."+this.NumToStr((date.getMonth()+1))+"."+this.NumToStr(date.getDate())
      },
      DatetimeToStr(datetime){
          let date = new Date(Number(datetime))
          return date.getFullYear()+"-"+this.NumToStr((date.getMonth()+1))+"-"+this.NumToStr(date.getDate())+"  "+this.NumToStr(date.getHours())+":"+this.NumToStr(date.getMinutes())
      },
      cancel(){
          switch (this.modal.viewState){
              case "backup":
                  if(this.modal.backup.state == 1){
                      this.modal.backup.state = 0
                  }else{
                      this.modal.showState = false
                  }
                  break;
              default:
                  this.modal.showState = false
                  break;
          }

      },
      fileChange(e){
          let file = e.target.files[0]
          if(file){
              let file_reader = new FileReader();
              let blob = null
              file_reader.onload = () => {
                  let fc = file_reader.result;
                  this.modal.import.file = fc
                  e.target.value = ""
              };
              switch (this.modal.viewState){
                  case 'importTemplate':
                  case 'importOrganization':
                      file_reader.readAsText(file,'utf-8');
                      break;
                  case 'importAir':
                  case 'importBatch':
                      file_reader.readAsArrayBuffer(file);
                      break;
                  case 'importConfiguration':
                  case 'importResource':
                      file_reader.readAsBinaryString(file)
                      break;
              }
              this.modal.import.path = file.name
          }
      },
      importData(){
          if(!this.modal.import.file){
              this.$Message.error("请选择相关数据包！")
              return
          }
          let successCallBack = (content)=>{
              this.$Message.success('导入成功')

              this.setSystemHistory({
                  content:content,
              })
              this.modal.showState = false
          }
          let state = 0
          switch (this.modal.viewState){
              case "importAir":
                  state = DB.importAir(this.modal.import.file,this.menuList.edit.currentData.id)
                  if(state == 0){
                      Bus.$emit('import-air')
                      successCallBack("导入单机数据包")
                  }
                  break;
              case "importBatch":
                  state = DB.importBatch(this.modal.import.file,this.menuList.edit.currentData.id)
                  if(state == 0){
                      Bus.$emit('import-batch')
                      successCallBack("导入批次数据包")
                  }
                  break;
              case "importTemplate":
                  state = DB.importTemplate(this.modal.import.file)
                  if(state == 0){
                      Bus.$emit('import-template')
                      successCallBack("导入模板数据包")
                  }
                  break;
              case "importOrganization":
                  state = DB.importOrganizations(this.modal.import.file)
                  if(state == 0){
                      successCallBack("导入单位数据包")
                  }
                  break
              case "importResource":
                  state = DB.importResource(this.modal.import.file)
                  if(state == 0){
                      Bus.$emit('import-resource')
                      successCallBack("导入保障资源数据包")
                  }
                  break;
              case "importConfiguration":
                  state = DB.importConfigurationNames(this.modal.import.file,this.menuList.edit.currentData.id,this.menuList.edit.currentData.keyName)
                  if(state == 0){
                      Bus.$emit('import-configuration')
                      successCallBack("导入构型数据包")
                  }
                  break;
          }
      },
      confirm(){
           switch (this.modal.viewState){
               case "release":
                   db_buildInstall(this.modal.release.organizationList[this.modal.release.organizationIndex].id)
                   this.$Message.success("发布成功！")
                   this.modal.showState = false
                   break;
               case "backup":
                   if(this.modal.backup.state == 1){
                       this.backup(()=>{
                           this.modal.databaseList.splice(this.modal.backup.chooseIndex,1)
                           this.modal.databaseList.unshift({
                               version:this.modal.backup.version,
                               fileName:this.modal.backup.fileName,
                               remark:this.modal.backup.remark,
                               time:Date.now()
                           })
                       })
                   }
                   break;
               case "restore":
                   if(this.modal.restore.state == 1){
                       this.restore()
                   }
                   break;
               case "automaticBackup":
                   this.$Message.success("定时成功！")
                   this.automaticBackup()
//                   this.modal.showState=false
                   break;

           }
      },
      getSystemHistory(){
          let list = []
          let cache = localStorage.getItem("systemHistory")
          if(cache){
              list = JSON.parse(cache)
          }else{
              localStorage.setItem("systemHistory",JSON.stringify(list))
          }
          return list
      },
      setSystemHistory(obj){
          obj.time = Date.now()
          obj.userId = auth.user.id
          let list = this.getSystemHistory()
          list.unshift(obj)
          localStorage.setItem("systemHistory",JSON.stringify(list))
      },
      itemClick(name){
          this.modal.import.file = null
          this.modal.import.path = ""
          this.modal.viewState = name
              switch (name){
              case "release":
                  this.modal.title = "程序发布"
                  this.modal.footerState = true
                  this.modal.showState = true
                  this.modal.release.organizationList = DB.Select('organizations','','')
                  this.modal.release.organizationIndex= 0
              break;
              case "restore":
                  this.modal.title = "恢复数据库"
                  this.modal.footerState = true
                  this.modal.showState = true
                  this.modal.databaseList = this.getDatabaseList()
                  if(this.modal.databaseList.length){
                      this.modal.restore.chooseIndex = 0
                      this.modal.restore.version = this.modal.databaseList[0].version
                      this.modal.restore.fileName = this.modal.databaseList[0].fileName
                      this.modal.restore.time = this.modal.databaseList[0].time
                      this.modal.restore.remark = this.modal.databaseList[0].remark
                  }
                  break;
              case "automaticBackup":
                  this.modal.title = "自动备份数据库"
                  this.modal.footerState = true
                  this.modal.showState = true
                  break;
              case "backup":
                  this.modal.title = "备份数据库"
                  this.modal.footerState = true
                  this.modal.showState = true
                  this.modal.databaseList = this.getDatabaseList()
                  let time = Date.now()
                  this.modal.backup.version = this.DatetimeToVersion("数据库备份-",time)
                  this.modal.backup.fileName = this.DatetimeToVersion("数据库备份-",time)
                  this.modal.backup.time = time
                  this.modal.backup.remark = ""
                  this.modal.backup.state = 0
                  this.modal.backup.chooseIndex = -1
                  break;
              case "systemHistory":
                  this.modal.title = "系统历史"
                  this.modal.footerState = false
                  this.modal.showState = true
                  let systemHistoryList = this.getSystemHistory()
                  let viewList = []
                  systemHistoryList.forEach((item)=>{
                      let user =  this.userList.find((u)=>{
                          return u.id == item.userId
                      })
                      if(user){
                          item.content = user['login_name'] +" "+item.content
                      }else{
                          item.content = "未知用户 "+item.content
                      }
                      viewList.push(item)
                  })
                  this.modal.systemHistory.viewList = viewList
                  break;
              case "about":
                  this.modal.title = "关于"
                  this.modal.footerState = false
                  this.modal.showState = true
                  break;
              case "userManager"://用户管理
                  this.$router.push({
                      path:"/users"
                  })
                  break;
              case "basisDataManager"://基础数据管理
                  this.$router.push({
                      path:"/basisData"
                  })
                  break;
              case "exportResource"://导出四随
                  DB.exportResource(this.menuList.edit.currentData.id)
                  this.setSystemHistory({
                      content:"导出保障资源数据包 "+this.menuList.edit.currentData.title,
                  })
                  this.$Message.success("保障资源数据包导出成功！")
                  break;
              case "exportConfiguration"://导出构型
                  DB.exportConfiguration(this.menuList.edit.currentData.id)
                  this.setSystemHistory({
                      content:"导出构型数据包 "+this.menuList.edit.currentData.title,
                  })
                  this.$Message.success("构型数据包导出成功！")
                  break;
              case "exportBatch"://导出批次
                  DB.exportBatch(this.menuList.edit.currentData.id)
                  this.setSystemHistory({
                      content:"导出批次数据包 "+this.menuList.edit.currentData.title
                  })
                  this.$Message.success("批次数据包导出成功！")
                  break;
              case "exportAir"://导出单机包
                  DB.exportAir(this.menuList.edit.currentData.id)
                  this.setSystemHistory({
                      content:"导出单机数据包 "+this.menuList.edit.currentData.title,
                  })
                  this.$Message.success("单机数据包导出成功！")
                  break;
              case "exportTemplate"://导出模板
                  DB.exportTemplate(this.menuList.edit.currentData.id)
                  this.setSystemHistory({
                      content:"导出模板数据包 "+this.menuList.edit.currentData.title
                  })
                  this.$Message.success("模板数据包导出成功！")
                  break;
              case "exportOrganization"://导出单位 & 用户
                  DB.exportOrganizations()
                  this.setSystemHistory({
                      content:"导出单位用户数据包"
                  })
                  this.$Message.success("单位用户数据包导出成功！")
                  break;
              case "importAir":
                  this.modal.title = "导入单机包"
                  this.modal.footerState = true
                  this.modal.showState = true
                  break;
              case "importBatch":
                  this.modal.title = "导入批次包"
                  this.modal.footerState = true
                  this.modal.showState = true

                  break;
              case "importResource":
                  this.modal.title = "导入保障资源包"
                  this.modal.footerState = true
                  this.modal.showState = true

                  break;
              case "importConfiguration":
                  this.modal.title = "导入构型包"
                  this.modal.footerState = true
                  this.modal.showState = true
                  break;
              case "importTemplate":
                  this.modal.title = "导入模板包"
                  this.modal.footerState = true
                  this.modal.showState = true

                  break;
              case "importOrganization":
                  this.modal.title = "导入单位包"
                  this.modal.footerState = true
                  this.modal.showState = true
                  break;
              case "edit-delete": //删除节点  （批次 单机 模板 构型）
                  Bus.$emit("edit-delete","")
                  break;
              case "edit-copy": //拷贝节点 （批次 单机  构型）
                  this.menuList.edit.ready.data = this.menuList.edit.currentData
                  this.menuList.edit.ready.type = 0
                  break;
              case "edit-cut": //剪切节点 （批次 单机  构型）
                  this.menuList.edit.ready.data = this.menuList.edit.currentData
                  this.menuList.edit.ready.type = 1
                  break;
              case "edit-paste"://粘贴节点 （批次 单机  构型）
                  Bus.$emit("edit-paste",{
                      data:this.menuList.edit.ready.data,
                      type:this.menuList.edit.ready.type
                  })
                  break;
              case "edit-move-up": //构型节点移动
                  Bus.$emit("edit-move",'up')
                  break;
              case "edit-move-down"://构型节点移动
                  Bus.$emit("edit-move",'down')
                  break;
                  //路由跳转
              case "/":
              case "/design":
              case "/fill":
              case "/search":
              case "/analysis":
              case "/technical":
                  this.$router.push({
                      path:name
                  })
                  break;
          }
      }
  }
}
</script>
