<template>
    <div id="nav">
        <div class="item-nav" v-show="(item.name=='填报'&&role<4)||(item.name=='设计'&&role<2)||(item.name!='填报'&&item.name!='设计')" v-for="(item,index) in navList" :class="navIndex==index?'choose':'default'" @click="toPage(item)" :key="index">
            <a>
                <img :src="require('../assets/nav/'+item.name+'.png')">
                {{item.name}}
            </a>
        </div>

    </div>
</template>

<script>
    import auth from "@lib/auth";
export default {
    name: 'Nav',
    components: {},

    props: {},

    data () {
        return {
            navList:[
                {
                    name:"浏览",
                    routePath:'/'
                },
                {
                    name:"设计",
                    routePath:'/design'
                },
                {
                    name:"填报",
                    routePath: '/fill'
                },
                {
                    name:"检索分析",
                    routePath: '/analysis'
                },
                {
                    name:"技术状态控制",
                    routePath:'/technical'
                },
            ],
            role:-1,
            navIndex:0
        }
    },

    computed: {},

     watch: {
         $route(to, from) {
             this.routeChange(to.path)
         }
     },

    created () {},

    mounted () {
        this.user = auth.user
        this.role = auth.getCurrentUserRole()
        this.routeChange(this.$route.path)
    },

    destroyed () {},

    methods: {
        toPage(nav){
              this.$router.push({
                  path:nav.routePath
              })
        },
        routeChange(path){
            let navIndex = this.navList.findIndex((item)=>{
                let flag = false
                if(path=="/"){
                    flag = true
                }else if(path.indexOf(item.routePath)>=0&&item.routePath!="/"){
                    flag = true
                }
                return flag
            })
//            if(navIndex>-1){
                this.navIndex = navIndex
//            }
        }
    }
}
</script>
<style>
    #nav {
        background: url(../assets/nav/底图.png);
        background-size: 100% 100%;
        width: 100vw;
        /*height: 2.0833333vw;*/
        height: 40px;
        display: flex;
        align-items: center;
        /*padding-left: 1.25vw;*/
        position: relative;
        padding-left: 24px;

    }
    #nav .item-nav{
        /*width: 4.375vw;*/
        /*height: 1.5625vw;*/
        /*line-height: 1.5625vw;*/
        /*font-size:0.72916666vw;*/
        min-width: 75px;
        height: 30px;
        padding-right: 12px;
        padding-left: 12px;
        line-height: 30px;
        font-size: 14px;
    }
    #nav .item-nav.choose{
        background:url(../assets/nav/选中框.png);
        background-size: 100% 100%;
    }
    #nav .item-nav a{
        text-decoration: none;
        width: 100%;
        height:100%;
        cursor: default;
        user-select: none;
        display: flex;
        justify-content: center;
        align-items: center;
        color: #000000;
    }
    #nav .item-nav.default:hover{
        background:url(../assets/nav/鼠标进入时底图.png);
        background-size: 100% 100%;
    }
    #nav .item-nav img{
        /*width:0.83333366vw;*/
        /*height:0.83333333vw;*/
        /*margin-right: 0.20833333vw;*/
        width: 16px;
        height: 16px;
        margin-right: 4px;
    }

</style>