<template>
  <Tabs type="card">
    <TabPane label="批次">
      <Table border :columns="columns" :data="BatchData" :max-height="clientHeight * 0.78" :disabled-hover="true" >
        <template slot-scope="{row, index}" slot="action">
          <Button @click="restoreData(row, index, 'batchs')">撤销删除</Button>
        </template>
      </Table>
    </TabPane>
    <TabPane label="单机">
      <Table border :columns="columns" :data="AloneData" :max-height="clientHeight * 0.78" :disabled-hover="true" >
        <template slot-scope="{row, index}" slot="action">
          <Button @click="restoreData(row, index, 'airs')">撤销删除</Button>
        </template>
      </Table>
    </TabPane>
  </Tabs>
  
</template>

<script>
import { getRecycleData, restoreData } from '@api/fill'
import Bus from '@event/bus'
export default {
  data() {
    return {
      BatchSelectedAll: false,
      AloneSelectedAll: false,
      BatchData: [],
      AloneData: [],
        clientHeight:document.documentElement.clientHeight,

        columns: [
        {
          title: '名称',
          key: 'no'
        },
        {
          title: '备注',
          key: 'description'
        },
        {
          title: '操作',
          slot: 'action',
          width: 150,
          align: 'center'
        }
      ],

    }
  },
  mounted() {
    this.getData()
  },
  methods: {
    getData() {
      this.BatchData = getRecycleData('batchs')
      this.AloneData = getRecycleData('airs')
    },
    restoreData(row, index, tableName) {
      restoreData(tableName, row)
      Bus.$emit('fill-restore',true)
      if (tableName === 'batchs') {
        this.BatchData.splice(index, 1)
      } else {
        this.AloneData.splice(index, 1)
      }
    }
  }
}
</script>

