import DB from '@lib/Db'
export function getDataById(id) {
  return DB.getBatchById(id)
}
export function getValue(table, id) {
  let result = DB.Select(table, `id = ${id}`, '')[0]
  if (!result) return {}
  for (let key in result) {
    try {
      result[key] = JSON.parse(result[key])
    // eslint-disable-next-line no-empty
    } catch(err) {}
  }
  return result
}
export function getType(table) {
  const type = DB.Select('types', `name = '${table}'`, '')[0]
  console.log(`getType`, type)
  return type
}
export function getConstructorTree() {
  return DB.Select("构型","","")
}
export function getTableData(tableName, query) {
  const result = DB.Select(tableName, query, '')
  console.log('getTableData', result)
  return result;
}
export function saveTableData(tableName, data, isNew = true) {
  if (isNew) {
    DB.Insert(tableName, data)
  } else {
    DB.Update(tableName, data)
  }
  if (tableName === '技术状态文件' && data['附件']) {
    DB.FillFileContent(tableName, data.id, '附件', data['附件']) 
  }
  
}
export function removeTableData(tableName, data) {
  DB.Remove(tableName, data.id.value)
}
export function getFileValue(tableName, id, columnName) {
  return DB.GetFileContent('files', id, columnName)
}

export function  getTableList(type, tableName) {
  const relation = DB.getListElement(type.template_id, type.no);
  
  function getSubChildren(template_id, type_no) {
    const result = []
    const columns = DB.getSchemaComponents(template_id, type_no)

    columns.forEach(column => {
       
      if (['随机设备工具', '机械专业随机备件', '随机资料目录', '随机资料配套目录'].indexOf(tableName) !== -1) {
        if (column.name.indexOf('id') !== -1 && column.name !== 'id') return 
      }
      if (tableName == '技术状态文件') {
        if (['基线id', 'previous_version_id', 'digest', 'state'].indexOf(column.name) !== -1) return 
      }
      if(['有寿件', '双流水', '交付遗留问题及处置情况'].indexOf(tableName) !== -1) {
        if (['基线id', 'id'].indexOf(column.name) !== -1) return 
      }
      let obj = {
        name: column.name,
        relation: column,
        type: DB.getTypeByTemplateIdAndNo(String(template_id), column.type_no),
        value: ''
      }
      if (obj.type.type == 2) {
        obj.children = getSubChildren(template_id, obj.type.no)

      } 
      if (obj.type.type == 1) {
        obj.children = DB.getCodeValues(template_id, obj.type.no)
      }
      if (obj.children) { // 防止没有添加子项
        if (obj.children.length) {
          result.push(obj)
        }
      } else {
        result.push(obj)
      }
      
      
    })
    return result
  }
  try {
    const children = getSubChildren(type.template_id, relation.element_type_no)
    return {
      relation,
      children
    }
  } catch (err) {
    console.log(relation, type)
    return {
      relation,
      children: []
    }
  }
  
}


export function getTaskData(tableName, id) {
  const obj = DB.Select(tableName, `id=${id}`, '')
  if (!obj.length) {
    return {}
  }
  return obj[0]
}
export function getRecycleData(tableName) {
  return DB.Select(tableName, `state = 1`, '' )
}
export function restoreData(tableName, data) {
  DB.Update(tableName, {
    id: data.id,
    state: 0
  })
}
export function getTaskType(type, id, key) {
  const obj = DB.Select('lists', `parent_no = ${type.no}`, '')[0]
  let result = DB.Select('schemas', `template_id = ${type.template_id} AND parent_no = ${obj.element_type_no} AND name = '${key}'`, '')
  if (!result.length) {
    result = DB.Select('schemas', `template_id is null AND parent_no = ${obj.element_type_no} AND name = '${key}'`, '')
  }
  let col = result[0]
  let restriction
  try {
    restriction = JSON.parse(col.restriction)
    if (!restriction) throw Error('null')
  } catch(err) {
    restriction = {
      unit: {},
      min: '',
      max: '',
      precision: '',
      format: ''
    }
  }
  col.restriction = restriction
  return {
    name: col.name,
    relation: col,
    type: DB.getTypeByTemplateIdAndNo(String(type.template_id), col.type_no),
    value: getTaskData('批次', id)[key]||''
  }
  
}
