export function saveComplexData(tableName, data) {
  let obj = {}
  for (let key in data) {
    if (typeof data[key] === 'object') {
      obj [key] = JSON.stringify(data[key])
    } else {
      obj[key] = data[key]
    } 
  }
  DB.Update(tableName, obj)
}

export function getEquipmentType(name, template_id) {
  let type = DB.Select('types', `name = '${name}' AND template_id = ${template_id}`, '')[0];
  if (!type) {
    type = DB.Select('types', `name = '${name}' AND template_id is null`, '')[0];
  }
  return type
}

export function getEuqTableValue(tableName,baseLineId, id) {
  const data = DB.Select(tableName, `基线id = ${baseLineId} AND 序号 LIKE ${id}%`, '' )
  console.log('getEuqTableValue', baseLineId, id, data)
  return data
}

