import DB from '@lib/Db'

/**
 * 获取类型里的每一项数据
 * @param {number} type 类型的类型
 * @param {number} id 类型的ID
 */
export function getDatas (type, val, filterForeign) {
  if(!val){
    val = {}
  }
  let detail
  switch (type.type) {
    case 1: // 枚举类型
    case '1':
      {
        return {
          children: DB.getCodeValues(type.template_id, type.no)
        }
      }
      
    case 2: // 复合类型
    case '2': {
      // 如果是批次 要过滤掉 布局，主要技术参数，主要配备，喷涂方案，state，
      // 如果是状态 过滤掉 使命任务，
      return {
        children: DB.render(type.template_id, type.no, val)
      }
      
    }   
      
    case 3: // 表格类型
    case '3':
    case -1:
    case '-1':
      {
        try {
          const obj = {
          relation: DB.Select('lists', `parent_no = ${type.no}`, '')[0]
        }
        obj.children = DB.render(type.template_id, obj.relation.element_type_no, val)
        if (filterForeign) {
          obj.children = obj.children.filter(item => {
            if (item.isForeignKey) return false
            if (item.name == 'parent_id') return false 
            return true

          })
        }
        if (type.name == '状态') {
          obj.children = obj.children.filter(item => ['使命任务', 'state'].indexOf(item.relation.name) == -1)
          return obj
        } 
        if (type.name == '批次') {
          obj.children = obj.children.filter(item => ['布局', '主要技术参数', '主要配备', '喷涂方案', 'state'].indexOf(item.relation.name)  === -1 )
          return obj
        }
        obj.children = obj.children.filter(item => ['state'].indexOf(item.relation.name) == -1)

        return obj
        } catch(err) {
          db(JSON.stringify(err))
        }
        break;
      }
      
    case -2: // 模板
    case '-2':
      detail = DB.Select('templates', `id = ${type.no}`, '')[0]

      break
   
  }
  return detail
}

/**
 * 新增类型中的某项
 * @param {number} type
 * @param {object} data
 */
export function addItem (type, data) {
  let table
  switch (type) {
    case 1:
    case '1':
      table = 'codes'
      break
    case 2:
    case '2':
      table = 'schemas'
      break
    case 3:
    case '3':
      table = 'lists'
      break

    case -1:
    case '-1':
      table = 'schemas'
      break
  }
  DB.Insert(table, data)
}

/**
 * 更新类型中某一项
 * @param {*} type
 * @param {*} data
 */
export function updateItem (type, data) {
  let table
  switch (type) {
    case 1:
    case '1':
      table = 'codes'
      break

    case 2:
    case '2':
      table = 'schemas'
      break
    case -1:
    case '-1':
    case 3:
    case '3':
      table = 'lists'
      break
    case -2:
    case '-2':
      table = 'templates'
      break
    
    default: break
  }
  DB.Update(table, data)
}

/**
 * 创建新类型或者模板
 */
export function addType (data) {
  DB.Insert('types', data)
}

/**
 * 更新类型
 */
export function updateType (data) {
  DB.Update('types', data)
}

/**
 * 获取所有的类型
 */
export function getTypes (id = null, type) {
  let whereCause = `template_id is null`
  const originList = DB.Select('types', whereCause, '').sort((a, b) => a.type - b.type)
  if (id == null) return originList;

  if (type == 3) {
    whereCause = `template_id = ${id} AND type != 3 `
  } else {
    whereCause = `template_id = ${id}` 
  }
  const list = DB.Select('types', whereCause, '').sort((a, b) => a.type - b.type)
  
  return [...originList, ...list]
}
