<template>
  <div id="technicalChangeList">
      <Modal
              v-model="modal.showState"
              class-name="modal"
              :closable="false"
              :mask-closable="false"
              :styles="{top: '0',width:'420px'}"
      >
          <div slot="header" class="modal-header">
              <div class="title">{{modal.title}}</div>
              <div class="control">

                  <img src="../../assets/sidebar/关闭1@3x.png" @click="cancel()"><img src="../../assets/sidebar/确认1@3x.png" @click="confirm()">
              </div>
          </div>
          <div class="form-info" v-show="modal.viewState == 'insert' || modal.viewState == 'update' || modal.viewState=='view'">
              <Form :model="modal.info"  ref="formValidate"  :label-width="100">
                  <FormItem label="名称" >
                      <Input v-model="modal.info.name" :disabled="true"></Input>
                  </FormItem>
                  <FormItem label="文件">
                      <div class="info-file">
                          <Input v-model="modal.info.path"  :disabled="true"></Input>
                          <!--<div class="info-file-choose" v-show="modal.viewState!='view'">-->
                              <!--<input type="file" id="chooseFile"  @change="fileChange($event)">-->
                          <!--</div>-->
                      </div>
                  </FormItem>
                  <FormItem label="版本" >
                      <Input v-model="modal.info.version" :disabled="true"></Input>
                  </FormItem>
                  <FormItem label="构型节点">
                      <Select v-model="modal.info.configurationId" :disabled="true">
                          <Option v-for="(item,index) in configurationSelectList" :value="item.id" :key="index">{{item.index}} {{ item.title }}</Option>
                      </Select>
                  </FormItem>

                  <div class="old-detail" v-show="modal.viewState=='update'">
                      <p>旧文件</p>
                      <FormItem label="名称" >
                          <Input v-model="modal.oldInfo.name" :disabled="true"></Input>
                      </FormItem>
                      <FormItem label="文件">
                          <div class="info-file">
                              <Input v-model="modal.oldInfo.path"  :disabled="true"></Input>
                              <!--<div class="info-file-choose" v-show="modal.viewState!='view'">-->
                              <!--<input type="file" id="chooseFile"  @change="fileChange($event)">-->
                              <!--</div>-->
                          </div>
                      </FormItem>
                      <FormItem label="版本" >
                          <Input v-model="modal.oldInfo.version" :disabled="true"></Input>
                      </FormItem>
                      <FormItem label="构型节点">
                          <Select v-model="modal.oldInfo.configurationId" :disabled="true">
                              <Option v-for="(item,index) in configurationSelectList" :value="item.id" :key="index">{{item.index}} {{ item.title }}</Option>
                          </Select>
                      </FormItem>
                  </div>
              </Form>
          </div>
          <div slot="footer" class="modal-footer">
              <Button  size="large"  @click="confirm">完成</Button>
          </div>
      </Modal>


      <div class="table-title">
          <div class="back-btn">      <Button @click="back">返回</Button></div>
        技术状态控制
      </div>
      <div class="main-box">
        <div class="table-box">
          <Table highlight-row  border  :columns="columns" :data="content" :max-height="clientHeight * 0.7" @on-current-change="nodeClick">
            <template slot-scope="{ row }" slot="path">
              <div  class="path-box">
                <span class="path">{{row.path}}</span><img src="../../assets/tree/文件选择图标@3x.png" class="detail-btn" @click="view(row)">
              </div>
            </template>
            <template slot-scope="{ row }" slot="type">
                <div class="table-btn" v-if="row.type=='insert'">
                  <img src="../../assets/technical/新增@3x.png">
                  新增
                </div>
                <div class="table-btn" v-if="row.type=='delete'" >
                  <img src="../../assets/technical/删除@3x.png">
                  删除
                </div>
                <div class="table-btn" v-if="row.type=='update'" >
                  <img src="../../assets/technical/修订@3x.png">
                  修订
                </div>
            </template>
          </Table>
        </div>
        <div class="tree-box">
          <Tree  empty-text="" :data="treeList"  class="demo-tree-render"></Tree>
        </div>
      </div>
  </div>
</template>

<script>
    import DB from '@lib/Db';
export default {
    data() {
        return {
            clientHeight:document.documentElement.clientHeight,

            columns:[
                {
                    type: 'index',
                    align: 'center',
                    title:'序号',
                    width:80
                },
                {
                    key:'name',
                    title:'名称',
                    align: 'center',

                }
                ,{
                    slot:'path',
                    title:'文件',
                    align: 'center',

                },{
                    slot:'type',
                    title:'维护类型',
                    align: 'center',

                }
            ],
            content:[],
            treeList:[],
            modal:{
                showState:false,
                viewState:'',//organization  air  model template
                title:"",
                info:{
                    name:"",
                    version:"",
                    path:"",
                    configurationId:"",
                    typeNo:"",
                    id:""
                },
                oldInfo:{
                    name:"",
                    version:"",
                    path:"",
                    configurationId:"",
                    typeNo:"",
                    id:""
                },
            },
            configurationSelectList:[],
        }
    },
    methods:{
        back(){
            this.$router.back()
        },
        confirm(){
            this.modal.showState = false
        },
        cancel(){
            this.modal.showState = false
        },
        view(row){
            this.modal.viewState = row.type
            this.modal.title = "详情"
            switch (this.modal.viewState){
                case "insert":
                    this.modal.info.name = row.tofile["名称"]
                    this.modal.info.configurationId = row.tofile["构型节点id"]
                    this.modal.info.path = row.tofile.path
                    this.modal.info.version = row.tofile["版本"]
                    break;
                case "update":
                    this.modal.oldInfo.name = row.fromfile["名称"]
                    this.modal.oldInfo.configurationId = row.fromfile["构型节点id"]
                    this.modal.oldInfo.path = row.fromfile.path
                    this.modal.oldInfo.version = row.fromfile["版本"]
                    this.modal.info.name = row.tofile["名称"]
                    this.modal.info.configurationId = row.tofile["构型节点id"]
                    this.modal.info.path = row.tofile.path
                    this.modal.info.version = row.tofile["版本"]
                    break;
                case "delete":
                    this.modal.info.name = row.fromfile["名称"]
                    this.modal.info.configurationId = row.fromfile["构型节点id"]
                    this.modal.info.path = row.fromfile.path
                    this.modal.info.version = row.fromfile["版本"]
                    break;
            }

            this.modal.showState = true
        },
        selectConfigurationNode(node,configurationId){
            this.$set(node,"selected",configurationId == node.id)
            if(node.children.length){
                node.children.forEach((item)=>{
                    this.selectConfigurationNode(item,configurationId)
                })
            }
        },
        nodeClick(currentRow ,oldCurrentRow){
            console.log(currentRow,oldCurrentRow)
            this.selectConfigurationNode(this.treeList[0],currentRow["构型节点id"])
        },
        findConfiguration(configurationId,index){
            let children = []
            let configuration = this.configurationList.find((item)=>{
                return item.id == configurationId
            })
            //只是为了弄下拉框
            configuration.index = index
            this.configurationSelectList.push(configuration)
            this.configurationList.forEach((item)=>{
                if(item.parent_id == configurationId){
                    let i = children.length
                    children.push(this.findConfiguration(item.id,index+"-"+i))
                }
            })

            configuration.disabled = true
            configuration.title = configuration['名称']
            configuration.baselineId = configuration['基线id']
            configuration.parentId = configuration.parent_id
            configuration.isCanDelete = true
            configuration.children = children
            if(children.length){
                this.$set(configuration,'expand',true)
            }
            return configuration
        },
    },
    mounted(){
        this.configurationList = DB.Select("构型","","")
        let treeObject = this.findConfiguration(this.$route.query.configurationId,0)
        this.treeList.push(treeObject)

        let changeHistoryDetails = DB.Select("change_history_details","change_history_id = "+this.$route.query.changeHistoryId,"")
        let fileList = DB.Select("技术状态文件",'"基线id" = '+this.$route.query.baselineId,"")
        changeHistoryDetails.forEach((item)=>{
            if(item.from_file_id&&item.to_file_id){
                item.type = "update"
            }else if(item.from_file_id){
                item.type = "delete"
            }else if(item.to_file_id){
                item.type = "insert"
            }
            fileList.forEach((file)=>{

                if(item.to_file_id == file.id||item.from_file_id == file.id){
                    if(item.from_file_id == file.id){
                        item.fromfile = file
                    }
                    if(item.to_file_id == file.id){
                        item.tofile = file
                        item.name = item.tofile["名称"]
                        item.path = item.tofile['路径']
                        item["构型节点id"] = item.tofile["构型节点id"]
                    }
                    if(item.type == 'delete'){
                        item.name = item.fromfile["名称"]
                        item.path = item.fromfile['路径']
                        item["构型节点id"] = item.fromfile["构型节点id"]

                    }
                }

            })

        })
        this.content = changeHistoryDetails
    },
    components: {

    },
}
</script>

<style>
  #technicalChangeList{
    padding-left: 20px;
    padding-right: 20px;
    width: 100%;
    height: 100%;
  }
  #technicalChangeList .back-btn{
      position: absolute;
      left: 20px;
  }
  #technicalChangeList .table-title{
    border:solid 1px #dcdee2;
    margin-top: 20px;
    height: 50px;
    line-height: 50px;
    border-bottom: none;
    text-align: center;
    background: rgb(225,241,249);
    position: relative;
  }
  #technicalChangeList .main-box{
    display: flex;
    height: 100%;
  }
  #technicalChangeList .main-box .table-box{
    width: 80%;
  }
  #technicalChangeList .main-box .tree-box{
    width: 20%;
    height: 100%;
    border-left: solid 1px #e8eaec;
    border-right: solid 1px #e8eaec;
      overflow: scroll;
      padding-left: 19.2px;
      border-bottom: solid 1px #dcdee2;
      text-align: left;
  }
  #technicalChangeList .main-box .tree-box .ivu-tree ul{
      /*font-size: 0.72916666vw;*/
      font-size: 14px;
  }
  #technicalChangeList .main-box .tree-box .ivu-tree-arrow i{
      /*font-size: 0.72916666vw;*/
      font-size: 14px;

  }
  #technicalChangeList .main-box .tree-box .ivu-tree-arrow{
      display: inline-flex;
      align-items: center;
  }
  #technicalChangeList .main-box .tree-box .ivu-icon-ios-arrow-forward:before{
      content: "";
  }
  #technicalChangeList .main-box .tree-box .ivu-tree-arrow-open .ivu-icon.ivu-icon-ios-arrow-forward{
      background:url(../../assets/tree/收起.png);
      -webkit-background-size: 100% 100%;
      background-size: 100% 100%;
  }
  #technicalChangeList .main-box .tree-box .ivu-icon.ivu-icon-ios-arrow-forward{
      background:url(../../assets/tree/展开.png);
      -webkit-background-size: 100% 100%;
      background-size: 100% 100%;
      transform: rotate(0deg);
      /*width: 0.57291666vw;*/
      /*height: 0.57291666vw;*/
      width: 11px;
      height: 11px;
  }
  #technicalChangeList .main-box .tree-box .ivu-icon{
      /*width:0.83333366vw;*/
      /*height:0.83333333vw;*/
      width: 16px;
      height: 16px;
  }
  #technicalChangeList .table-btn{
    display: flex;
    width: 50px;
    margin: auto;
    align-items: center;
    justify-content: space-between;
  }
  #technicalChangeList .table-btn img{
    width: 15px;
  }
  #technicalChangeList .path-box{
    display: flex;
    align-items: center;
    justify-content: center;
  }
  #technicalChangeList .path-box span{
    /*width: 100px;*/
  }
  #technicalChangeList .detail-btn{
      width:15px;
      margin-left: 5px;
  }
</style>