<template>
   <div class="content fill" >
     <component v-if="!canPublished" :is="content.component" :hierarchy="content.hierarchy"  :data="content.data" :type="content.type" :tableName="content.tableName" :query="content.query" :val="content.val" :idRule="content.idRule" />
     <div v-else>
        <Button v-if="!readOnly" type="primary" size="small" ghost @click="publish" class="btn-publish">发布</Button>
        <component :is="content.component" :data="content.data" :hierarchy="content.hierarchy" :type="content.type" :tableName="content.tableName" :query="content.query" :val="content.val" />
     </div>
   </div>
</template>

<script>
import bus from '@event/bus'
import FillTree from '@component/fill/batch/FillTree'
import Task from './Task'
import ComplexType from './ComplexType'
import { getValue, getType, saveComplexData, getEquipmentType, getTaskType, getTableName } from '@api/fill'
import TableType from './TableType'
import StatusTableType from './StatusTable'
import { setFillValue } from '@lib/util'
import EuqTable from './EuqTable'
import Restore from './Restore'
export default {
  name: 'FillHub',
  data() {
    return {
      content: {
        component: null,
        data: {},
        table: '',
        query: '',
        val: {},
        type: {},
        tableName: '',
        readOnly: false
      }
    }
  },
  computed: {
    canPublished: function () {
      if (!this.content.data.type) {
        return false
      }
      return this.content.data.type == 1 || (this.content.data.type == 2 && !this.content.data.subType)
    }
  },
  beforeDestroy() {
    bus.$off('fill-editType')
    bus.$off('fill-deleteType')
  },
  mounted() {
    if (this.$route.path == '/') {
      this.readOnly = true
    } else {
      this.readOnly = false
    }
    bus.$on('fill-deleteType', () => {
        // 删除节点
        this.content = {
        component: null,
        data: {},
        table: '',
        val: {},
        type: {}
      }
      })
      bus.$on('fill-editType', data => {
        console.log(data)
        switch (data.type) {
          case 0:
            {
              const val = getValue('机型', data.id)
              setFillValue(val)
              this.content = {
                component: ComplexType,
                data: data,
                tableName: '机型',
                hierarchy: [],
                val,
                type: {...getType('机型'), template_id: data.relation.templateId}
              }
            }
            
            // 机型
            break;
          case 1:
            // 状态
            {
              const val = getValue('状态', data.id)
              setFillValue(val)
              this.content = {
                component: ComplexType,
                data: data,
                hierarchy: [],
                tableName: '状态',
                val,
                type: {...getType('状态'), template_id: data.relation.templateId}
              }
            break;
            }
            
          case 2: 
            // 批次
            if (data.subType) {
              this.processBatch(data)
            } else {
              const val = getValue('批次', data.id)
              setFillValue(val)
              this.content = {
                component: ComplexType,
                tableName: '批次',
                data: data,
                hierarchy: [],
                val,
                type: { ...getType('批次'), template_id: data.relation.templateId}
              }
            }
            
            break;
          case 3:
            // 单位
            
            break;
          case 4:
            // 单机
            if (data.subType) {
              this.processBatch(data)
            } else {
              const val = getValue('飞机', data.id)
              setFillValue(val)
              this.content = {
                component: ComplexType,
                tableName: '飞机',
                data: data,
                hierarchy: [],
                val,
                type: { ...getType('飞机'), template_id: data.relation.templateId}
              }
            }
            break;
          case -3:
            // 回收站 ，只有批次和单机可以进回收站，
            this.content = {
              component: Restore,
              data: data,
            }
            break;
        }
       
    })
  },
  methods: {
    publish() {
      let obj = {
        id: this.content.data.id,
        state: "2"
      }
      saveComplexData(this.content.tableName, obj)
      this.$Message.success('发布成功');
      bus.$emit('fill-restore')
    },
    processTables(data) {
      const tableName = getTableName(data.relation.tableNo)
      const type = getEquipmentType(tableName, data.relation.templateId)
        this.content = {
          component: EuqTable,
          data,
          type: {...type, template_id: data.relation.templateId},
          tableName: tableName,
          query: `序号Like${data.relation.idRule}%`,
          idRule: data.relation.idRule
        }
    },
    processBatch(data) {
      
      switch (data.subType) {
        case '使命任务':
            {
              let type = getType('状态');
              const val = getValue('状态', data.id)
              setFillValue(val)
              type.template_id = data.relation.templateId;
              this.content = {
                tableName: '状态',
                component: Task,
                hierarchy: [],
                data: data,
                type,
                val
              }
              break;
            }
        case '布局':
          {
              let type = getType('批次');
              const val = getValue('批次', data.id)
              const child = getTaskType({...type, template_id: data.relation.templateId}, data.id, data.subType)
              setFillValue(val)
              /* type.template_id = data.relation.templateId; */
              this.content = {
                tableName: '批次',
                component: ComplexType,
                hierarchy: ['布局'],
                data,
                type: child.type,
                val: val['布局']
              }
              break;
            }
          
        case '主要技术参数':
          {
              let type = getType('批次');
              const val = getValue('批次', data.id)
              const child = getTaskType({...type, template_id: data.relation.templateId}, data.id, data.subType)
              setFillValue(val)
              
              this.content = {
                tableName: '批次',
                component: ComplexType,
                hierarchy: ['主要技术参数'],
                data,
                type: child.type,
                val: val['主要技术参数']
              }
              break;
            }
          
        case '主要配备':

          {
              let type = getType('批次');
              const val = getValue('批次', data.id)
              const child = getTaskType({...type, template_id: data.relation.templateId}, data.id, data.subType)
              setFillValue(val)
              this.content = {
                tableName: '批次',
                component: ComplexType,
                hierarchy: ['主要配备'],
                data,
                type: child.type,
                val: val['主要配备']
              }
              break;
            }
        case '构型':
          this.content = {
            component: FillTree,
            data: data
          }
          break;
        case '保障资源':
          this.content = {
            component: null,
            data,
            type: null
          }
          break;
        /* case '一线保障装设备':
          {
            const type = getEquipmentType('随机设备工具', data.relation.templateId)
            this.content = {
              component: EuqTable,
              data,
              type,
              tableName: '随机设备工具',
              query: '序号Like6.1.1%',
              idRule: '6.1.1'
            }
            break;
          }
        case '随机工具':
          {
            const type = getEquipmentType('随机设备工具', data.relation.templateId)
            this.content = {
              component: EuqTable,
              data,
              type,
              tableName: '随机设备工具',
              query: '序号Like6.2.1%',
              idRule: '6.2.1'
            }
            break;
          }
        case '二线保障设备':
          {
            const type = getEquipmentType('随机设备工具', data.relation.templateId)
            this.content = {
              component: EuqTable,
              data,
              type,
              tableName: '随机设备工具',
              query: '序号Like6.1.2%',
              idRule: '6.1.2'
            }
            break;
          }
        case '大比例随机配件':
          {
            const type = getEquipmentType('随机设备工具', data.relation.templateId)
            this.content = {
              component: EuqTable,
              data,
              type,
              tableName: '随机设备工具',
              query: '序号Like6.3.1%',
              idRule: '6.3.1'
            }
            break;
          }
        case '挂架和导发装置':
          {
            const type = getEquipmentType('随机设备工具', data.relation.templateId)
            this.content = {
              component: EuqTable,
              data,
              type,
              tableName: '随机设备工具',
              query: '序号Like6.3.2%',
              idRule: '6.3.2'
            }
            break;
          }
        case '机械专业随机备件':
          {
            const type = getEquipmentType('机械专业随机备件', data.relation.templateId)
            this.content = {
              component: EuqTable,
              data,
              type,
              tableName: '机械专业随机备件',
              query: '序号Like6.3.3%',
              idRule: '6.3.3'
            }
            break;
          }
        case '随机资料目录':
          {
            const type = getEquipmentType('随机资料目录', data.relation.templateId)
            this.content = {
              component: EuqTable,
              data,
              type,
              tableName: '随机资料目录',
              query: '序号Like6.4.1%',
              idRule: '6.4.1'
            }
            break;
          }
        case '随机资料配套目录':
          {
            const type = getEquipmentType('随机资料配套目录', data.relation.templateId)
            this.content = {
              component: EuqTable,
              data,
              type,
              tableName: '随机资料配套目录',
              query: '序号Like6.4.2%',
              idRule: '6.4.2'
            }
            break;
          } */
        case '喷涂方案':
          {
            let type = getType('批次');
            const val = getValue('批次', data.id)
            setFillValue(val)
            type.template_id = data.relation.templateId;
            
            this.content = {
              tableName: '批次',
              component: Task,
              hierarchy: [],
              data,
              type,
              val
            }
            break;
          }
        case '技术状态文件清单':
          {
            let type = {...getType('技术状态文件'), template_id: data.relation.templateId}
            this.content = {
              type,
              data,
              component: StatusTableType,
              tableName: '技术状态文件',
              query: ''
            }
            break;
          }
          
        case '有寿件目录':
          {
            let type = {...getType('有寿件'), template_id: data.relation.templateId}
            this.content = {
              type,
              data,
              component: TableType,
              tableName: '有寿件',
              query: ''
            }
            break;
          }
          
        case '双流水':
          {
            let type = {...getType('双流水'), template_id: data.relation.templateId}
            this.content = {
              type,
              data,
              component: TableType,
              tableName: '双流水',
              query: ''
            }
            break;
          }
        case '交付遗留问题及处置情况':
          {
            let type = {...getType('交付遗留问题及处置情况'), template_id: data.relation.templateId}
            this.content = {
              type,
              data,
              component: TableType,
              tableName: '交付遗留问题及处置情况',
              query: ''
            }
            break;
          }

        default: {
          this.processTables(data)
        }
         

      }
    }
  }
}
</script>

