<template>
  <div>
    <Row type="flex" align="middle" class="t-tr" justify="center">
      <Col class="t-td t-value" :class="{ largeInput: type.no == 2}">
        <template v-if="readOnly">
          <Row type="flex" align="middle">
            <Col v-if="isEnumType" class="col-auto t-td-read">
              {{ data | enumFormat}}
            </Col>
            <Col v-if="type.no == 2 || type.no == 1 || type.no == 3 || type.no == 4 || type.no == 5" class="col-auto t-td-read">
              {{ data.value}}
            </Col>
            <Col v-if="type.no == 6" class="col-auto t-td-read">
              {{ data | dateFormat }}
            </Col>
            <Col v-if="type.no == 7" class="col-auto t-td-read">
              {{ data | timeFormat }}
            </Col>
            <Col v-if="type.no == 9" v-viewer class="col-auto t-td-read">
              <img class="img-thumbnail" :src="file.content" alt="">
            </Col>
            <Col v-if="type.no == 8" class="col-auto t-td-read">
              <a class="" @click="openFile">
                <Icon type="ios-folder-open" />
              </a>
            </Col>
          </Row>
        </template>
        <template v-else>
          <Row v-if="!isComplexType" type="flex" align="middle">
            <Select v-if="isEnumType" v-model="data.value" size="small">
              <Option v-for="(item, index) in data.children" :label="item.value" :value="item.no" :key="index"></Option>
            </Select>
            <Col class="col-auto" v-else-if="data.isForeignKey">{{data.children[0][data.desc.show]}}</Col>
            <Col class="col-auto" v-else>
              <Input v-if="type.no == 2" :rows="1" type="textarea" v-model="data.value"  placeholder="请输入..."/>
              <Input v-if="(type.no == 1 || type.no == 3 || type.no == 4 || type.no == 5)" v-model="data.value" placeholder="请输入..."/>
              <DatePicker v-if="type.no == 6" v-model="data.value" :format="data.relation.restriction.format" />
              <vue-timepicker v-if="type.no == 7" v-model="data.value" :format="data.relation.restriction.format" />
              <Row v-if="type.no == 8" type="flex" justify="center">
                <Col class="t-file" v-if="data.value">
                  <Icon type="ios-folder-open" />
                </Col>
                <Col>
                  <Upload :before-upload="handleUploadFile" >
                    <Button icon="ios-cloud-upload-outline">上传文件</Button>
                  </Upload>
                </Col>
                
                
              </Row>
              <Row v-if="type.no == 9" type="flex" align="middle" justify="center">
                <Col v-viewer v-if="data.value">
                  <img :src="data.value" alt="">
                </Col>
                <Col>
                  <Upload :before-upload="handleUploadImage" accept="image/png, image/jpeg">
                    <Button icon="ios-cloud-upload-outline">上传图片</Button>
                  </Upload>
                </Col>
                
              </Row>
            </Col>
            <Col>
              {{data.relation.restriction.unit.value}}
            </Col>

          </Row>
        </template>
          
        </Col>
    </Row>
  </div>
</template>

<script>

import ComplexType from './ComplexType'
import TableType from './commonTable'
import { getKeyValue, getFileValue } from '@lib/util'
export default {
  props: {
    data: {
      type: Object,
      required: true
    },
    val: {
      type: Object,
    },
    type: {
      type: Object,
      required: true
    },
    index: {
      type: Number,
      required: true
    },
    tableName: {
      type: String
    },
    // 表示层级关系
    hierarchy: {
      type: Array
    }
  },
  data(){
    const hierarchyChain = [...this.hierarchy,this.data.relation.name]
    return {
      showChildren: false,
      readOnly: false,
      componentId: '',
      hierarchyChain,
      fileId: null,
      value: getKeyValue(hierarchyChain)
    }
  },
  watch: {
    'type': function(type) {
      this.updateComponet(type)
      this.reset()
      if (type.no == 8 || type.no == 9) {
        this.getFile()
      }
    }
  },
  mounted() {
    if (this.$route.path == '/') {
      this.readOnly = true
    } else {
      this.readOnly = false
    }
    if (this.type.no == 8 || this.type.no == 9) {
      this.getFile()
    }
    this.updateComponet(this.type)
  },
  computed: {
    isComplexType: function () {
      if (this.data.isForeignKey) {
        return false
      } else {
        return this.type.type == 2 || this.type.type == 3
      }
    },
    isEnumType: function () {
      return this.type.type == 1
    }
  },
  methods: {
    getFile() {
      if (this.data.value) {
        this.fileId = this.data.value;
        this.file.content = getFileValue(this.tableName, this.data.value, this.data.relation.name)
      }
    },
    openFile() {
      if (db_openfile) {
        db_openfile(this.fileId)
      }
    },
    reset() {
      this.showChildren = false;
      this.file.content = null
      this.value = getKeyValue([...this.hierarchy,this.data.relation.name])
      this.hierarchyChain = [...this.hierarchy,this.data.relation.name]
    },
    updateComponet(val) {
      switch (val.type) {
          case '2':
          case 2:
            this.componentId = ComplexType;
            break;
          case '3':
          case 3:
            this.componentId = TableType;
            break;
          default:
            this.componentId = null;
            
        }
    },
    handleUploadImage(file) {
      const reader = new FileReader()
      const vm = this;
      reader.onload =  ()=> {
        vm.data.value = reader.result
        vm.data.fileType = file.type
      }
      reader.readAsDataURL(file)
    },
    handleUploadFile(file) {
      const reader = new FileReader()
      const vm = this;
      reader.onload = ()=> {
        vm.data.value = new Blob([reader.result])
        vm.data.fileType = file.type
      }
      reader.readAsArrayBuffer(file)

    },
    toggleChildren() {
      this.showChildren = !this.showChildren;
    }
  }
}
</script>

