<template>
  <div id="design-main">
      <div class="type-detail" v-show="viewState=='type'">
          <div class="tree-box" >
            <Tree  ref="tree" empty-text="" :data="treeList" @on-select-change="nodeClick" :render="renderContent" class="demo-tree-render"></Tree>
          </div>
          <div class="detail-box"  v-show="detailItem">
            <Form :model="detailItem" v-if="detailItem" ref="formValidate" :label-width="130">
              <FormItem label="名称" >
                <Input :style="'width: '+info.width+'px !important;'"  v-model="detailItem.title"  @on-blur="inputBlurName"></Input>
              </FormItem>
              <FormItem label="类型"  v-if="detailItem.type.type!=4" >
                <Select :style="'width: '+info.width+'px'"  v-model="detailItem.type.no" @on-change="selectChangeType"  v-if="detailItem.typeList&&detailItem.typeList.length">
                  <Option v-for="type,index in detailItem.typeList" :value="type.no" :key="index">
                    <i class="ivu-icon" :class="'ivu-icon-'+type.iconType" style="margin-right: 8px;"></i>
                    {{type.name}}</Option>
                </Select>
                <Button style="margin-left: 10px" @click="openTypeInfo('insert','type')" >新增</Button>
                <Button style="margin-left: 10px" @click="openTypeInfo('update','type')" >修改</Button>
              </FormItem>
              <FormItem label="元素类型"  v-show="detailItem.type.type == 3" v-if="detailItem.lists">
                <Select :style="'width: '+info.width+'px'"  v-model="detailItem.lists.element_type.no" @on-change="selectChangeElementType" :disabled="detailItem.type.template_id == null"  v-if="detailItem.lists.element_type&&detailItem.elementTypeList&&detailItem.elementTypeList.length">
                  <Option v-for="type,index in detailItem.elementTypeList" :value="type.no" :key="index">
                    <i class="ivu-icon" :class="'ivu-icon-'+type.iconType" style="margin-right: 8px;"></i>
                    {{type.name}}</Option>
                </Select>
                <Button style="margin-left: 10px" @click="openTypeInfo('insert','elementType')" >新增</Button>
                <Button style="margin-left: 10px" @click="openTypeInfo('update','elementType')" >修改</Button>
              </FormItem>
              <FormItem label="树状展示" v-show="detailItem.type.type == 3&&detailItem.children.length>1&&detailItem.isRoot" v-if="detailItem.lists">
                <Checkbox v-model="info.lists.isTree" @on-change="checkChangeTree">&nbsp;</Checkbox>
              </FormItem>
              <FormItem label="节点显示字段"  v-if="detailItem.lists&&detailItem.lists.element_type" v-show="detailItem.lists.tree_show_column_no!=null&&detailItem.children.length>1&&detailItem.isRoot">
                <Select :style="'width: '+info.width+'px'" v-model="detailItem.lists.tree_show_column_no" @on-change="selectChangeListsTreeColumn">
                  <Option v-for="child,index in detailItem.children" v-if="child.iconType!='append'" :value="child.schema.no" :key="index">{{child.title}}</Option>
                </Select>
              </FormItem>
              <FormItem label="关联树状数据项"  v-if="detailItem.lists&&detailItem.lists.element_type" v-show="detailItem.lists.tree_show_column_no==null&&detailItem.children.length>1&&detailItem.isRoot">
                <Select :style="'width: '+info.width+'px'" v-model="infoConfigDetail.reference_to" @on-change="selectChangeInfoConfigTreeRef">
                  <Option v-for="infoConfig,index in infoConfigTreeList"  :value="infoConfig.id" :key="index">{{infoConfig.name}}</Option>
                </Select>
              </FormItem>
              <FormItem label="长度" v-show="detailItem.type.no == 1" v-if="detailItem.restriction">
                <div class="number-input" >
                  <Input type="number" v-model="detailItem.restriction.max" @on-blur="inputBlurSchemaRestriction"></Input>
                </div>
              </FormItem>
              <FormItem label="范围" v-show="detailItem.type.no>=3&&detailItem.type.no<=5" v-if="detailItem.restriction">
                <div class="number-inputs">
                  <div class="number-input" >
                    <Input type="number" v-model="detailItem.restriction.min" @on-blur="inputBlurSchemaRestriction" />
                  </div>
                  到
                  <div class="number-input" >
                    <Input type="number" v-model="detailItem.restriction.max" @on-blur="inputBlurSchemaRestriction"/>
                  </div>
                </div>
              </FormItem>
              <FormItem label="精度"  v-if="detailItem.restriction" v-show="detailItem.type.no==3">
                <div class="number-input" >
                  <Input type="number" v-model="detailItem.restriction.precision" @on-blur="inputBlurSchemaRestriction"></Input>
                </div>
              </FormItem>
              <FormItem label="格式"  v-if="detailItem.restriction"  v-show="detailItem.type.no==7||detailItem.type.no==6">
                <RadioGroup v-model="detailItem.restriction.format" @on-change="inputBlurSchemaRestriction">
                  <Radio v-show="detailItem.type.no==6" :label="item" v-for="item in dateList">
                    {{item}}
                  </Radio>
                  <Radio v-show="detailItem.type.no==7" :label="item" v-for="item in timeList">
                    {{item}}
                  </Radio>
                </RadioGroup>
              </FormItem>
              <FormItem label="单位类型" v-show="detailItem.type.no>=3&&detailItem.type.no<=5">
                <Select :style="'width: '+info.width+'px'" v-model="info.unitTypeId" @on-change="selectChangeUnitType">
                  <Option v-for="unitType,index in unitTypeList" :value="unitType.id" :key="index">{{unitType.name}}</Option>
                </Select>
              </FormItem>
              <FormItem label="单位量词"  v-show="detailItem.type.no>=3&&detailItem.type.no<=5&&info.unitTypeId">
                <Select :style="'width: '+info.width+'px'" v-model="info.unitId" @on-change="selectChangeUnit">
                  <Option v-for="unit,index in unitList" v-show="!info.unitTypeId||info.unitTypeId==unit.type_id" :value="unit.id" :key="index">{{unit.name}}</Option>
                </Select>
              </FormItem>
              <FormItem label="必填"  v-show="detailItem.type.type!=3&&detailItem.type.type!=4">
                <Checkbox v-model="info.is_required" @on-change="checkChangeRequired"></Checkbox>
              </FormItem>
              <FormItem label="备注" >
                <Input :rows="5" :style="'width: '+info.width+'px !important;'"  v-model="info.description" type="textarea" @on-blur="inputBlurDescription"></Input>
              </FormItem>
            </Form>
          </div>
        </div>

      <div class="template-detail" v-show="viewState=='template'" v-if="templateDetail">
        <div class="t-table">
          <div class="header">
            模板详情
            <Button @click="saveTemplate"  style="left: 20px;position: absolute;top: 0;bottom: 0; margin: auto;">保存</Button>
          </div>
          <Row type="flex" align="middle" class-name="content-name">
            <Col>
            模板名称：
            </Col>
            <Col>
            <Input v-model="templateDetail.name" placeholder="..."/>
            </Col>

          </Row>
          <Row type="flex" align="middle" class-name="content-name">
            <Col>
            模板标注：
            </Col>
            <Col>
            <Input v-model="templateDetail.description" placeholder="..."/>
            </Col>

          </Row>
        </div>
      </div>
      <div class="infoConfig-detail" v-show="viewState == 'infoConfig'" v-if="infoConfigDetail">
        <div class="t-table">
          <div class="header">
            目录信息
            <Button @click="saveInfoConfig"  style="left: 20px;position: absolute;top: 0;bottom: 0; margin: auto;">保存</Button>
          </div>
          <Row type="flex" align="middle" class-name="content-name">
            <Col>
            目录名称：
            </Col>
            <Col>
            <Input v-model="infoConfigDetail.name" placeholder="..."/>
            </Col>

          </Row>
        </div>
      </div>
      <div class="trash-main" v-show="viewState=='trash'">
        <div class="table-box">
          <div class="table-title">
            模板回收站
          </div>
          <Table  border  :columns="columns" :data="templateList" max-height="750" :disabled-hover="true">
            <template slot-scope="{ row }" slot="controls">
              <Button @click="restore(row)">撤销删除</Button>
            </template>
          </Table>
        </div>
      </div>
      <Modal
              v-model="modal.showState"
              class-name="modal"
              :closable="false"
              :mask-closable="false"
              :styles="{top: '0',width:'420px'}"
      >
        <div slot="header" class="modal-header">
          <div class="title">{{modal.title}}</div>
          <div class="control">
            <img src="../../assets/sidebar/关闭1@3x.png" @click="cancel()">
          </div>
        </div>
        <div class="type-box" v-show="modal.viewState!='baseData'">
          <Form :model="modal.info"  :label-width="100" >
            <FormItem label="名称" prop="name">
              <Input v-model="modal.info.name" ></Input>
            </FormItem>
            <FormItem label="类型" prop="type">
              <Select v-model="modal.info.type" :disabled="modal.isEditDisabled">
                <Option v-for="type,index in modal.typeList" :value="type.type" :key="index">
                  <i class="ivu-icon" :class="'ivu-icon-'+type.iconType" style="margin-right: 8px;"></i>
                  {{type.name}}</Option>
              </Select>
            </FormItem>
            <FormItem label="备注" prop="description">
              <Input :rows="5"  v-model="modal.info.description" type="textarea" ></Input>
            </FormItem>
          </Form>
        </div>
        <div class="type-box" v-show="modal.viewState=='baseData'">
          <Form :model="modal.baseData.info"  :label-width="100" >
            <FormItem label="名称" prop="name">
              <Input v-model="modal.baseData.info.name" ></Input>
            </FormItem>
          </Form>
        </div>
        <div slot="footer" class="modal-footer" >
          <span v-show="modal.viewState == 'insert'" style="margin-right: 50px">执行操作将切换到改类型</span>
          <Button  size="large"  @click="confirm">确定</Button>
        </div>
      </Modal>
  </div>
</template>

<script>
import DB from "@lib/Db";
import Bus from '@event/bus';
import Split from "../../../node_modules/view-design/src/components/split/split.vue";
import {dp} from '@lib/util'
export default {
    components: {
        Split
    },

    props: {

    },

    data(){
        return{
            baseDataList:[],
            columns:[
                {
                    type: 'index',
                    align: 'center',
                    title:'序号',
                    width:80
                },
                {
                    key:'name',
                    title:'名称',
                    align: 'center',

                }
                ,{
                    key:'description',
                    title:'备注',
                    align: 'center',

                },{
                    slot:'controls',
                    title:'操作',
                    width:150,
                    align: 'center',
                },
            ],
            templateList:[],
            templateDetail:null,
            viewState:'',//'template','type',
            modal:{
                showState:false,
                viewState:'',
                title:"",
                info:{
                    name:"",
                    type:0,
                    description:""
                },
                baseData:{
                  info:{
                    name:""
                  }
                },
                typeList:[]
            },
            split1:0.5,
            currentItem:{},
            detailItem:null,
            info:{
                lineType:"0",
                unitTypeId:null,
                unitId:null,
                is_required:false,
                lists:{
                    min:0,
                    max:-1,
                    isTree:false,
                },
                description:"",
                width:200
            },
            dateList:['yyyy-MM-dd','yyyy/MM/dd'],
            timeList:['HH:mm:ss', 'HH:mm', 'hh:mm:ss A', 'hh:mm A'],
            treeList:[],
            typeList:[],
            unitList:[],
            unitTypeList:[],
            infoConfigDetail:null
        }
    },

    watch: {},

    created () {},
    beforeDestroy(){
        Bus.$off('design-choose')
        Bus.$off('edit-move')
    },
    mounted () {

        Bus.$on("design-choose",(data,type)=>{
            this.viewState = type
            if(data){
                switch (this.viewState){
                    case 'infoConfig':
                        this.infoConfigDetail = DB.selectOne('info_configs','id = '+data.id)
                        break;
                    case 'type':
                        this.infoConfigDetail = DB.selectOne('info_configs','id = '+data.id)
                        let info_configs = DB.Select('info_configs','type = 1 AND template_id = '+this.infoConfigDetail.template_id,'')
                        let currentTree = null
                        this.infoConfigTreeList = info_configs.filter((infoConfig)=>{
                            let flag = false
                            let infoConfigType = DB.getTypeByTemplateIdAndNo(this.infoConfigDetail.template_id,infoConfig.type_no)
                            if(infoConfigType.type == 3&&infoConfig.id!=this.infoConfigDetail.id){
                                let listsItem = DB.getTableSchema(this.infoConfigDetail.template_id,infoConfigType.no)
                                if(listsItem.tree_show_column_no!=null){
                                    flag = true
                                }
                            }
                            if(!flag&&infoConfig.id == this.infoConfigDetail.reference_to){
                                DB.Update('info_configs',{
                                    id:this.infoConfigDetail.id,
                                    reference_to:null,
                                })
                            }
                            return flag
                        })
                        this.currentItem = this.updateBaseData()
                        this.createTree()
                        break;
                    case 'template':
                        this.templateDetail = DB.selectOne('templates','id = '+data.id,'')
                        break;
                    case 'trash':
                        this.templateList = DB.Select('templates','state=1','')
                        break;
                }

            }else{
                this.viewState = 'empty'
            }
        })
        Bus.$on('edit-move',(type)=>{
            this.move(this.detailItem.root,this.detailItem.node,this.detailItem,type)
        })
        this.typeList = DB.Select("types","","")
        this.typeList.forEach((item)=>{
            item.iconType = this.getIconType(item)
        })
        this.unitList = DB.Select("units","","")
        this.unitTypeList = DB.Select("unit_types","","")


    },

    methods: {
        updateBaseData(){
            this.infoConfigDetail = DB.selectOne('info_configs','id = '+this.infoConfigDetail.id)
            let currentItem = {
                templateId:this.infoConfigDetail.template_id,
                typeNo:this.infoConfigDetail.type_no
            }
            currentItem.name = this.infoConfigDetail.name
            currentItem['type'] = DB.getTypeByTemplateIdAndNo(this.infoConfigDetail.template_id,currentItem.typeNo)
            return currentItem
        },
        selectChangeBaseData(){
            DB.Update('info_configs',{
                id:this.infoConfigDetail.id,
                type_no:this.infoConfigDetail.type_no
            })
            this.currentItem = this.updateBaseData()
            this.createTree()
        },
        saveInfoConfig(){
            Bus.$emit('design-update',{
                name:this.infoConfigDetail.name,
                dataType:'infoConfig',
            })
            DB.Update('info_configs',{
                id:this.infoConfigDetail.id,
                name:this.infoConfigDetail.name
            })
            this.$Message.success('保存成功！')
        },
        restore(template){
            this.templateList.splice(template._index,1)
            DB.Update('templates',{
                id:template.id,
                state:0
            })
            Bus.$emit('design-restore')
        },
        saveTemplate(){
            Bus.$emit('design-update',{
                name:this.templateDetail.name,
                dataType:'template',
            })
            DB.Update('templates',{
                id:this.templateDetail.id,
                name:this.templateDetail.name,
                description:this.templateDetail.description
            })
            this.$Message.success('保存成功！')
        },
        sendLeftTree(){
            switch (this.detailItem.dataType){
                case 'supportConfig':
                    Bus.$emit('design-update',{
                        name:this.detailItem.name,
                        dataType:this.detailItem.dataType,
                        typeNo:this.detailItem.type.no
                    })
                    break;
                case 'batchExtend':
                    Bus.$emit('design-update',{
                        name:this.detailItem.name,
                        dataType:this.detailItem.dataType,
                        typeNo:this.detailItem.type.no
                    })
                    break;
            }
        },
        openTypeInfo(viewState,type){
            this.modal.type = type
            this.modal.viewState = viewState
            this.modal.typeList = [{type:2,name:"复合类型"},{type:1,name:"枚举类型"}]
            if(type == 'type'){
                this.modal.typeList.push({type:3,name:"表格类型"})
            }
            switch (this.modal.viewState){
                case 'insert':
                    this.modal.title = '新增类型'
                    this.modal.info.name = ""
                    this.modal.info.type = 2
                    this.modal.info.description = ""
                    this.modal.isEditDisabled = false
                    break;
                case 'update':
                    this.modal.isEditDisabled = this.modal.info.isEditDisabled
                    this.modal.info.type = this.detailItem.type.type
                    if(this.modal.info.type == 0){
                        this.modal.isEditDisabled = true
                        this.modal.typeList.unshift({type:0,name:"基础类型"})
                    }
                    this.modal.title = '编辑类型'
                    this.modal.info.description = this.detailItem.type.description
                    this.modal.info.name = this.detailItem.type.name
                    break;
            }
            this.modal.typeList.forEach((item)=>{
                item.iconType = this.getIconType(item)
            })
            this.modal.showState = true
        },
        cancel(){
            this.modal.showState = false
        },
        confirm(){
            switch (this.modal.viewState){
                case 'baseData':

                    break;
                default:
                    if(this.modal.info.name==""){
                        this.$Message.error("名称不能为空")
                        return
                    }
                    if(this.modal.type == 'type'){//类型
                        if(this.modal.viewState == 'insert'){
                            this.detailItem.type = {
                                "template_id": this.currentItem.templateId,
                                "name": this.modal.info.name,
                                "type": this.modal.info.type,
                                "description": this.modal.info.description
                            }
                            this.detailItem.type.id = DB.Insert('types',this.detailItem.type)
                            this.detailItem.type.no = this.detailItem.type.id + 65535
                            DB.Update('types',this.detailItem.type)
                            if(this.detailItem.type.type == 3){
                                this.detailItem.lists = {
                                    "template_id": this.currentItem.templateId,
                                    "parent_no": this.detailItem.type.no,
                                    "element_type_no": 1,
                                    tree_show_column_no:null,
                                    "min": 0,
                                    "max": -1,
                                    "group_by": null,
                                    "is_hidden": 1,
                                    "description": null
                                }
                                this.detailItem.lists.id = DB.Insert('lists',this.detailItem.lists)
                            }
                        }else{
                            this.detailItem.type.name = this.modal.info.name
                            this.detailItem.type.description = this.modal.info.description
                            if(this.detailItem.type.type!=this.modal.info.type){
                                this.detailItem.type.type = this.modal.info.type
                                if(this.detailItem.type.type == 3){
                                    let obj = DB.selectOne('lists','template_id='+ this.currentItem.templateId+' AND parent_no='+this.detailItem.type.no)
                                    if(!obj){
                                        this.detailItem.lists = {
                                            "template_id": this.currentItem.templateId,
                                            "parent_no": this.detailItem.type.no,
                                            "element_type_no": 1,
                                            tree_show_column_no:null,
                                            "min": 0,
                                            "max": -1,
                                            "group_by": null,
                                            "is_hidden": 1,
                                            "description": null
                                        }
                                        this.detailItem.lists.id = DB.Insert('lists',this.detailItem.lists)
                                    }
                                }
                            }
                            DB.Update('types',this.detailItem.type)
                        }
                        this.updateDetailTypeList()
                        this.selectChangeType(this.detailItem.type.no)
                    }else{//元素类型
                        if(this.modal.viewState == 'insert'){
                            this.detailItem.lists.element_type = {
                                "template_id": this.currentItem.templateId,
                                "name": this.modal.info.name,
                                "type": this.modal.info.type,
                                "description": this.modal.info.description
                            }
                            this.detailItem.lists.element_type.id = DB.Insert('types',this.detailItem.lists.element_type)
                            this.detailItem.lists.element_type.no = this.detailItem.lists.element_type.id + 65535
                            DB.Update('types',this.detailItem.lists.element_type)
                        }else{
                            this.detailItem.lists.element_type.name = this.modal.info.name
                            this.detailItem.lists.element_type.description = this.modal.info.description
                            this.detailItem.lists.element_type.type = this.modal.info.type
                            DB.Update('types',this.detailItem.lists.element_type)
                        }
                        this.updateDetailTypeList()
                        this.selectChangeElementType(this.detailItem.lists.element_type.no)
                    }

                    break;
            }
            this.cancel()
        },
        updateDetailTypeList(){
            let typeList = DB.Select('types','','')
            let elementTypeList = DB.Select('types','type != 3','')
            this.detailItem.elementTypeList = []
            this.detailItem.elementTypeList = elementTypeList.filter((type)=>{
                let flag = false
                if(type.type==0 || ( type.template_id == this.currentItem.templateId && type.no >= 65535)){
                    flag = true
                }
                return flag
            })
            this.detailItem.typeList = typeList.filter((type)=>{
                let flag = false
                if(type.type==0 || ( type.template_id == this.currentItem.templateId  && type.no >= 65535)){
                    flag = true
                }
                return flag
            })
            this.detailItem.typeList.forEach((item)=>{
                item.iconType = this.getIconType(item)
            })
            this.detailItem.elementTypeList.forEach((item)=>{
                item.iconType = this.getIconType(item)
            })

        },
        //节点 名字
        inputBlurName(){
//            this.detailItem.title = this.detailItem.title.replaceAll('-','至')
            if(this.detailItem.isRoot) {
                DB.Update('info_configs', {
                    id: this.infoConfigDetail.id,
                    name: this.detailItem.title
                })
                Bus.$emit('design-update', {
                    name: this.detailItem.title,
                    dataType: 'infoConfig',
                })
                this.selectChangeBaseData()
            }else{
                switch (this.detailItem.type.type){
                    case 4:
                        this.detailItem.code.value = this.detailItem.title
                        DB.Update('codes',{
                            id:this.detailItem.code.id,
                            value:this.detailItem.code.value
                        })
                        break;
                    default:
                        if(this.detailItem.dataType == 'supportConfig'||this.detailItem.dataType == 'batchExtend'){
                            this.detailItem.name = this.detailItem.title
                            this.sendLeftTree()
                        }else{
                            this.detailItem.schema.name = this.detailItem.title
                            DB.Update('schemas',{
                                id:this.detailItem.schema.id,
                                name:this.detailItem.schema.name
                            })
                        }
                        break;
                }
            }
        },
        //关联一个树表格
        selectChangeInfoConfigTreeRef(){
            DB.Update('info_configs',{
                id:this.infoConfigDetail.id,
                reference_to:this.infoConfigDetail.reference_to
            })
        },
        //是否按找树形结构展示
        checkChangeTree(){
            if(this.info.lists.isTree){
                this.detailItem.lists.tree_show_column_no = this.detailItem.children[0].schema.no
            }else{
                this.detailItem.lists.tree_show_column_no = null
            }
            this.selectChangeListsTreeColumn()
        },
        //lists tree_show_column_no
        selectChangeListsTreeColumn(){
            DB.Update('lists',{
                id:this.detailItem.lists.id,
                tree_show_column_no:this.detailItem.lists.tree_show_column_no
            })
        },
        //schema  restriction
        inputBlurSchemaRestriction(){
            DB.Update('restrictions',this.detailItem.restriction)
        },
        //备注
        inputBlurDescription(){
            switch (this.detailItem.type.type){
                case 3:
                    this.detailItem.lists.description = this.info.description
                    DB.Update('lists',{
                        id:this.detailItem.lists.id,
                        description:this.detailItem.lists.description
                    })
                    break;
                case 4:
                    this.detailItem.code.description = this.info.description
                    DB.Update('codes',{
                        id:this.detailItem.code.id,
                        description:this.detailItem.code.description
                    })
                    break;
                default:
                    this.detailItem.schema.description = this.info.description
                    DB.Update('schemas',{
                        id:this.detailItem.schema.id,
                        description:this.detailItem.schema.description
                    })
                    break;
            }
        },
        //勾选 是否必选
        checkChangeRequired(){
            if(this.detailItem.isRoot){
                this.infoConfigDetail.is_required = this.info.is_required?1:0
                DB.Update('info_configs',{
                    id:this.infoConfigDetail.id,
                    is_required:this.infoConfigDetail.is_required
                })
            }else{
                this.detailItem.schema.is_required = this.info.is_required?1:0
                DB.Update('schemas',{
                    id:this.detailItem.schema.id,
                    is_required:this.detailItem.schema.is_required
                })
            }
        },
        //数字类型 更换单位量词
        selectChangeUnit(id){
            if(this.detailItem.isRoot){
                this.infoConfigDetail.unit_id = id
                DB.Update('info_configs',{
                    id:this.infoConfigDetail.id,
                    unit_id: this.infoConfigDetail.unit_id
                })
            }else{
                this.detailItem.schema.unit_id = id
                DB.Update('schemas',{
                    id:this.detailItem.schema.id,
                    unit_id: this.detailItem.schema.unit_id
                })
            }
        },
        //数字类型 更换单位类型
        selectChangeUnitType(){
            this.info.unitId = null
        },
        //列表类型  更换下面的元素类型
        selectChangeElementType(no){
            DB.Update('lists',{
                id:this.detailItem.lists.id,
                element_type_no:no,
                group_by:null
            })
            this.createItem(this.detailItem)
            this.$nextTick(()=>{
                this.$refs.tree.handleSelect(this.detailItem.nodeKey)
            })

        },
        //更换 类型
        selectChangeType(no){
            this.detailItem.type = DB.getTypeByTemplateIdAndNo(this.currentItem.templateId,no)
            if(this.detailItem.isRoot){
                this.infoConfigDetail.type_no = this.detailItem.type.no
                DB.Update('info_configs',{
                    id:this.infoConfigDetail.id,
                    type_no:this.infoConfigDetail.type_no
                })
            }else{
                DB.Update('schemas',{
                    id:this.detailItem.schema.id,
                    type_no:no
                })
                if(this.detailItem.lists){
                    DB.Update('lists',{
                        id:this.detailItem.lists.id,
                        parent_no:no
                    })
                }
            }
            if(this.currentItem.nodeKey == this.detailItem.nodeKey){
                this.treeList = []
                this.$nextTick(()=>{
                    this.currentItem = this.detailItem
                    this.createTree()
                })
            }else{
                this.createItem(this.detailItem)
            }
            this.$nextTick(()=>{
                this.$refs.tree.handleSelect(this.detailItem.nodeKey)
            })

        },
        getRestriction(){
          return {
              min: 0,
              max: Infinity,
              precision: 5,
              format: null
          }
        },
        nodeClick(nodeList,node){
            //新增节点
            if(node.iconType == 'append'){
                const parentKey = node.root.find(el => el.nodeKey === node.nodeKey).parent;
                let index
                if(parentKey!=null){
                    const parent = node.root.find(el => el.nodeKey === parentKey).node;
                    index = parent.children.indexOf(node);
                    let child = {

                    }
                    let children = []
                    let no = 0
                    switch (node.type.type){
                        case 1:
                            children = DB.getCodeValues(this.currentItem.templateId,node.type.no)
                            if(children.length){
                                no = children[children.length-1].no + 1
                            }
                            child.type = {
                                type:4
                            }
                            child.iconType = this.getIconType(child.type)
                            child.title = '新增枚举值'
                            child.code = {
                                "template_id": this.currentItem.templateId,
                                "parent_no": node.type.no,
                                "no": no,
                                "value": child.title,
                                "description": ""
                            }
                            child.code.id = DB.Insert('codes',
                                child.code
                            )
                            break;
                        default:
                            let parentNo = 0
                            if(node.type.type == 2){
                                parentNo = node.type.no
                                children = DB.getSchemaComponents(this.currentItem.templateId,node.type.no)
                            }else if(node.type.type == 3){
                                parentNo = node.lists.element_type.no
                                children = node.lists.element_type.children
                            }
                            if(children.length){
                                no = children[children.length-1].no + 1
                            }
                            child.type = DB.getTypeByTemplateIdAndNo(this.currentItem.templateId,2)
                            child.iconType = this.getIconType(child.type)
                            child.title = '新增字段'
                            child.restriction = this.getRestriction()
                            child.restriction.id = DB.Insert('restrictions',child.restriction)
                            child.schema = {
                                "template_id": this.currentItem.templateId,
                                "parent_no": parentNo,
                                "no": no,
                                "name": child.title,
                                "type_no": child.type.no,
                                "is_required": 0,
                                "restriction_id": child.restriction.id,
                                "description": ""
                            }
                            child.schema.id = DB.Insert('schemas',child.schema)
                            break;
                    }
                    parent.children.splice(index, 0 ,child);
                    this.$nextTick(()=>{
                        this.$refs.tree.handleSelect(child.nodeKey)
                    })
                }
                return
            }
            if(nodeList[0]){
                this.detailItem = nodeList[0]
                Bus.$emit("chooseData",this.detailItem)
                this.updateDetailTypeList()
                this.info.unitId = null

                this.info.unitTypeId = null
                if(this.detailItem.isRoot){
                    this.info.description = this.infoConfigDetail.description
                    if(this.infoConfigDetail.unit_id){
                        this.info.unitId = this.infoConfigDetail.unit_id
                        this.info.unitTypeId = DB.selectOne('units',"id = "+this.info.unitId)['type_id']
                    }
                    this.info.is_required = (this.infoConfigDetail.is_required == 1)
                }else{
                    if(this.detailItem.schema){
                        this.info.is_required = (this.detailItem.schema.is_required == 1)
                    }
                    switch (this.detailItem.type.type){
                        case 0:
                            this.info.description = this.detailItem.schema.description
                            if(this.detailItem.schema.unit_id){
                                this.info.unitId = this.detailItem.schema.unit_id
                                this.info.unitTypeId = DB.selectOne('units',"id = "+this.info.unitId)['type_id']
                            }
                            break;
                        case 1:
                            this.info.description = this.detailItem.schema.description
                            break;
                        case 2:
                            this.info.description = this.detailItem.schema.description
                            break;
                        case 4:
                            this.info.description = this.detailItem.code.description

                            break;
                    }
                }
                if(this.detailItem.type.type == 3){
                    this.info.description = this.detailItem.lists.description
                    this.info.lists.isTree = this.detailItem.lists.tree_show_column_no !=null
                    this.info.lists.min = this.detailItem.lists.min
                    this.info.lists.max = this.detailItem.lists.max
                    if(this.info.lists.min == this.info.lists.max){
                        this.info.lineType = "0"
                    }else{
                        this.info.lineType = "1"
                    }
                }
            }else{
                node.selected = true
            }
        },
        move (root, node, data ,type) {
            const parentKey = root.find(el => el.nodeKey === node.nodeKey).parent;
            let index
            let otherIndex
            if(parentKey!=null){
                const parent = root.find(el => el.nodeKey === parentKey).node;
                index = parent.children.findIndex((el)=>el.nodeKey === data.nodeKey)
                parent.children[index].selected = false
                if(type == 'up'){
                    otherIndex = index - 1
                }else{
                    otherIndex = index + 1
                }
                parent.children.splice(otherIndex,1,...parent.children.splice(index,1,parent.children[otherIndex]))
                this.$refs.tree.handleSelect(parent.children[otherIndex].nodeKey)
                /*互换序号*/
                let newIndex = 0
                let oldIndex = 0
                let tableName = ""
                let currentId = 0
                let otherId = 0
                switch (parent.children[index].type.type){
                    case 4:
                        tableName = "codes"
                        currentId = parent.children[index].code.id
                        otherId = parent.children[otherIndex].code.id
                        break;
                    default:
                        tableName = "schemas"
                        currentId = parent.children[index].schema.id
                        otherId = parent.children[otherIndex].schema.id
                        break;
                }
                newIndex = DB.selectOne(tableName,'id = '+currentId,'')['no']
                oldIndex = DB.selectOne(tableName,'id = '+otherId,'')['no']
                DB.Update(tableName,{
                    id:otherId,
                    no:newIndex
                })
                DB.Update(tableName,{
                    id:currentId,
                    no:oldIndex
                })
            }
        },
        remove(root, node, data){
            const parentKey = root.find(el => el.nodeKey === node.nodeKey).parent;
            if(parentKey!=null){
                const parent = root.find(el => el.nodeKey === parentKey).node;
                let index = parent.children.findIndex((item)=>{
                    return item.nodeKey == node.nodeKey
                });
                setTimeout(()=>{
                    this.$refs.tree.handleSelect(parent.nodeKey)
                    if(parent.lists){
                        DB.Update('lists',{
                            id:parent.lists.id,
                            tree_show_column_no:null
                        })
                        parent.lists.tree_show_column_no = null
                    }
                    parent.children.splice(index,1)
                    switch (data.type.type){
                        case 4:
                            DB.Remove('codes',data.code.id)
                            break;
                        default:
                            DB.Remove('schemas',data.schema.id)
                            break;
                    }
                },200)
            }
        },
        renderContent (h, { root, node, data }) {
            data.root = root
            data.node = node
            const parentKey = root.find(el => el.nodeKey === node.nodeKey).parent;
            if(parentKey!=null){
                const parent = root.find(el => el.nodeKey === parentKey).node;
                let index = parent.children.findIndex((el)=>el.nodeKey === node.nodeKey)
                data.isCanMoveUp = index!=0
                data.isCanMoveDown = index != parent.children.length - 1 && parent.children[parent.children.length - 1].iconType !='append'
            }
            let children  = [
                h('span', data.title)

            ]
            if(data.iconType){
                children.unshift(h('Icon', {
                    props: {
                        type: data.iconType?data.iconType:'ios-paper-outline'
                    },
                    style: {
                        marginRight: '8px'
                    }
                }))
            }
            if(!data.isRoot&&!data.isEditDisabled&&data.iconType!='append'){
                children.push(h('Button', {
                    props: Object.assign({}, {
                        type: 'default',
                        size: 'small',
                    }, {
                        icon:  'node-remove'
                    }),
                    style: {
                        display: 'inline-block',
                        float: 'right',
                        marginLeft: '5px',
                        border:'none',
                        background: 'transparent',
                    },
                    on: {
                        click: () => { this.remove(root, node, data) }
                    }
                }))
            }
            return h('span', {
                style: {
                    display: 'inline-block',
                    width: '100%',
                }
            }, [
                h('span',{
                    style: {
                        display:'flex',
                        'justify-content':'center',
                        'align-items':'center'
                    }
                }, children),
            ]);
        },
        createTree(){
            this.currentItem.isRoot = true
            let item = this.createItem(this.currentItem)
            item.isEditDisabled = !item.isBatchExtend
            this.treeList = [item]
            this.$nextTick(()=>{
                this.$refs.tree.handleSelect(this.treeList[0].nodeKey)
            })
        },
        getIconType(type){
            let iconType = ''
            switch (type.type) {
                case 0:
                    switch (type.no) {
                        case 1:
                            iconType='text'
                            break;
                        case 2:
                            iconType='richText'
                            break;
                        case 3:
                            iconType='numeric'
                            break;
                        case 4:
                            iconType='integer'
                            break;
                        case 5:
                            iconType='real'
                            break;
                        case 6:
                            iconType='date'
                            break;
                        case 7:
                            iconType='time'
                            break;
                        case 8:
                            iconType='data'
                            break;
                        case 9:
                            iconType='image'
                            break;
                    }
                    break;
                case 1:
                    iconType='select'
                    break;
                case 2:
                    iconType='map'
                    break;
                case 3:
                    iconType='array'
                    break;
            }
            return iconType
        },
        createItem(item){
            if(item.isRoot){
                item.restriction = DB.selectOne('restrictions','id = '+this.infoConfigDetail.restriction_id)
            }else if(item.schema){
                item.restriction = DB.selectOne('restrictions','id = '+item.schema.restriction_id)
            }
            item.title = item.name
            item.children = []
//            this.$set(item,'children',[])
            item.iconType = this.getIconType(item.type)
            let childList = []
            switch (item.type.type){
                case 0:
                    if(item.schema){
                        item.title = item.schema.name
                    }
                    break;
                case 1:
                    if(item.schema){
                        item.title = item.schema.name
                    }
                    let codeValues = DB.getCodeValues(this.currentItem.templateId,item.type.no)
                    codeValues.forEach((code)=>{
                        item.children.push({
                            title:code.value,
                            type:{
                                type:4
                            },
                            code:code,
                            iconType:this.getIconType({
                                type:4
                            })
                        })
                    })
                    item.children.push({
                        iconType:'append',
                        title:'新增',
                        type:item.type,
                        schema:item.schema
                    })
                    break;
                case 2:
                    if(item.schema){
                        item.title = item.schema.name
                    }
                    let schemas = DB.getSchemaComponents(this.currentItem.templateId,item.type.no)
                    schemas.forEach((schema)=>{
                        childList.push({
                            type:DB.getTypeByTemplateIdAndNo(this.currentItem.templateId,schema.type_no),
                            schema:schema,
                            name:schema.name
                        })
                    })
                    childList.forEach((child)=>{
                        item.children.push(this.createItem(child))
                    })
                    item.children.push({
                        iconType:'append',
                        title:'新增',
                        type:item.type,
                        schema:item.schema
                    })
                    break;
                case 3:
                    if(item.schema){
                        item.title = item.schema.name
                    }else{
                        item.title = item.type.name
                    }
                    item.lists = DB.getTableSchema(this.currentItem.templateId,item.type.no)
                    item.lists.element_type.children.forEach((schema)=>{
                        let child = {
                            type:DB.getTypeByTemplateIdAndNo(this.currentItem.templateId,schema.type_no),
                            schema:schema,
                            name:schema.name
                        }
                        if(item.lists.element_type.template_id == null){
                            item.isEditDisabled = true
                            child.isEditDisabled = true
                        }
                        childList.push(child)
                    })
                    childList = DB.removeUnneedFields(childList)
                    childList.forEach((child)=>{
                        item.children.push(this.createItem(child))
                    })
                    if(item.lists.element_type.type != 0){
                        item.children.push({
                            iconType:'append',
                            title:'新增',
                            type:item.type,
                            schema:item.schema,
                            lists:item.lists
                        })
                    }
                    break;
            }
            if(item.isRoot){
                item.title = item.name
            }
            if(item.children.length){
                this.$set(item,'expand',true)
            }
            return item
        }
    }
}
</script>

