<template>
  <div class="tableList">
      <div class="tableListBox" v-if="controls.isOpen">
          <div class="table-header">
              <Button @click="back" v-if="!controls.isFirst&&!controls.isOther" style="margin-left: 20px">返回</Button>
              <Button @click="save" v-else-if="controls.isFirst&&!isView&&!controls.isOther" style="margin-left: 20px">保存</Button>
              <Button  @click="exportExcel" v-if="!isView" style="margin-left: 20px">生成模板</Button>
              <Button v-if="!isView"  style="margin-left: 20px;position: relative;">
                  导入
                  <input type="file" class="file-input"  @change="fileChange($event)">
              </Button>
              <Button @click.stop="append()" style="margin-left: 20px;" v-if="(isGroupBy?currentGroup:true)&&!isView">新增</Button>
              <Page show-sizer :current="pageNo" style="margin-left: 20px" :total="viewList.length" @on-change="pageNoChange" @on-page-size-change="pageSizeChange" :page-size="pageSize"/>
          </div>
          <div class="table-flex">
              <div class="tree-box" v-if="treeList.length" v-show="!currentTable">
                  <Tree  ref="tree" :render="renderContent" empty-text=""  :data="treeList" @on-select-change="nodeClick" class="demo-tree-render"></Tree>
              </div>
              <div ref="table-box" class="table-box" :class="treeList.length?'':'alone'" v-show="!currentTable">
                  <Table  border  :columns="compositeColumns" :disabled-hover="true" :data="currentPageList" >
                      <template slot-scope="{ row }" slot="control" v-if="!isView">
                          <Button @click.stop="remove(row)" size="small" >删除</Button>
                          <Button @click.stop="openRow(row)" size="small" v-if="item.lists.element_type.children.length">编辑</Button>
                      </template>
                      <template slot-scope="{ row,column}" v-for="(expandColumn,columnIndex) in expandColumns" :slot="expandColumn.slot">
                          <div style="padding-top: 10px;padding-bottom: 10px">
                              <Item :isView="true" :ref="expandColumn.slot+'-'+row._index" v-model="row[expandColumn.slot]" @input="inputChange(row,expandColumn.slot,$event)" :control="{row:1}"  v-if="row[expandColumn.slot].type.type<=1||row[expandColumn.slot].valueSet"></Item>
                              <a v-else-if="row[expandColumn.slot].type.type==3" @click="openTable(row,expandColumn.slot)">{{row[expandColumn.slot].value.length}}行</a>
                          </div>
                      </template>
                  </Table>
                  <div class="append-box" v-if="(isGroupBy?currentGroup:true)&&!isView&&false">
                      <Button @click.stop="append()">新增</Button>
                  </div>
              </div>
          </div>
          <div class="tableChildren" v-if="currentTable">
              <table-list :isView="isView" @back="back" :isChild="true" :controls="{isOpen:true}"  ref="table-list"  :columns="currentTable.columns" :item="currentTable"  :expandColumns="currentTable.expandColumns" :content="currentTable.value"></table-list>
          </div>
          <div class="tableChildrenComposite" v-if="currentRow">
              <div class="table-header">
                  <Button @click="back"  style="margin-left: 20px">返回</Button>
              </div>
              <div class="table-composite-box">
                  <ChildComposite :isView="isView"  ref="composite"   :content="currentContent"></ChildComposite>

              </div>
          </div>
      </div>
      <div class="lineLength" v-show="!controls.isOpen">
          <a @click="controls.isOpen = true">{{viewList.length}}行</a>
      </div>
      <Modal
              v-model="modal.showState"
              class-name="modal"
              :closable="false"
              :mask-closable="false"
              :styles="{top: '0',width:'420px'}"
      >
          <div slot="header" class="modal-header">
              <div class="title">{{modal.title}}</div>
              <div class="control">
                  <img src="../../assets/sidebar/关闭1@3x.png" @click="cancel()">
              </div>
          </div>
          <div class="group-box" v-show="modal.viewState == 'group'">
              <Form :model="modal.info" :label-width="100">
                  <FormItem :label="treeList[0].name" v-if="treeList.length">
                      <Item ref="group" v-model="modal.info" v-if="modal.info" :control="{row:1}"></Item>
                  </FormItem>
              </Form>
          </div>
          <div slot="footer" class="modal-footer">
              <Button  size="large"  @click="confirm">确定</Button>
          </div>
      </Modal>
  </div>
</template>

<script>
//    import Composite from '@component/fill/ChildComposite';
    import DB from "@lib/Db";
    import Bus from '@event/bus';
    import {dp} from '@lib/util';
    import Split from "../../../node_modules/view-design/src/components/split/split.vue";
    export default {
        name:'table-list',
        components: {
            Split,
        },
        props: {
            controls:{
                type:Object,
                default(){
                    return{
                        isOpen:false,
                        isFirst:false,
                        isOther:false,
                    }
                },
            },
            isView:{
                type:Boolean,
                default:false
            },
            isChild:{
                type:Boolean,
                default:false
            },
            item:{
                type:Object,
                default(){
                    return {}
                }
            },
            content:{
                type:Array,
                default(){
                    return []
                }
            },
            columns:{
                type:Array,
                default(){
                    return []
                }
            },
            expandColumns:{
                type:Array,
                default(){
                    return []
                }
            }
        },

        data(){

            return{
                split1:0,
                compositeColumns:[],
                treeList:[],
                currentGroup:null,
                isGroupBy:false,
                viewList:[],
                modal:{
                    showState:false,
                    title:"",
                    info:null,
                    viewState:""
                },
                currentRow:null,
                currentContent:[],
                currentTable:null,
                rowIndex:0,
                columnSlot:'',
                currentPageList:[],
                pageSize:10,
                pageNo:1
            }
        },

        watch: {
            ['controls.isOpen'](){
                if(this.controls.isOpen){
                    this.onShow()
                }
            },
            item(){
//                if(this.controls.isOpen){
                    this.onShow()
//                }
            }
        },

        created () {},
        beforeDestroy(){

        },
        update(){

        },
        mounted () {
            this.onShow()
        },

        methods: {
            pageSizeChange(pageSize){
                this.pageSize = pageSize
                this.render(1)
            },
            render(pageNo){
                this.pageNo = pageNo
                this.currentPageList = []
                this.viewList.forEach((item,index)=>{
                    if( index >= (pageNo-1)*this.pageSize && index < pageNo*this.pageSize ){
                        this.currentPageList.push(item)
                    }
                })
                if(this.currentPageList.length == 0&&this.pageNo!=1){
                    this.render(this.pageNo - 1)
                }
            },
            pageNoChange(pageNo){
                this.render(pageNo)
            },
            fileChange(e){
                let file = e.target.files[0]
                if(file){
                    let file_reader = new FileReader();
                    let blob = null
                    file_reader.onload = () => {
                        let fc = file_reader.result;
//                    blob = new Blob([fc])
                        e.target.value = ""
                        this.$Message.success('文件读取成功')
                        this.importExcel(fc)

                    };
                    file_reader.readAsBinaryString(file);
                }
            },
            importExcel(file){
                this.$Message.success('正在解析文件')
                let list = DB.importExcel(file)
                this.$Message.success('解析成功-共'+list.length+'条数据')
                list.forEach((item)=>{
                    this.append(item)
                })
            },
            exportExcel(){
                let columns = this.expandColumns.filter((item)=>{
                    return this.item.lists.group_by?this.item.lists.group_by != item.name : true
                })
                let list = []
                columns = columns.filter((item)=>{
                    let flag = (!item.children || item.children.length == 0)&&item.type.type!=3
                    if(flag){
                        list.push(item.slot)
                    }
                    return flag
                })
                db_writeExcel(this.item.name+"-表格模板","sheet1",JSON.stringify(list))
            },
            save(){
                this.$emit('save')
            },
            compositeToRow(row){
                let newRow = {}
                if(Object.prototype.toString.call(row) === '[object Object]'){
                    newRow = DB.extendDeep({},row)
                }
                this.expandColumns.forEach((column)=>{
                    const path = column.slot.split('-')
                    let v = row
                    if(!this.item.lists.element_type.children.length){

                    }else{
                        for (const part of path) {
                            if (v) {
                                if (typeof(v) === 'string'&&DB.isJSON(v)) {
                                    v = JSON.parse(v)
                                }
                                if (v[part]!=undefined) {
                                    v = v[part]
                                } else if(path[path.length-1] == part) { //如果是最后再赋值为null
                                    v = null
                                }
                            }
                        }
                    }
                    newRow[column.slot] = DB.extendDeep({},column)
                    newRow[column.slot].value = v||v==0?v:''
                })
                return newRow
            },
            back(){
                if(this.isChild){
                    this.$emit('back')
                    return
                }
                if(this.currentTable){
                    this.state = true
                    let value = this.getRefValue('table-list',this.currentTable.value)
                    if(this.state){
                        this.viewList[this.rowIndex][this.columnSlot ].value = value
                        this.render(this.pageNo)
                        this.currentTable = null
                    }else{
                        this.$Message.error(this.hint)
                    }
                }else if(this.currentRow){
                    this.state = true
                    let value = this.getRefValue('composite',this.currentRow)
                    if(this.state){
                        this.$set(this.viewList,this.rowIndex,this.compositeToRow(value))
                        this.render(this.pageNo)
                        this.currentRow = null
                    }else{
                        this.$Message.error(this.hint)
                    }
                }else{
                    let obj = this.getValue()
                    if(!obj.state&&!this.isView){
                        this.$Message.error(obj.hint)
                        return
                    }
                    this.content = obj.value
                    this.$emit('input',obj)
                    this.controls.isOpen = false
                }
            },
            openRow(row){
                this.rowIndex = (this.pageNo - 1) * this.pageSize + row._index
                this.currentRow = row
                let findContent = (column,key)=>{
                    if(this.currentRow[key]){
                        column.value = this.currentRow[key].value
                    }
                    if(column.children){
                        column.children.forEach((child)=>{
                            findContent(child,child.slot)
                        })
                    }
                }
                this.currentContent = DB.extendDeep([],this.expandColumns,true)
                this.currentContent.forEach((column)=>{
                    findContent(column,column.slot)
                })
            },
            openTable(row,columnSlot){
                this.rowIndex = (this.pageNo - 1) * this.pageSize + row._index
                this.columnSlot = columnSlot
                let table = this.viewList[this.rowIndex][this.columnSlot ]
                this.currentTable = table
            },
            cancel(){
                this.modal.showState = false
            },
            confirm(){
                let value = this.getRefValue("group","")
                if(this.state){
                    let group = this.treeList[0].children.find((item)=>{
                        return value == item.value
                    })
                    if(group){
                        this.$Message.error("此分组已存在！")
                    }else{
                        let groupColumn = this.treeList[0]
                        let children = this.treeList[0].children
                        groupColumn.children.splice(groupColumn.children.length -1,0,{
                            title:value,
                            value:value
                        })
                        this.$set(this.treeList,0,groupColumn)
                        this.$forceUpdate()
                        this.modal.showState = false
                        setTimeout(()=>{
                            this.$refs.tree.handleSelect(groupColumn.children[groupColumn.children.length-2].nodeKey)
                        },300)
                    }
                }
            },
            getRefValue(refName,value){
                let ref = null
                if(this.$refs[refName]&&this.$refs[refName][0]){
                    ref = this.$refs[refName][0]
                }else{
                    ref = this.$refs[refName]
                }
                if(ref&&ref.getValue){
                    let obj = ref.getValue()
                    if(!obj.state){
                        this.state = obj.state
                        this.hint = obj.hint
                    }
                    value = obj.value
                }
                return value
            },
            getValue(){
                this.state = true
                let list = []
                this.currentPageList.forEach((row,index)=>{
                    let obj = {}
                    if(this.item.lists.element_type.children.length){
                        for(let key in row){
                            if(key.indexOf('-') !== -1){
                                let keyList = key.split('-')
                                let parent
                                let cell = obj
                                let lastKey = ''
                                keyList.forEach((keyItem)=>{
                                    if (!Object.hasOwnProperty.call(cell, keyItem) || cell[keyItem]==null ||typeof(cell[keyItem]) === 'string') {
                                        cell[keyItem] = {}
                                    }
                                    parent = cell
                                    cell = cell[keyItem]
                                    lastKey = keyItem
                                })
                                parent[lastKey] = this.getRefValue(key+'-'+index,row[key].value)
                            }else{
                                let oldValue = row[key]
                                if(row[key]&&row[key]!=0&&Object.prototype.toString.call(row[key]) === '[object Object]'){
                                    if(row[key].value==undefined||row[key].value===""){
                                        oldValue = null
                                    }else{
                                        oldValue = row[key].value
                                    }
                                }
                                obj[key] = this.getRefValue(key+'-'+index,oldValue)
                            }
                        }
                        for(let key in obj){
                            if(Object.prototype.toString.call(obj[key]) === '[object Object]'){
                                obj[key] = JSON.stringify(obj[key])
                            }
                        }
                    }else{
                        obj = this.getRefValue('值-'+index,row["值"].value)
                    }
//                    list.push(obj)
                })
                this.viewList.forEach((row,index)=>{
                    let obj = {}
                    if(this.item.lists.element_type.children.length){
                        for(let key in row){
                            if(key.indexOf('-') !== -1){
                                let keyList = key.split('-')
                                let parent
                                let cell = obj
                                let lastKey = ''
                                keyList.forEach((keyItem)=>{
                                    if (!Object.hasOwnProperty.call(cell, keyItem) || cell[keyItem]==null ||typeof(cell[keyItem]) === 'string') {
                                        cell[keyItem] = {}
                                    }
                                    parent = cell
                                    cell = cell[keyItem]
                                    lastKey = keyItem
                                })
                                parent[lastKey] = row[key].value
                            }else{
                                let oldValue = row[key]
                                if(row[key]&&row[key]!=0&&Object.prototype.toString.call(row[key]) === '[object Object]'){
                                    if(row[key].value==undefined||row[key].value===""){
                                        oldValue = null
                                    }else{
                                        oldValue = row[key].value
                                    }
                                }
                                obj[key] = oldValue
                            }
                        }
                        for(let key in obj){
                            if(Object.prototype.toString.call(obj[key]) === '[object Object]'){
                                obj[key] = JSON.stringify(obj[key])
                            }
                        }
                    }else{
                        obj = row["值"].value
                    }
                    list.push(obj)
                })
                return {
                    value:list,
                    state:this.state,
                    hint:this.hint
                }
            },
            inputChange(row,column,e){
                if(row._index!=undefined){
                    this.viewList[ (this.pageNo - 1) * this.pageSize + row._index][column].value = e.value
                }
            },
            createRequired(item){
                if(item.schema&&item.schema.is_required){
                    item.title = "※ " +item.title
                }
                if(item.children){
                    item.children.forEach((child)=>{
                        this.createRequired(child)
                    })
                }
            },
            onShow(){
                let isArray = Object.prototype.toString.call(this.content) == '[object Array]'
                if(!isArray){
                    this.content = []
                }
                this.viewList = []
                this.content.forEach((row)=>{
                    this.viewList.push(this.compositeToRow(row))
                })
                this.render(1)
                this.treeList = []
                this.currentGroup = null
                let group_by = null
                if(this.item.lists&&this.item.lists.group_by){
                    this.isGroupBy = true
                    group_by = this.item.lists.group_by
                    this.split1 = 0.15
                    let groupColumn = this.columns.find((item)=>{
                        return item.name == group_by
                    })
                    groupColumn.title = groupColumn.name +"分组"
                    groupColumn.children = []
                    if(groupColumn.type.type == 1){
                        groupColumn.codeValues.forEach((code)=>{
                            let newCode = DB.extendDeep({},code)
                            newCode.title = code.value
                            newCode.value = code.no
                            newCode.dataType = 'code'
                            groupColumn.children.push(newCode)
                        })
                    }else{
                        this.viewList.forEach((row)=>{
                            let group = groupColumn.children.find((item)=>{
                                return row[groupColumn.name].value == item.value
                            })
                            if(!group){
                                let value = row[groupColumn.name].value
                                let title = row[groupColumn.name].value
                                groupColumn.children.push({
                                    title:title,
                                    value:value
                                })
                            }
                        })
                    }
                    groupColumn.dataType = 'expand'
                    if(groupColumn.type.type != 1&&!this.isView){
                        groupColumn.children.push({
                            title:'新增分组',
                            iconType:'append'
                        })
                    }

                        this.treeList.push(groupColumn)
                        if(groupColumn.children[0].iconType!='append'){
                            this.$set(this.treeList[0],'expand',true)
                            this.$nextTick(()=>{
                                this.$refs.tree.handleSelect(this.treeList[0].children[0].nodeKey)
                            })
                        }

                }else{
                    this.isGroupBy = false
                }
                if(this.columns.length==1&&this.columns[0].children&&this.columns[0].children.length){
                    this.columns = this.columns[0].children
                }
                this.compositeColumns = []
                this.$nextTick(()=>{
                    let columns = this.columns.filter((item)=>{
                        return group_by?group_by != item.name : true
                    })
                    let width = this.isView?1:70
                    if(this.$refs['table-box']){
                        let columnWidth = (this.$refs['table-box'].clientWidth - width -2) / columns.length
                        columns.forEach((item,index)=>{
                            this.$set(item,'resizable',true)
                            this.$set(item,'width',columnWidth)
                            this.createRequired(item)
                        })
                        let compositeColumns = [{
                            title:"操作",
                            resizable:true,
                            slot:"control",
                            align:'center',
                            width:width,
                            filters: [
                                {
                                    label: '',
                                    value: 1
                                }
                            ],
                            filterMultiple: false,
                            filteredValue:[1],
                            filterMethod: (value, row)=> {
                                if(this.currentGroup){

                                }
                                return this.currentGroup?this.currentGroup.value == row[this.treeList[0].name].value:true;
                            }
                        }].concat(columns)
                        this.compositeColumns = compositeColumns
                    }
                })
            },
            remove(row){
                this.viewList.splice((this.pageNo - 1) * this.pageSize + row._index,1)
                this.render(this.pageNo)
            },
            append(data){
                let obj = {}
                this.expandColumns.forEach((expandColumn)=>{
                    obj[expandColumn.slot] = DB.extendDeep({},expandColumn)
                })
                for (var item in data){
                    if(obj[item]){
                        obj[item].value = data[item]
                    }
                }
                if(this.currentGroup){
                    obj[this.treeList[0].name].value = this.currentGroup.value
                }
                this.viewList.push(obj)
                this.render(Math.ceil(this.viewList.length/this.pageSize))
                if(!data){
                    let row = this.currentPageList[this.currentPageList.length - 1]
                    row._index = this.currentPageList.length - 1
                    this.openRow(row)
                }
            },
            renderContent (h, { root, node, data }) {
                let children  = [
                    h('span', data.title)
                ]
                if(data.iconType){
                    children.unshift(h('Icon', {
                        props: {
                            type: data.iconType?data.iconType:'ios-paper-outline'
                        },
                        style: {
                            marginRight: '8px'
                        }
                    }))
                }
                if(data.iconType!='append'&&data.dataType!='expand'&&data.dataType!='code'&&!this.isView){
                    children.push(h('Button', {
                        props: Object.assign({}, {
                            type: 'default',
                            size: 'small',
                        }, {
                            icon:  'ios-remove'
                        }),
                        style: {
                            display: 'inline-block',
                            float: 'right',
                            marginLeft: '5px'
                        },
                        on: {
                            click: () => { this.delete(root, node, data) }
                        }
                    }))
                }
                return h('span', {
                    style: {
                        display: 'inline-block',
                        width: '100%',
                    }
                }, [
                    h('span',{
                        style: {
                            display:'flex',
                            'justify-content':'center',
                            'align-items':'center'
                        }
                    }, children),
                ]);
            },
            nodeClick(nodeList,node){
                if(node.iconType == 'append'){
                    node.selected = false
                    if(this.currentGroup){
                        this.currentGroup.selected = true
                    }
                    let obj = this.getValue()
                    if(!obj.state){
                        this.$Message.error(obj.hint)
                    }else{
                        this.modal.showState = true
                        this.modal.viewState = 'group'
                        this.modal.titile = "创建分组"
                        this.modal.info = DB.extendDeep({},this.treeList[0])
                    }
                    return
                }
                if(node.dataType=='expand'){
                    node.selected = false
                    this.$set(node,'expand',!node.expand)
                    if(this.currentGroup){
                        this.$refs.tree.handleSelect(this.currentGroup.nodeKey)
                    }
                    return
                }
                if(node!=this.currentGroup&&this.currentGroup){
                    let obj = this.getValue()
                    if(!obj.state&&!this.isView){
                        node.selected = false
                        this.$Message.error(obj.hint)
                        this.currentGroup.selected = true
                        return
                    }
                }
                node.selected = true
                this.currentGroup = node
                let viewList = DB.extendDeep([],this.viewList,true)
                this.viewList = []
                this.$nextTick(()=>{
                    this.viewList = viewList
                    this.render(1)
                })
            },
            delete (root, node, data) {
                setTimeout(()=>{
                    const parentKey = root.find(el => el === node).parent;
                    let index
                    if(parentKey!=null){
                        const parent = root.find(el => el.nodeKey === parentKey).node;
                        index = parent.children.indexOf(data);
                        this.viewList = this.viewList.filter((row)=>{
                            return data.value != row[this.treeList[0].name].value
                        })
                        parent.children.splice(index, 1);
                        this.treeList = JSON.parse(JSON.stringify([parent]))
                        if(this.treeList[0].children[0].iconType=='append'){
                            data.selected = false
                            this.currentGroup = null
                        }else{
                            this.$refs.tree.handleSelect(this.treeList[0].children[0].nodeKey)
                        }
                    }
                },300)

            }
        }
    }
</script>

<style>
    .file-input{
        width: 100%;
        height: 100%;
        position: absolute;
        opacity: 0;
        left: 0;
        top: 0;
    }
.table-flex{
    display: flex;
    height: calc(100% - 50px);
}
    .tableList .ivu-table-cell .ivu-poptip{
        display: none;
    }
    .tableList{
        display: flex;
        height: 100%;
        /*position: relative;*/
    }
   #fill-main .tableList .table-box{
        width: calc(100% - 12vw);
       overflow: scroll;
       flex-grow: 1;
    }
    #fill-main .tableList .table-box.alone{
        width:100%;
    }
  .tableList .tree-box{
      overflow-x: scroll;
      padding-left: 10px;
      text-align: left;
      /* flex: 0 0 300px; */
      /* max-height: 800px; */
      max-width: 12vw;
      width: auto;
      border: solid 1px #dcdee2;
      margin-right: 10px;
      padding-right: 10px;
  }

  .tableList .append-box{
    border:solid 1px #dcdee2;
    border-top: none;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 50px;
  }
  .tableList .append-box img{
    height: 50%;
  }
