<template>
  <div id="fill-main">
      <div class="content-main" v-if="content.item&&viewState=='fill'">
          <div class="table-header" v-show="!isConfiguration">
              <Button @click="openModal(2)" v-if="isCanRelease&&!isView" style="margin-left: 20px;">发布</Button>
              <Button @click="openModal(0)" v-if="isCanRelease&&isView" style="margin-left: 20px;">撤销发布</Button>
              <!--<img src="../../assets/users/提交-常态@3x.png" @click="save">-->
              <Button @click="save" v-if="!isView" style="margin-left: 20px;">保存</Button>
          </div>
          <div class="table-content">
              <component @save="save" :controls="{isOpen:true,isFirst:true}" :isView="isView" ref="current" :isRealList="content.isRealList" :control="{row:20}" v-model="content.item" :is="content.component"  :item="content.item" :expandColumns="content.expandColumns" :content="content.content" :columns="content.columns" />
          </div>
      </div>
      <div class="trash-main" v-if="viewState=='trash'">
        <div class="table-box">
          <Restore></Restore>
        </div>
      </div>
      <Modal
              v-model="modal.showState"
              class-name="modal"
              :closable="false"
              :mask-closable="false"
              :styles="{top: '0',width:'420px'}"
      >
          <div slot="header" class="modal-header">
              <div class="title">{{modal.title}}</div>
              <div class="control">
                  <img src="../../assets/sidebar/关闭1@3x.png" @click="modal.showState = false">
              </div>
          </div>
          <div class="hint-box" >
              {{modal.viewState==0?'确认撤销发布？':'确认发布数据包？'}}
          </div>
          <div slot="footer" class="modal-footer">
              <Button  size="large"  @click="confirm">确定</Button>
          </div>
      </Modal>
  </div>
</template>

<script>
import DB from "@lib/Db";
import Bus from '@event/bus';
import {dp} from '@lib/util'
import FillTree from '@component/fill/batch/FillTree'
import Restore from '@pages/fill/component/Restore'
export default {
    components: {
        Restore
    },

    props: {

    },

    data(){
        return{
            content:{
                component:null,
                item:null,
                list:[],
                columns:[],
                specProc:null,
                isRealList:false
            },
            viewState:"",
            isConfiguration:false,
            isCanRelease:true,
            isView:false,
            modal:{
                showState:false,
                title:"提示信息",
                viewState:""
            },
        }
    },

    watch: {},

    created () {},
    beforeDestroy(){
        Bus.$off('fill-choose')
    },
    mounted () {
        Bus.$on("fill-choose",(data,specProc)=>{
            if(this.$route.path == '/'){
                this.isView = true
            }else{
                this.isView = false
            }
            if(data){
                this.isCanRelease = false
                this.content.specProc = specProc
                this.content.isRealList = data.isRealList
                this.isConfiguration = false
                switch (data.dataType){
                    case 'infoConfig':
                        this.content.item = data
                        this.viewState = "fill"
                        this.intoComponent()
                        break;
                    case 'trash':
                        this.viewState = data.dataType
                        break;
                    case 'batch':
                    case 'air':
                        this.isCanRelease = true
                    default:
                        this.content.item = data
                        this.viewState = "fill"
                        this.intoComponent()
                        break;
                }
            }else{
                this.viewState = 'empty'
            }
        })
    },

    methods: {
        confirm(){
            this.release(this.modal.viewState)
            this.modal.showState = false
        },
        openModal(state){
            this.modal.viewState =   state
            this.modal.showState = true
        },
        release(state){
            switch (this.content.item.dataType){
                case 'batch':
                    DB.Update('batchs',{
                        id:this.content.item.id,
                        state:state
                    })
                    break;
                case 'air':
                    DB.Update('airs',{
                        id:this.content.item.id,
                        state:state
                    })
                    break;
            }
            switch (state){
                case 2:
                    this.$Message.success('发布成功！')
                    break;
                case 0:
                    this.$Message.success('撤销成功！')
                    break;
            }
            Bus.$emit('fill-release')
            this.viewState = 'empty'
        },
        save(){
            let obj = this.$refs.current.getValue()
            if(obj.state){
                this.$Message.success('保存成功！')
                Bus.$emit('fill-save',obj.value)
            }else{
                this.$Message.error(obj.hint)
            }
            return obj.state
        },
        createTableList(item,expandColumns,parentSlot){
            let childList = []
            let children = []
            if(!item.title){
                item.title = item.name
            }
            item.slot = (parentSlot?parentSlot+'-':'')+ item.name
            switch (item.type.type){
                case 0:
                    expandColumns.push(item)
                    break;
                case 1:
                    let codeValues = DB.getCodeValues(this.content.item.relation.templateId,item.type.no)
                    item.codeValues = codeValues
                    expandColumns.push(item)
                    break;
                case 2:
                    let schemas = DB.getSchemaComponents(this.content.item.relation.templateId,item.type.no)
                    schemas.forEach((schema)=>{
                        childList.push({
                            type:DB.getTypeByTemplateIdAndNo(this.content.item.relation.templateId,schema.type_no),
                            schema:schema,
                            name:schema.name
                        })
                    })
                    childList.forEach((child)=>{
                        let obj = this.createTableList(child,expandColumns,item.slot)
                        if(obj.type.type!=2||obj.children){
                            children.push(child)
                        }
                    })
                    break;
                case 3:
                    if(!item.valueSet){
                        if(!item.value){
                            item.value = []
                        }
                        item.expandColumns = []
                        item.lists = DB.getTableSchema(this.content.item.relation.templateId,item.type.no)
                        item.lists.element_type.children.forEach((schema)=>{
                            let child = {
                                type:DB.getTypeByTemplateIdAndNo(this.content.item.relation.templateId,schema.type_no),
                                schema:schema,
                                name:schema.name,
                                title:schema.title
                            }
                            childList.push(child)
                        })
                        if(!item.lists.element_type.children.length){
                            childList.push({
                                type:item.lists.element_type,
                                name:"值"
                            })
                        }
                        if (this.content.specProc) {
                            childList = this.content.specProc(childList)
                            this.content.specProc = null
                        }
                        childList.forEach((child)=>{
                            let obj = this.createTableList(child,item.expandColumns)
                            if(obj.type.type!=2||obj.children){
                                children.push(child)
                            }
                        })
                        item.columns = DB.extendDeep([],children,true )
                        children = []
                    }
                    if(expandColumns){
                        expandColumns.push(item)
                    }
                    break;
            }
            if(children.length){
                item.children = children
            }
            return item
        },
        createComposite(item,value){
            item.children = []
            let childList = []
            item.value = value
            if(!item.title){
                item.title = item.name
            }
            if(item.schema){
                item.id = item.schema.id
            }
            switch (item.type.type){
                case 0:

                    break;
                case 1:
                    let codeValues = DB.getCodeValues(this.content.item.relation.templateId,item.type.no)
                    item.codeValues = codeValues
                    break;
                case 2:
                    let schemas = DB.getSchemaComponents(this.content.item.relation.templateId,item.type.no)
                    schemas.forEach((schema)=>{
                        childList.push({
                            type:DB.getTypeByTemplateIdAndNo(this.content.item.relation.templateId,schema.type_no),
                            schema:schema,
                            name:schema.name,
                            title:schema.title,
                        })
                    })
                    if (this.content.specProc) {
                        childList = this.content.specProc(childList)
                        this.content.specProc = null
                    }
                    childList.forEach((child)=>{
                        let childValue = child.value
                        if(value&&value[child.name]!==null&&value[child.name]!==undefined){
                            childValue = value[child.name]
                        }
                        if(!childValue&&childValue!=0){
                            childValue = ""
                        }
                        let obj = this.createComposite(child,childValue)
                        if(obj.children.length||obj.type.type!=2){
                            item.children.push(obj)
                        }
                    })
                    break;
                case 3:
                    this.createTableList(item)
                    break;
            }
            return item
        },
        intoComponent(){
            this.isConfiguration = false
            switch (this.content.item.type.type){
                case 0:
                case 1:
                    if(!this.content.item.title){
                        this.content.item.title = this.content.item.name
                    }
                    this.content.component = "Item"
                    break;
                case 2:
                    this.createComposite(this.content.item,this.content.item.value)
                    this.content.content = this.content.item.children
                    this.content.component = "Composite"
                    break;
                case 3:
                    let lists = DB.getListElement(this.content.item.relation.templateId,this.content.item.type.no)
                    if(lists.tree_show_column_no!=null){
                        this.isConfiguration = true
                        this.content.item.elementItem = {
                            type:DB.getTypeByTemplateIdAndNo(this.content.item.relation.templateId,lists.element_type_no)
                        }
                        this.content.item.lists = lists
                        this.createComposite(this.content.item.elementItem,{})
                        let otherList = DB.Select("info_configs","reference_to = "+this.content.item.relation.infoConfigId,"")
                        this.content.item.otherList = otherList.filter((item)=>{
                            let flag = false
                            let listsItem = DB.getTableSchema(this.content.item.relation.templateId,item.type_no)
                            if(listsItem.tree_show_column_no == null){
                                item.type = DB.getTypeByTemplateIdAndNo(this.content.item.relation.templateId,item.type_no)
                                flag = true
                            }
                            return flag
                        })
                        this.content.component = FillTree
                    }else{
                        this.createTableList(this.content.item)
                        this.content.expandColumns = this.content.item.expandColumns
                        this.content.columns = this.content.item.columns
                        this.content.content = this.content.item.value
                        this.content.component = "TableList"
                    }
                    break;
            }
            
        }
    }
}
</script>

<style>
  #fill-main{
      height: 100%;
    padding-left: 6px;
  }
  #fill-main .content-main{
      height: 100%;
  }
  #fill-main .table-header{
      border:solid 1px #dcdee2;
      height: 50px;
      border-bottom: none;
      text-align: center;
      background: rgb(225,241,249);
      position: relative;
      display: flex;
      align-items: center;
      /*justify-content: center;*/
  }
  #fill-main .table-header img{
      position: absolute;
      right: 20px;
      top: 0;
      bottom: 0;
      margin: auto;
      width: 30px;
  }
  #fill-main .table-content{
      height: calc(100% - 50px);
      overflow: scroll;
  }
  #fill-main .ivu-table-wrapper{
      overflow: visible;
  }
  #fill-main .table-box{
      width: 100%;
  }
  #fill-main .tree-box .ivu-tree ul{
    /*font-size: 0.72916666vw;*/
    font-size: 14px;
  }
  #fill-main .tree-box .ivu-tree-arrow i{
    /*font-size: 0.72916666vw;*/
    font-size: 14px;

  }
  #fill-main .tree-box .ivu-tree-arrow{
    display: inline-flex;
    align-items: center;
  }
  #fill-main .tree-box .ivu-icon-ios-arrow-forward:before{
    content: "";
  }
  #fill-main .tree-box .ivu-tree-arrow-open .ivu-icon.ivu-icon-ios-arrow-forward{
    background:url(../../assets/tree/收起.png);
    -webkit-background-size: 100% 100%;
    background-size: 100% 100%;
  }
  #fill-main .tree-box .ivu-icon.ivu-icon-ios-arrow-forward{
    background:url(../../assets/tree/展开.png);
    -webkit-background-size: 100% 100%;
    background-size: 100% 100%;
    transform: rotate(0deg);
    /*width: 0.57291666vw;*/
    /*height: 0.57291666vw;*/
    width: 11px;
    height: 11px;
  }
  #fill-main .ivu-icon{
    width: 16px;
    height: 16px;
  }


</style>