<template>
  <div>
    <Menu/>
    <Nav/>
    <div class="contents">
      <router-view></router-view>
    </div>
  </div>
</template>

<script>
import Menu from '@component/Menu'
import Nav from '@component/Nav'
import Bus from '@event/bus'
export default {
  components: {
    Menu,
    Nav
  },
  watch: {
    $route: function(to, from) {
      Bus.$emit('route-change', to, from);
    }
  }
}
</script>
<style>
  #app>div{
    height: 100%;
  }
  .contents{
    height: calc(100% - 80px);
  }
</style>

