<template>
  <div class="item-input" :class="[{'error':!state},{'isView':isView}]">
      <template v-if="refresh">
              <Input :readonly="isView" type="text" v-if="item.type.no == 1" @on-change="inputChange" v-model="item.value" :placeholder="!isView?'请输入...':''"/>
              <Input :readonly="isView" type="textarea" v-else-if="item.type.no == 2" @on-change="inputChange" v-model="item.value" :rows="control.row" :placeholder="!isView?'请输入...':''"/>
              <InputNumber :readonly="isView"  v-else-if="item.type.no == 3" :min="restriction.min" :max="restriction.max" :precision="restriction.precision" @on-change="inputChange"  v-model="item.value" :placeholder="!isView?'请输入...':''"></InputNumber>
              <InputNumber :readonly="isView"  v-else-if="item.type.no == 4" :min="restriction.min" :max="restriction.max" @on-change="inputChange"  v-model="item.value" :placeholder="!isView?'请输入...':''"></InputNumber>
              <InputNumber :readonly="isView"  v-else-if="item.type.no == 5" :min="restriction.min" :max="restriction.max" @on-change="inputChange"  v-model="item.value" :placeholder="!isView?'请输入...':''"></InputNumber>

              <DatePicker :readonly="isView"  type="date" v-else-if="item.type.no == 6" @on-change="inputChange" v-model="item.value" :format="item.schema.restriction.format" :placeholder="!isView?'请输入...':''" ></DatePicker>
              <DatePicker :readonly="isView"  type="datetime" v-else-if="item.type.no == 7" @on-change="inputChange" v-model="item.value" :format="item.schema.restriction.format" :placeholder="!isView?'请输入...':''" ></DatePicker>
              <div  class="file-box" style="    width: 100%;" v-else-if="item.type.no == 8||item.type.no == 9">
                  <Upload
                          v-if="!isView"
                          :accept="(item.type.no==9?'image/jpeg,application/pdf':'application/pdf,image/jpeg')+',image/gif,text/plain,application/msword,application/vnd.ms-excel'"
                          multiple
                          type="drag"
                          :before-upload="handleUpload"
                          action="">
                      <div style="padding: 5px 0">
                          <Icon type="ios-cloud-upload" size="30" style="color: #3399ff;width:auto;height: auto"></Icon>
                          <p>选择文件</p>
                      </div>
                  </Upload>
                  <!--<div v-else-if="item.value">已有文件</div>-->
                  <Button v-show="item.value" @click="openFile">打开文件</Button>
              </div>
              <Select :disabled="isView"  v-else-if="item.type.type == 1" v-model="item.value" @on-change="inputChange">
                  <Option v-for="code in item.codeValues" :key="code.no" :value="code.no">{{code.value}}</Option>
              </Select>
              <Select :disabled="isView"  v-else-if="item.type.type == 3" v-model="item.value" @on-change="inputChange">
                  <Option v-for="code in item.valueSet" :key="code.id" :value="code.id">{{code.value}}</Option>
              </Select>

          <span v-if="unit" style="margin-left: 10px">{{unit.symbol}}</span>
      </template>

  </div>
</template>

<script>
import DB from "@lib/Db";
import Bus from '@event/bus';
import {dp} from '@lib/util'
export default {
    components: {

    },

    props: [ 'value' , 'control' ,'isView'],

    data(){
        return{
            item: this.value,
            refresh:true,
            state:true,
            hint:"",
            restriction:{
                precision:0,
                max:Infinity,
                min:-Infinity
            },
            unit:null,
            upload:{
                format:["txt","pdf"]
            },
            routePath:'',
        }
    },

    watch: {
        value(){
            this.onShow()
        }
    },

    created () {},
    beforeDestroy(){

    },
    mounted () {
        this.onShow()
    },

    methods: {
        onShow(){
            this.restriction.precision = 0
            this.restriction.max = Infinity
            this.restriction.min = -Infinity
            this.state = true
            this.item = this.value
            let restriction = null
            if(this.item.schema){
                this.item.is_required = this.item.schema.is_required
                restriction = DB.selectOne('restrictions','id = '+this.item.schema.restriction_id)
            }else {
                if(this.item.restriction_id){
                    restriction = DB.selectOne('restrictions','id = '+this.item.restriction_id)
                }
            }
            if(restriction){
                this.item.restriction = restriction
                if(this.item.restriction.precision){
                    this.restriction.precision = Number(this.item.restriction.precision)
                }
                if(this.item.restriction.max){
                    if(this.item.restriction.max != -1){
                        this.restriction.max = Number(this.item.restriction.max)
                    }
                }
                if(this.item.restriction.min){
                    this.restriction.min = Number(this.item.restriction.min)
                    if(!this.item.value&&this.item.value!=this.restriction.min){
                        this.item.value = this.restriction.min
                    }
                }
            }
            switch (this.item.type.no){
                case 3:
                case 4:
                case 5:
                    if(this.item.schema){
                        this.unit = DB.selectOne('units','id = '+this.item.schema.unit_id)
                    }else if(this.item.unit_id){
                        this.unit = DB.selectOne('units','id = '+this.item.unit_id)
                    }
                    break;
                default:

                    break;
            }

        },
        openFile(){
            DB.OpenFile(this.item.value)
        },
        handleUpload(file){
            if(file){
                let file_reader = new FileReader();
                let blob = null
                file_reader.onload = () => {
                    let fc = file_reader.result;
                    if(!this.item.value){
                        this.$set(this.item,'value',DB.Insert('files',{
                            type:file.type,
                            ext:file.name.substring(file.name.lastIndexOf('.')+1)
                        }))
                    }
                    DB.FillFileContent("files",this.item.value,"content",fc)
                    this.$emit('input', this.item)
                };
                file_reader.readAsArrayBuffer(file);
            }
            return false
        },
        inputChange(){
            this.$emit('input', this.item)
        },
        getValue(){
            let value = this.item.value
            if(!value&&value!==0){
                value = ""
            }
            if(this.item.is_required&&value===""){
                this.state = false
                this.hint = "有必填项未输入"
            }else{
                this.state = true
            }
            if(this.item.restriction){
                switch (this.item.type.type){
                    case 0:
                        switch (this.item.type.no){
                            case 1:
                                if(this.item.restriction.max && this.item.restriction.max > 0){
                                    if(this.item.restriction.max < value.length){
                                        this.state = false
                                        this.hint = "输入超出设计长度"
                                    }
                                }
                                break;
                            case 2:

                                break;
                            case 3:
                            case 4:
                            case 5:
                                if((this.item.restriction.max || this.item.restriction.max >= 0)&&this.item.restriction.max!=""){
                                    if(this.item.restriction.max < value){
                                        this.state = false
                                        this.hint = "输入超出设计长度"
                                    }
                                }
                                if((this.item.restriction.min || this.item.restriction.min == 0)&&this.item.restriction.min!=""){
                                    if(this.item.restriction.min > value){
                                        this.hint = "输入未达到设计长度"
                                        this.state = false
                                    }
                                }
                                break;
                            case 6:

                                break;
                            case 7:

                                break;
                            case 8:

                                break;
                            case 9:

                                break;
                        }
                        break;
                    case 1:

                        break;
                    case 3:

                        break;
                }
            }
            if(value === undefined || value === ''){
                value = null
            }
            return {
                value:value,
                state:this.state,
                hint:this.hint
            }
        }
    }
}
</script>

<style>
.item-input{
    display: flex;
    align-items: center;
}
.item-input.isView .ivu-input{
    border: none;
}
.item-input.isView .ivu-input-number{
    border: none;
}
.item-input.isView .ivu-upload-drag{
    border: none;
}
.item-input.isView .ivu-select-selection{
    border: none;
}
.item-input.isView .ivu-select-disabled .ivu-select-selection{
    background: inherit;
    color: inherit;
}
.item-input.isView .ivu-select-disabled .ivu-icon-ios-arrow-down:before{
    content: "";
}
.item-input.error .ivu-input{
    border: 1px solid #fb3e3e;
}
.item-input.error .ivu-input-number{
    border: 1px solid #fb3e3e;
}
.item-input.error .ivu-upload-drag{
    border: 1px solid #fb3e3e;
}
.item-input.error .ivu-select-selection{
    border: 1px solid #fb3e3e;
}
.item-input .ivu-input-number{
    width: auto;
}

</style>