<template>
  <div class="composite">
    <Table row-key="id" border :disabled-hover="true" @on-column-width-resize="widthChange" :columns="columns" :data="content">
      <template slot-scope="{ row }" slot="name">
        <span style="padding-left: 20px;" v-if="!row.children||!row.children.length"><span style="color: #FF0000;margin-right: 5px;" v-if="row.schema.is_required == 1">※</span></span>{{row.title}}
      </template>
      <template slot-scope="{ row }" slot="value">
        <div> <!--style="padding-top: 10px;padding-bottom: 10px"-->
          <Item :isView="isView" :ref="'ref-'+row.schema.id" @input="inputChange(row,$event)" v-if="row.type.type<=1||row.valueSet" v-model="row" :control="{row:5}"></Item>
          <!--<a :isView="isView" :ref="'ref-'+row.schema.id" @input="inputChange(row,$event)" :columns="row.columns" :item="row" :expandColumns="row.expandColumns" :content="row.value" v-if="row.type.type==3&&!row.valueSet" @click="controls.isOpen = true">{{row.value.length}}行</a>-->
          <TableList :isView="isView" :ref="'ref-'+row.schema.id" @input="inputChange(row,$event)" :columns="row.columns" :item="row" :expandColumns="row.expandColumns" :content="row.value" v-if="row.type.type==3&&!row.valueSet"></TableList>
        </div>
      </template>
    </Table>
  </div>
</template>

<script>
import DB from "@lib/Db";
import Bus from '@event/bus';
import {dp} from '@lib/util'

export default {
    name:"composite",
    components: {

    },
    props: {
        content:{
            type:Array,
            default(){
                return []
            },
        },
        isView:{
            type:Boolean,
            default:false
        }
    },

    data(){
        return{
            columns:[{
                title:"项目",
                slot:"name",
                tree:true,
                display:'inline',
                resizable: true,
                width: 200,
            },{
                title:"值",
                slot:'value'
            }],
        }
    },

    watch: {

    },

    created () {},
    beforeDestroy(){

    },
    mounted () {


    },

    methods: {
        widthChange(newWidth, oldWidth, column, event){
//            column.width = newWidth
        },
        inputChange(row,e){
            let findValue = (item)=>{
                if(item.id == row._rowKey){
                    item.value = e.value
                }
                if(item.children){
                    item.children.forEach((child)=>{
                        findValue(child)
                    })
                }
            }
            this.content.forEach((item)=>{
                findValue(item)
            })
        },
        getRefValue(refName,value){
            let ref = null
            if(this.$refs[refName]&&this.$refs[refName][0]){
                ref = this.$refs[refName][0]
            }else{
                ref = this.$refs[refName]
            }
            if(ref&&ref.getValue){
                let obj = ref.getValue()
                if(!obj.state){
                    this.state = obj.state
                    this.hint = obj.hint
                }
                value = obj.value
            }
            return value
        },
        getValue(){
            this.state = true
            let value = {

            }
            this.content.forEach((item)=>{
                this.findValue(item,value)
            })
            return {
                state:this.state,
                value:value,
                hint:this.hint
            }
        },
        findValue(item,value){
            let childObj
            switch (item.type.type){
                case 2:
                    childObj = value[item.name] = {}
                    break;
                case 3:
                    if(item.value){
                        value[item.name] = item.value
                    }else{
                        value[item.name] = []
                    }
                    break;
                default:
                    value[item.name] = this.getRefValue('ref-'+item.schema.id,null)
                    break;
            }
            if(item.children){
                item.children.forEach((child)=>{
                    this.findValue(child,childObj)
                })
            }
        }
    }
}
</script>

<style>
  .ivu-table-cell {
    padding: 5px !important;
  }
  .ivu-table td, ivu-table th {
    height: 24px !important;
  }

  #fill-main .composite .ivu-table-wrapper{
    position:inherit !important;
  }
  .composite .ivu-table:before{
    position: inherit;
  }
</style>