<template>
  <div id="technicalDetail">
    <div class="left-box">

      <div class="table-box">
        <div class="table-title">
          <div class="back-btn">      <Button @click="back">返回</Button></div>
          基线数据
        </div>
        <Table highlight-row row-key="index" border  :disabled-hover="true" :columns="columns" :data="content" :max-height="clientHeight * 0.75" @on-current-change="nodeClick">
          <template slot-scope="{ row }" slot="path">
            <span v-if="row.dataType=='type'">{{row.path}}</span>
            <div v-if="row.dataType=='file'" class="path-box">
              <span class="path">{{row.path}}</span><img src="../../assets/tree/文件选择图标@3x.png" class="detail-btn" @click="view(row)">
            </div>
          </template>
          <template slot-scope="{ row }" slot="control">

            <div class="table-btn" v-if="row.dataType=='type'" @click.stop="insert(row)">
              <img src="../../assets/technical/新增@3x.png">
              新增
            </div>

            <div class="table-btns" v-else-if="row.dataType=='file'">

              <div class="table-btn" @click.stop="remove(row)">
                <img src="../../assets/technical/删除@3x.png">
                删除
              </div>
              <div class="table-btn" @click.stop="update(row)">
                <img src="../../assets/technical/修订@3x.png">
                修订
              </div>
            </div>
          </template>
        </Table>
      </div>
    </div>
    <Modal
            v-model="modal.showState"
            class-name="modal"
            :closable="false"
            :mask-closable="false"
            :styles="{top: '0',width:'420px'}"
    >
      <div slot="header" class="modal-header">
        <div class="title">{{modal.title}}</div>
        <div class="control">

          <img src="../../assets/sidebar/关闭1@3x.png" @click="cancel()"><img src="../../assets/sidebar/确认1@3x.png" @click="confirm()">
        </div>
      </div>
      <div class="form-info" v-show="modal.viewState == 'insert' || modal.viewState == 'update' || modal.viewState=='view'">
        <Form :model="modal.info"  ref="formValidate" :rules="ruleValidate" :label-width="100">
          <FormItem label="名称" prop="name">
            <Input v-model="modal.info.name" :disabled="modal.viewState=='view'"></Input>
          </FormItem>
          <FormItem label="文件" prop="path">
            <div class="info-file">
              <Input v-model="modal.info.path" :disabled="modal.viewState=='view'"></Input>
              <div class="info-file-choose" v-show="modal.viewState!='view'">
                <input type="file" id="chooseFile"  @change="fileChange($event)">
              </div>
            </div>
          </FormItem>
          <FormItem label="版本" prop="version">
            <Input v-model="modal.info.version" :disabled="modal.viewState=='view'"></Input>
          </FormItem>
          <FormItem label="构型节点">
            <Select v-model="modal.info.configurationId" :disabled="modal.viewState=='view'">
              <Option v-for="(item,index) in configurationSelectList" :value="item.id" :key="index">{{item.index}} {{ item.title }}</Option>
            </Select>
          </FormItem>
        </Form>
      </div>
      <div v-show="modal.viewState == 'delete'">
        确定要删除吗？
      </div>
      <div v-show="modal.viewState == 'save'">
        确定要保存并退出吗？
      </div>
      <div slot="footer" class="modal-footer">
        <Button  size="large"  @click="confirm">完成</Button>
      </div>
    </Modal>

    <div class="tree-box">
      <Tree  empty-text="" :data="treeList"  class="demo-tree-render"></Tree>
    </div>

  </div>
</template>

<script>
import DB from '@lib/Db';
import {dp} from '@lib/util'
export default {
    data() {
        return {
            clientHeight:document.documentElement.clientHeight,
            isEdit:false,
            columns:[
                {
                    type: 'index',
                    align: 'center',
                    title:'序号',
                    width:80
                },
                {
                    key:'name',
                    title:'项目',
                    tree:true,
                    width:420
                }
                ,{
                    slot:'path',
                    title:'文件',
                    align: 'center',

                }
            ],
            content:[],
            modal:{
                showState:false,
                viewState:'',//organization  air  model template
                title:"",
                info:{
                    name:"",
                    version:"",
                    file:"",
                    path:"",
                    configurationId:"",
                    typeNo:"",
                    id:""
                },
                oldInfo:null,
            },
            ruleValidate: {
                name: [
                    {
                        required: true,
                        message: '名称不能为空',
                        trigger: 'blur'
                    }
                ],
                path: [
                    { required: true, message: '文件不能为空', trigger: 'blur' },
                ],
                version: [
                    { required: true, message: '版本不能为空', trigger: 'blur' },
                ]
            },
            treeList:[],
            configurationSelectList:[],
            historyList:[],
            changeHistory:null,
        }
    },
    methods:{
        updateIndex(){
            this.content.forEach((item,index)=>{
                item.index = index
                item.children.forEach((child,i)=>{
                    child.index = index+'-'+i
                })
            })
        },
        back(){
            this.$router.back()
        },
        view(row){
            this.modal.viewState = "view"
            this.modal.title = "详情"
            this.modal.info.name = row.name
            this.modal.info.configurationId = row["构型节点id"]
            this.modal.info.path = row.path
            this.modal.info.version = row["版本"]
            this.modal.showState = true
        },
        selectConfigurationNode(node,configurationId){
            this.$set(node,"selected",configurationId == node.id)
            if(node.children.length){
                node.children.forEach((item)=>{
                    this.selectConfigurationNode(item,configurationId)
                })
            }
        },
        nodeClick(currentRow ,oldCurrentRow){
            this.selectConfigurationNode(this.treeList[0],currentRow["构型节点id"])
        },
        remove(row){
            this.modal.title = "提示"
            this.modal.viewState = "delete"
            this.modal.info.id = row.id
            this.modal.info.historyIndex = row.historyIndex
            this.modal.showState = true
        },
        update(row){
            this.modal.oldInfo = row
            this.modal.viewState = "update"
            this.modal.title = "修订"
            this.modal.info.id = row.id
            this.modal.info.historyIndex = row.historyIndex
            this.modal.info.name = row.name
            this.modal.info.configurationId = row["构型节点id"]
            this.modal.info.path = row['路径']
            this.modal.info.version = row["版本"]
            this.modal.info.fileId = row["附件"]
            this.modal.showState = true
        },
        insert(row){
            this.modal.viewState = "insert"
            this.modal.title = "新增"
            this.modal.info.file = ""
            this.modal.info.name = ""
            this.modal.info.path = ""
            this.modal.info.version = ""
            this.modal.info.typeNo = row.no
            this.modal.info.configurationId = this.configurationSelectList[0].id
            this.modal.showState = true
        },
        findConfiguration(configurationId,index){
            let children = []
            let configuration = this.configurationList.find((item)=>{
                return item.id == configurationId
            })

            //只是为了弄下拉框
            configuration.index = index
            this.configurationSelectList.push(configuration)
            //

            this.configurationList.forEach((item)=>{
                if(item.parent_id == configurationId){
                    let i = children.length
                    children.push(this.findConfiguration(item.id,index+"-"+i))
                }
            })
            configuration.disabled = true
            configuration.title = configuration['名称']
            configuration.baselineId = configuration['基线id']
            configuration.parentId = configuration.parent_id
            configuration.isCanDelete = true
            configuration.children = children
            if(children.length){
                this.$set(configuration,'expand',true)
            }
            return configuration
        },
        NumToStr(num){
            if(num<10){
                num = "0"+num
            }
            return num
        },
        DatetimeToStr(datetime){
            let date = new Date(Number(datetime))
            return date.getFullYear()+"-"+this.NumToStr((date.getMonth()+1))+"-"+this.NumToStr(date.getDate())+"  "+this.NumToStr(date.getHours())+":"+this.NumToStr(date.getMinutes())
        },
        confirm(){
            if(this.modal.viewState=='delete'){
                let flag = false
                this.historyList.forEach((item,i)=>{
                    if(i == this.modal.info.historyIndex){ //有修改记录
                        flag = true
                        item.type = "delete"
                        if(item.type == "update"){
                            delete item.changeInfo["to_file_id"]
                        }else{
                            item.type = "delete"
                        }
                    }
                })
                if(!flag){
                    this.historyList.push({
                        type:"delete",
                        changeInfo:{
                            "change_history_id":this.changeHistory.id,
                            "from_file_id": this.modal.info.id,
                        },
                    })
                }
                let tableIndex = -1
                this.content.forEach((item)=>{
                    item.children.forEach((child,i)=>{
                        if(child.id == this.modal.info.id || child.historyIndex == this.modal.info.historyIndex){
                            tableIndex = i
                        }
                    })
                    if(tableIndex>=0){
                        item.children.splice(tableIndex,1)
                        this.$set(item,"children",item.children)
                        this.$set(item,"path","（"+item.children.length+"项）")
                    }
                })
                this.updateIndex()
                this.modal.showState = false

            }else if(this.modal.viewState == 'save'){
                this.historyList.forEach((item)=>{
                    switch (item.type){
                        case "insert":
                            item.fileInfo['附件'] = DB.Insert('files',{
                                type:"技术状态文件"
                            })
                            if(item.file){
                                DB.Update('files',{
                                    id:item.fileInfo['附件'],
                                    type:item.file.type,
                                    ext:item.file.ext
                                })
                                DB.FillFileContent("files",item.fileInfo['附件'] ,"content",item.file.content)
                            }
                            item.changeInfo.to_file_id = DB.Insert("技术状态文件",item.fileInfo)
                            DB.Insert("change_history_details",item.changeInfo)
                            break;
                        case "update":
                            item.fileInfo.pervious_version_id = item.changeInfo.from_file_id
                            item.fileInfo['附件'] = DB.Insert('files',{
                                type:"技术状态文件"
                            })
                            item.changeInfo.to_file_id = DB.Insert("技术状态文件",item.fileInfo)
                            DB.Update("技术状态文件",{
                                id:item.changeInfo.from_file_id,
                                state:1
                            })
                            if(item.file){
                                DB.Update('files',{
                                    id:item.fileInfo['附件'],
                                    type:item.file.type,
                                    ext:item.file.ext
                                })
                                DB.FillFileContent("files",item.fileInfo['附件'] ,"content",item.file.content)
                            }
                            DB.Insert("change_history_details",item.changeInfo)
                            break;
                        case "delete":
                            if(item.changeInfo.from_file_id){
                                DB.Update("技术状态文件",{
                                    id:item.changeInfo.from_file_id,
                                    state:2,
                                })
                                DB.Insert("change_history_details",item.changeInfo)
                            }
                            break;
                    }

                })
                this.historyList = []
                this.$router.back()
            }else{
                this.$refs.formValidate.validate((valid) => {
                    if(valid){
                        let historyIndex = this.historyList.length
                        switch (this.modal.viewState){
                            case "insert":
                                let newInfo = {
                                    "构型节点id":this.modal.info.configurationId,
                                    "基线id":this.$route.query.baselineId,
                                    "名称":this.modal.info.name,
                                    "版本":this.modal.info.version,
                                    "修订时间":this.changeHistory.time,
                                    "类型":this.modal.info.typeNo,
                                    "路径":this.modal.info.path,
                                    "state":0,
                                }
                                this.historyList.push({
                                    type:"insert",
                                    changeInfo:{
                                        "change_history_id":this.changeHistory.id,
                                    },
                                    historyIndex:historyIndex,
                                    fileInfo:DB.extendDeep({},newInfo),
                                    file:this.modal.info.file
                                })
                                var content = DB.extendDeep([],this.content,true)
                                content.forEach((item)=>{
                                    if(item.no == this.modal.info.typeNo){
                                        newInfo.historyIndex = historyIndex
                                        newInfo.path = newInfo['路径']
                                        newInfo.dataType = "file"
                                        newInfo.name = newInfo["名称"]
                                        item.children.push(newInfo)
                                        item.path = "（"+item.children.length+"项）"
                                    }
                                })
                                this.content = content
                                this.updateIndex()
                                break;
                            case "update":
                                let flag = false
                                let updateInfo = {
                                    "构型节点id":this.modal.info.configurationId,
                                    "名称":this.modal.info.name,
                                    "版本":this.modal.info.version,
                                    "路径":this.modal.info.path,
                                    "修订时间":this.changeHistory.time,
                                }
                                this.historyList.forEach((item,index)=>{
                                    //如果已经有修改记录 或者 新增记录
                                    if(item.historyIndex == this.modal.info.historyIndex ){
                                        flag = true
                                        for(let key  in updateInfo){
                                            item.fileInfo[key] = updateInfo[key]
                                        }
                                        item.file = this.modal.info.file
                                    }
                                })
                                console.log(flag)
                                if(!flag){
                                    let newInfo = DB.selectOne('技术状态文件','id='+this.modal.oldInfo.id) || {}
                                    for(let key  in updateInfo){
                                        newInfo[key] = updateInfo[key]
                                    }
                                    newInfo['附件'] = this.modal.info.fileId
                                    this.historyList.push({
                                        type:"update",
                                        changeInfo:{
                                            "change_history_id":this.changeHistory.id,
                                            "from_file_id":this.modal.oldInfo.id,
                                        },
                                        file:this.modal.info.file,
                                        fileInfo:DB.extendDeep({},newInfo),
                                        historyIndex:historyIndex
                                    })
                                }
                                var content = DB.extendDeep([],this.content,true)
                                content.forEach((item)=>{
                                    item.children.forEach((child)=>{
                                        if(child.historyIndex == this.modal.oldInfo.historyIndex ||child.id == this.modal.oldInfo.id){
                                            child.historyIndex = historyIndex
                                            child.name = updateInfo["名称"]
                                            child.path = updateInfo['路径']
                                            for(let key  in updateInfo){
                                                child[key] = updateInfo[key]
                                            }
                                        }
                                    })
                                    item.path = "（"+item.children.length+"项）"
                                })
                                this.content = content
                                this.updateIndex()
                                break;
                        }
                        this.modal.showState = false

                    }
                })
            }

        },
        cancel(){
            this.modal.showState = false
        },
        fileChange(e){
            let file = e.target.files[0]
            if(file){
                let file_reader = new FileReader();
                let blob = null
                file_reader.onload = () => {
                    let fc = file_reader.result;
                    this.modal.info.file.content = fc
                    e.target.value = ""
                };
                file_reader.readAsArrayBuffer(file);
                this.modal.info.file = file
                this.modal.info.file.ext  = file.name.substring(file.name.lastIndexOf('.')+1)
                this.modal.info.name = file.name
                this.modal.info.path = file.name
            }
        },
        intoDetail(path){
            this.$router.push({
                path:'/technical/'+path,
                query:{
                    configurationId:this.$route.query.configurationId,
                }
            })
        },
        append(){
            this.modal.showState = true
        }
    },
    mounted(){
        this.isEdit = this.$route.query.isEdit
        //构型
        this.configurationList = DB.Select("构型","","")
        this.configurationSelectList = []
        let treeObject = this.findConfiguration(this.$route.query.configurationId,0)
        this.treeList.push(treeObject)
        //变更历史信息
        this.changeHistory = DB.selectOne("change_histories","id="+this.$route.query.changeHistoryId,"")

        let viewList = []
        let fileList = []
        let listItem = DB.Select("lists","parent_no="+DB.tableNos['技术状态文件'],"")[0]
        let technologyStateKeyList = DB.getSchemaComponents(this.$route.query.templateId,listItem.element_type_no)
        if(this.isEdit){
            this.columns.push({
                slot:'control',
                title:'操作',
                align: 'center',
            })
            //文件目录
            fileList = DB.Select("技术状态文件",'"基线id" = '+this.$route.query.baselineId+" AND state = 0","")
            technologyStateKeyList.forEach((key)=>{
                if(key.name == "类型"){
                    viewList = DB.getCodeValues(this.$route.query.templateId,key.type_no)
                    viewList.forEach((type)=>{
                        type.name = type.value
                        type.dataType = 'type'
                        type.children = []
                        fileList.forEach((file)=>{
                            if(type.no == file['类型']){
                                file.dataType = 'file'
                                file.name = file["名称"]
                                file.fileName = file["path"]
                                type.children.push(file)
                            }
                        })
                        type.path = "（"+type.children.length+"项）"
                    })
                }
            })
        }else{
            fileList = DB.Select("技术状态文件",'"基线id" = '+this.$route.query.baselineId,"")
            let changeHistoryDetails = DB.Select("change_history_details","change_history_id = "+this.changeHistory.id,"")
            let viewFileList = []
            fileList.forEach((item)=>{
                if(item["修订时间"] < this.changeHistory.time){
                    if(item.state == 0){
                        viewFileList.push(item)
                    }else{
                        changeHistoryDetails.forEach((changeHistoryDetail)=>{
                            if(changeHistoryDetail.to_file_id == item.id || changeHistoryDetail.from_file_id == item.id){
                                viewFileList.push(item)
                            }
                        })
                    }
                }
            })
            technologyStateKeyList.forEach((key)=>{
                if(key.name == "类型"){
                    let technologyStateFileTypeList = DB.getCodeValues(this.$route.query.templateId,key.type_no)
                    technologyStateFileTypeList.forEach((type)=>{
                        type.name = type.value
                        type.dataType = 'type'
                        type.children = []
                        viewFileList.forEach((file)=>{
                            if(type.no == file['类型']){
                                file.dataType = 'file'
                                file.name = file["名称"]
                                file.fileName = file["path"]
                                type.children.push(file)
                            }
                        })
                        type.path = "（"+type.children.length+"项）"
                        if(type.children.length){
                            viewList.push(type)
                        }
                    })
                }
            })
        }
        this.content = viewList
        this.updateIndex()
    },
    components: {

    },
    beforeRouteLeave(to,form,next){
        let flag = true
        if(this.historyList.length){
            flag = false
            this.modal.title = "提示"
            this.modal.viewState = "save"
            this.modal.showState = true
        }
        next(flag)
    }
}
</script>

<style>
  #technicalDetail .back-btn{
    position: absolute;
    left: 20px;
  }
  #technicalDetail{
    display: flex;
    height: 100%;
  }
  #technicalDetail .left-box{
    width: 75%;
    height: 100%;
    border-right: solid 1px rgb(235,235,235);
  }
  #technicalDetail .table-box{
    height: 100%;
    width: 97%;
    margin: auto;

  }
  #technicalDetail .table-title{
    border:solid 1px #dcdee2;
    margin-top: 20px;
    height: 50px;
    line-height: 50px;
    border-bottom: none;
    text-align: center;
    background: rgb(225,241,249);
    position: relative;
  }
  #technicalDetail .table-btns{
    display: flex;
    width: 150px;
    justify-content: space-between;
    margin: auto;
  }
  #technicalDetail .table-btn{
    display: flex;
    width: 50px;
    margin: auto;
    align-items: center;
    justify-content: space-between;
  }
  #technicalDetail .table-btn img{
    width: 15px;
  }
  #technicalDetail .path-box{
    display: flex;
    align-items: center;
    justify-content: center;
  }
  #technicalDetail .path-box span{
    width: 100px;
  }
  #technicalDetail .detail-btn{
    width:15px;
    margin-left: 5px;
  }

  #technicalDetail .tree-box{
    height: 100%;
    width: 25%;
    overflow: scroll;
    padding-left: 19.2px;
    text-align: left;
  }
  #technicalDetail .tree-box .ivu-tree ul{
    /*font-size: 0.72916666vw;*/
    font-size: 14px;
  }
  #technicalDetail .tree-box .ivu-tree-arrow i{
    /*font-size: 0.72916666vw;*/
    font-size: 14px;

  }
  #technicalDetail .tree-box .ivu-tree-arrow{
    display: inline-flex;
    align-items: center;
  }
  #technicalDetail .tree-box .ivu-icon-ios-arrow-forward:before{
    content: "";
  }
  #technicalDetail .tree-box .ivu-tree-arrow-open .ivu-icon.ivu-icon-ios-arrow-forward{
    background:url(../../assets/tree/收起.png);
    -webkit-background-size: 100% 100%;
    background-size: 100% 100%;
  }
  #technicalDetail .tree-box .ivu-icon.ivu-icon-ios-arrow-forward{
    background:url(../../assets/tree/展开.png);
    -webkit-background-size: 100% 100%;
    background-size: 100% 100%;
    transform: rotate(0deg);
    /*width: 0.57291666vw;*/
    /*height: 0.57291666vw;*/
    width: 11px;
    height: 11px;
  }
  #technicalDetail .tree-box .ivu-icon{
    /*width:0.83333366vw;*/
    /*height:0.83333333vw;*/
    width: 16px;
    height: 16px;
  }
</style>