<template>
  <div class="t-table">
      <div class="header complex-header" v-if="!readOnly">
      </div>
       <a class="content-save" @click="save">
          <Icon type="ios-checkmark" size="20" />
        </a>
      <table class="tableType">
        <colgroup>
        
        </colgroup>
        <thead>
         <tr v-for="(row, rowIndex) in tableData" :key="rowIndex">
          <td v-for="(col, colIndex) in row" :colspan="col.children && col.type.type != 1? col.children.length: 1" :rowspan="col.children && col.type.type != 1? 1: theadRowLength - rowIndex" :key="colIndex">
            {{col.relation.name}}
          </td>
         </tr>
        </thead>
        <tbody class="t-body" v-if="relation.group_by && readOnly">
          <template v-for="(row) in rowData" >
            <tr>
              <td :colspan="columnLength">
                <TableItem :type="row.groupBy.ref.type" :data="row.groupBy"/>
              </td>
            </tr>
            <tr v-for="(item, index) in row.column" :key="index">
              <template v-for="(val, name, subIndex) in item" >
                <td :key="name" v-if="name != 'id'">
                <TableItem  :type="val.ref.type" :index="subIndex" :data="val" :key="val.ref.relation.id"/>
                </td>
              </template>
              
            </tr>
          
          </template>
        </tbody>
        <tbody class="t-body" v-else >
          <tr v-for="(item, index) in columnData" :key="index">
            <td class="t-td t-operate">
              <a @click="addBeforeItem(index)">添加</a>
              <a @click="removeItem(index)"> 删除</a>
            </td>
            <template v-for="(val, name, subIndex) in item">
              <td v-if="name != 'id'" :key="name" >
              <TableItem  :type="val.ref.type" :index="subIndex" :data="val" :key="val.ref.relation.id"/>
              <!-- <TableItem :type="val.type" :index="subIndex" :data="val" :key="val.relation.id"/> -->
              </td>
            </template>
            
          </tr>
        </tbody>

      </table>
      <Row type="flex" class="t-add-wrapper" justify="center" align="middle" v-if="columnLength && !readOnly" >
          <a href="javascript:void(0)" class="t-add" @click="addColumnData" >
              <img src="@assets/sidebar/添加@2x.png" alt="">
            </a>
          <!-- <Dropdown v-else trigger="click" @on-click="addItem">
            <a href="javascript:void(0)" class="t-add">
              <img src="@assets/sidebar/添加@2x.png" alt="">
            </a>
            <DropdownMenu slot="list" >
              <DropdownItem name="添加分组行">添加{{ relation.group_by }}</DropdownItem>
              <DropdownItem name="添加内容行" v-if="canAddColumn">添加行</DropdownItem>
            </DropdownMenu>
          </Dropdown> -->
      </Row>
      
    </div>
</template>

<script>
import TableItem from './TableItem'
import table from '@mixin/table'
import { getEuqTableValue, saveTableData, removeTableData } from '@api/fill'
export default {
  props: {
    source: {
      type: String
    },
    tableName: {
      type: String
    },
    idRule: {
      type: String
    },
    data: {
      type: Object
    }
  },
  data() {
    return {
      
    }
  },
  components: {
    TableItem
  },
  mixins: [table],
  methods: {
    getValue() {
      this.value = getEuqTableValue(this.tableName, this.data.relation.baselineId, this.idRule)
      this.converValue()
    },
    converValue() {
      if (this.relation.group_by && this.readOnly) {
        this.value.forEach(item => {
          let groupValue = item[this.relation.group_by];
          if (!this.rowData[groupValue]) {
            this.$set(this.rowData, groupValue, {
              groupBy: {ref: this.groupBy, value: groupValue},
              column: []
            })
            
          } 
          let obj = {}
          if (Object.keys(item).length >4) {
            this.flatValue(obj, item)
            delete obj[this.relation.group_by]
            this.rowData[groupValue].column.push(obj)
          } 
        })
      } else {
        this.value.forEach(item => {
          let obj = {}
          this.flatValue(obj, item)
          this.columnData.push(obj)
        })
      }
    },
    flatValue(target, value) {
      for (let key in value) {
        if (this.columnMap[key]) {
          target[key] = {
            value: key == '序号'? value[key].replace(this.idRule, ''): value[key],
            ref: this.columnMap[key]
          }
        } else {
          let child = this.children.find(item => item.name === key)
          if (!child) break
          if (child.type.type == 2) {
            this.flatValue(target,JSON.parse(value[key]))
          } else {
            target[key] = {
            value: key == '序号'? value[key].replace(this.idRule, ''): value[key],
            ref: child
          }
          }
        }
      }
      
    },
    beforeSave() {
      if (this.relation.group_by && this.readOnly) {
        for (let key in this.rowData) {
          const val = this.rowData[key]
          if (val.column.length) {
            val.column.forEach(item => { // 插入的一条数据
              const obj = this.getChildrenValue(this, item)
              obj['基线id'] = this.data.relation.baselineId;
              obj[this.relation.group_by] = val.groupBy.value;
              let isNew = key.indexOf('newKey') !== -1
              if (!isNew) {
                isNew = this.newRowData.indexOf(item) != -1
              }
              this.saveData(obj, isNew)
            })
          } else {
            let obj = {}
            obj[this.relation.group_by] = val.groupBy.value;
            obj['基线id'] = this.data.relation.baselineId
            obj['序号'] = this.idRule;
            this.saveData(obj, key.indexOf('newKey') !== -1) 
          }

        }

        } else {
          this.columnData.forEach(item => {
            const obj = this.getChildrenValue(this, item)
            obj['基线id'] = this.data.relation.baselineId;
            if (this.newRowData.indexOf(item) != -1) {
              this.saveData(obj, true)
            } else {
              this.saveData(obj, false)
            }
          })
          this.removeRowData.forEach(item => {
            removeTableData(this.tableName, item)
          })
        }
    },
    saveData(obj, isNew) {
      
      for (let key in obj) {
        if (typeof(obj[key]) == 'object') {
          obj[key] = JSON.stringify(obj[key])
          break;
        }
        if (key === '序号') {
          obj[key] = `${this.idRule}${obj[key]}`
        }
      }
      console.log('saveData', obj, isNew)
      saveTableData(this.tableName, obj, isNew)
    },
  }
  
}
</script>

